import Flavor from "../conf/Flavor";
import CryptMgr from "../crypt/CryptMgr";
import Lighter from "../services/Lighthouse/Lighter";
import { LighthouseProto } from "../services/Lighthouse/proto/LighthouseProto";
import { lodash } from "../utils/NpmExport";
import { PlatformUtil } from "../platform/PlatformUtil";
import { ProjectInfo } from "../conf/ProjectInfo";

export default class HttpRequestUtil {
    private static _instance: HttpRequestUtil = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new HttpRequestUtil();
        }
        return this._instance;
    }

    private constructor() {}

    private buildSign(req_plain: any, ts: number) {
        const sign = CryptMgr.getInstance().md5_digest(req_plain, ts);
        return sign;
    }

    public encrypt_data(req_plain) {
        let req_encrypted = CryptMgr.getInstance().aes_encrypt(req_plain);
        return req_encrypted;
    }

    public decrypt_response(response) {
        return CryptMgr.getInstance().aes_decrypt(response);
    }

    public buildFullUrl(host: string, api: string, req_plain: any) {
        if (host) {
            let prefixIndex = host.indexOf("https");
            if (prefixIndex < 0) {
                host = "https://" + host;
            }
            if (host.charAt(host.length - 1) == "/") {
                host = host.substring(0, host.length - 1);
            }
        }
        if (api.charAt(0) != "/") {
            api = "/" + api;
        }

        let ts = new Date().getTime();
        let sign = this.buildSign(req_plain, ts);
        let gameVersion = ProjectInfo.getInstance().GAME_VERSION;

        const queryParams = lodash
            .chain(
                lodash.toPairs({
                    aid: CommonDepend.AppParamManager.getDeviceID(),
                    code: Flavor.Country.code,
                    lan: Flavor.Language.curLanguage,
                    svn: PlatformUtil.getInstance().getSvn(),
                    svc: PlatformUtil.getInstance().getSystemVersionCode() || 1,
                    cvc: PlatformUtil.getInstance().getClientVersionCode() || 1, // client(native) version code
                    cvn: gameVersion, // cocos version name
                    chn: CommonDepend.AppParamManager.getChn() || "unknown",
                    pkg: CommonDepend.AppParamManager.getPackageName(),
                    app: CommonDepend.AppParamManager.getAppName(),
                    ts,
                    sign,
                })
            )
            .filter(([key, value]) => value !== undefined)
            .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
            .reduce((pre, cur) => `${pre}&${cur}`)
            .value();

        let fullUrl = host + api;
        fullUrl += fullUrl.includes("?") ? "&" : "?" + queryParams;

        return fullUrl;
    }

    public canRetryWithHostBackup(host_backup: string[]) {
        return host_backup.length > 0 ? true : false;
    }

    public buildHeader(reqHeader: ReqHeader) {
        let now = new Date().getTime();
        let curr_time = now + CommonDepend.DateUtils.getTimeDiff();
        reqHeader.timestamp = parseInt(curr_time.toString());
        let user_id = CommonDepend.UserManager.uid;
        let token = CommonDepend.UserManager.token;

        reqHeader.user_id = user_id;
        reqHeader.auth_token = token.toString();
        reqHeader.brand_code = CommonDepend.AppParamManager.getBrandCode();
        return reqHeader;
    }

    public reBuildReq(req_header, req_prototype, req_plain) {
        let req = req_prototype.deserializeBinary(req_plain);
        let newHeader = this.buildHeader(req_header);
        req.header = newHeader;
        let new_req_plain = req.serializeBinary();
        return new_req_plain;
    }
}
