export class AdjustTrack {
    private static _instance: AdjustTrack = null;

    public readonly connect_to_naming = "Updated";
    public readonly app_start = "Start";
    public readonly user_active = "Activate";
    public readonly register_phone = "Phone_reg";
    public readonly register_fb = "FB_reg";
    public readonly register_account = "Register";
    public readonly pay_success_lowquota = "Small";
    public readonly pay_success_midquota = "Medium";
    public readonly pay_success_highquota = "Large";

    private adjustConf = {};

    public static getInstance() {
        if (!this._instance) {
            this._instance = new AdjustTrack();
        }

        return this._instance;
    }

    private constructor() {}

    public initAdjustConf() {
        let launchConfig = CommonDepend.LocalStorageMgr.readLauncherConfig();
        if (launchConfig && launchConfig.adjust_conf) {
            let eventInfo = launchConfig.adjust_conf.event_list;
            if (eventInfo) {
                let events = {};
                for (let key in eventInfo) {
                    let eventName = eventInfo[key].event_name;
                    let eventToken = eventInfo[key].event_token;
                    events[eventName] = eventToken;
                }
                this.adjustConf = events;

                CommonDepend.H5Util.initAdjustID(launchConfig.adjust_conf.appid || "");
                CC_DEBUG && cc.log("AdjustTrack: initAdjust", JSON.stringify(this.adjustConf));
            }
        }
    }

    /**
     * Adjust事件track入口，事件参数依赖LaunchConfig返回的事件列表
     * 如果没有找到事件，返回false，否则返回true
     * @param eventName
     * @param data
     */
    public trackEvent(eventName, data?): boolean {
        if (!eventName) {
            return false;
        }
        let eventToken = this.adjustConf[eventName];
        if (!eventToken) {
            return false;
        }
        if (!data) {
            data = {};
        }
        data.user_id = CommonDepend.UserManager.uid;
        data.channel = CommonDepend.AppParamManager.getChn();
        data.brand_code = CommonDepend.AppParamManager.getBrandCode();
        CC_DEBUG && cc.log("AdjustTrack: trackEvent", eventName, eventToken, JSON.stringify(data));
        CommonDepend.PlatformUtil.trackAdjustEvent(eventToken, data);

        return true;
    }

    /**
     * 按照金额范围Track对应事件
     * @param amount
     */
    public trackPaySuccessEvent(amount) {
        let eventName = "";
        if (amount > 6000000) {
            eventName = this.pay_success_highquota;
        } else if (amount > 300000 && amount <= 6000000) {
            eventName = this.pay_success_midquota;
        } else {
            eventName = this.pay_success_lowquota;
        }
        this.trackEvent(eventName);
    }
}
