import { BundleMgr } from "../bd/BundleMgr";

/** 背景音乐音量存储键 */
const MUSIC_VOLUME_STORAGE_KEY = "_music_volume";
/** 音效音量存储键 */
const EFFECT_VOLUME_STORAGE_KEY = "_effect_volume";
/** 背景音乐基础音量倍数 */
const MUSIC_VOLUME_RATIO = 0.5;
/** 音效基础音量倍数 */
const EFFECT_VOLUME_RATIO = 0.6;

export class AudioManager {
    private static _instance: AudioManager = null;

    private musicVolume: number = 0;
    private musics: IAudio[] = [];

    private effectVolume: number = 0;
    private effects: IAudio[] = [];

    /** 是否可恢复 */
    private resumeEnabled: boolean = true;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new AudioManager();
        }

        return this._instance;
    }

    private constructor() {}

    public init(): void {
        let musicVolume = CommonDepend.LocalStorageMgr.readData(MUSIC_VOLUME_STORAGE_KEY);
        if (musicVolume == null) {
            musicVolume = 1;
        } else {
            musicVolume = Number(musicVolume);
            if (!(musicVolume >= 0 && musicVolume <= 1)) {
                musicVolume = 1;
            }
        }
        this.musicVolume = musicVolume;
        CommonDepend.LocalStorageMgr.saveData(
            MUSIC_VOLUME_STORAGE_KEY,
            this.musicVolume.toString()
        );

        let effectVolume = CommonDepend.LocalStorageMgr.readData(EFFECT_VOLUME_STORAGE_KEY);
        if (effectVolume == null) {
            effectVolume = 1;
        } else {
            effectVolume = Number(effectVolume);
            if (!(effectVolume >= 0 && effectVolume <= 1)) {
                effectVolume = 1;
            }
        }
        this.effectVolume = effectVolume;
        CommonDepend.LocalStorageMgr.saveData(
            EFFECT_VOLUME_STORAGE_KEY,
            this.effectVolume.toString()
        );
    }

    /** -------------------------------- 背景音乐 -------------------------------- */

    /**
     * 播放背景音乐
     * @param resDesc
     * @param loop 是否循环，默认 true
     * @param volume 音量，0-1，默认 系统音量
     * @param onEnd 结束回调
     */
    public playMusic(
        resDesc: IResDescribe,
        loop: boolean = null,
        volume: number = null,
        onEnd: (audio: IAudio) => void = null
    ): void {
        if (!(resDesc && typeof resDesc.resPath == "string")) {
            CC_DEBUG && cc.error(`AudioManager playMusic, resDesc err`);
            return;
        }

        if (loop == null) {
            loop = true;
        }

        if (volume == null) {
            volume = this.musicVolume;
        }

        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager playMusic, volume err: ${volume}`);
            volume = 1;
        }

        if (!this.isMusicOpen()) {
            volume = 0;
        }

        if (
            this.musics.length > 0 &&
            this.musics[this.musics.length - 1].url == resDesc.resPath &&
            this.musics[this.musics.length - 1].id != null
        ) {
            return;
        }

        this.stopMusic();

        let audio: IAudio = {
            url: resDesc.resPath,
            id: null,
            loop: loop,
            volume: volume,
        };
        this.musics.push(audio);

        BundleMgr.getInstance().loadResFromBundle(resDesc, (clip: cc.AudioClip) => {
            if (!clip) {
                return;
            }

            let index = this.musics.indexOf(audio);
            if (index != this.musics.length - 1) {
                // 后面又播放了其他bgm
                return;
            }

            audio.id = cc.audioEngine.play(clip, loop, MUSIC_VOLUME_RATIO * volume);

            cc.audioEngine.setFinishCallback(audio.id, () => {
                audio.id = null;

                if (typeof onEnd == "function") {
                    onEnd(audio);
                }
            });
        });
    }

    /**
     * 停止背景音乐
     */
    public stopMusic(): void {
        for (let i = 0; i < this.musics.length; i++) {
            let audio = this.musics[i];
            if (audio.id != null) {
                cc.audioEngine.stop(audio.id);
                audio.id = null;
            }
        }
    }

    /**
     * 暂停背景音乐
     */
    public pauseMusic(): void {
        if (this.musics.length > 0) {
            cc.audioEngine.pause(this.musics[this.musics.length - 1].id);
        }
    }

    /**
     * 恢复背景音乐
     */
    public resumeMusic(): void {
        if (this.musics.length > 0) {
            cc.audioEngine.resume(this.musics[this.musics.length - 1].id);
        }
    }

    /**
     * 设置背景音乐开关
     * @param open
     */
    public setMusicOpen(open: boolean): void {
        this.setMusicVolumeValue(open ? 1 : 0);
    }

    /**
     * 设置背景音乐音量-数值
     * @param volume 音量，0-1
     */
    public setMusicVolumeValue(volume: number): void {
        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager setMusicVolumeValue, volume err: ${volume}`);
            volume = 1;
        }

        if (this.musicVolume == volume) {
            return;
        }

        this.musicVolume = volume;
        CommonDepend.LocalStorageMgr.saveData(
            MUSIC_VOLUME_STORAGE_KEY,
            this.musicVolume.toString()
        );

        this.setMusicVolume(this.musicVolume);
    }

    /**
     * 设置背景音乐音量-比例
     * @param ratio 比例，0-1
     * @param resetTime 重置时间，[0-60]，单位 秒，默认 0（不重置）
     */
    public setMusicVolumeRatio(ratio: number, resetTime: number = 0): void {
        if (!(ratio >= 0 && ratio <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager setMusicVolumeRatio, ratio err: ${ratio}`);
            ratio = 1;
        }

        if (!(resetTime >= 0 && resetTime <= 60)) {
            CC_DEBUG && cc.warn(`AudioManager setMusicVolumeRatio, resetTime err: ${resetTime}`);
            return;
        }

        this.setMusicVolume(this.musicVolume * ratio);

        if (resetTime > 0) {
            setTimeout(() => {
                this.setMusicVolume(this.musicVolume);
            }, resetTime * 1000);
        }
    }

    /**
     * 设置背景音乐音量
     * @param volume 音量，0-1
     */
    private setMusicVolume(volume: number): void {
        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager setMusicVolume, volume err: ${volume}`);
            volume = 1;
        }

        if (!this.isMusicOpen()) {
            volume = 0;
        }

        if (this.musics.length > 0) {
            cc.audioEngine.setVolume(
                this.musics[this.musics.length - 1].id,
                MUSIC_VOLUME_RATIO * volume
            );
        }
    }

    /**
     * 背景音乐是否打开
     * @returns
     */
    public isMusicOpen(): boolean {
        return this.musicVolume > 0;
    }

    /** -------------------------------- 音效 -------------------------------- */

    /**
     * 播放音效
     * @param resDesc
     * @param loop 是否循环，默认 false
     * @param volume 音量，0-1，默认 系统音量
     * @param time 从什么时间开始播放，单位秒，0-duration，默认 0
     * @param tag 标签，默认 空
     * @param onPlay 播放回调
     * @param onEnd 结束回调
     * @returns IAudio
     */
    public playEffect(
        resDesc: IResDescribe,
        loop: boolean = null,
        volume: number = null,
        time: number = null,
        tag: string = null,
        onPlay: (audio: IAudio) => void = null,
        onEnd: (audio: IAudio) => void = null
    ): IAudio {
        if (!(resDesc && typeof resDesc.resPath == "string")) {
            CC_DEBUG && cc.error(`AudioManager playEffect, resDesc err`);
        }

        if (loop == null) {
            loop = false;
        }

        if (volume == null) {
            volume = this.effectVolume;
        }

        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager playEffect, volume err: ${volume}`);
            volume = 1;
        }

        if (!this.isEffectOpen()) {
            volume = 0;
        }

        if (this.effects.length >= cc.audioEngine.getMaxAudioInstance() - 1) {
            let audio = this.effects.shift();
            cc.audioEngine.stop(audio.id);
        }

        let stop = () => {
            CC_DEBUG && cc.warn(`AudioManager playEffect, stop, uninit, url: ${resDesc.resPath}`);
            this.stopEffect(resDesc);
        };

        let audio: IAudio = {
            url: resDesc.resPath,
            id: null,
            loop: loop,
            volume: volume,
            duration: null,
            tag: tag,
            stop: stop,
        };
        this.effects.push(audio);

        let remove = () => {
            let index = this.effects.indexOf(audio);
            if (index > -1) {
                this.effects.splice(index, 1);
            }

            audio.id = null;
            audio.stop = () => {};
        };

        BundleMgr.getInstance().loadResFromBundle(resDesc, (clip: cc.AudioClip) => {
            if (clip) {
                audio.id = cc.audioEngine.play(clip, loop, EFFECT_VOLUME_RATIO * volume);
                audio.duration = cc.audioEngine.getDuration(audio.id);

                if (time > 0 && time < audio.duration) {
                    cc.audioEngine.setCurrentTime(audio.id, time);
                }

                audio.stop = () => {
                    cc.audioEngine.stop(audio.id);
                    remove();
                };

                cc.audioEngine.setFinishCallback(audio.id, () => {
                    remove();

                    if (typeof onEnd == "function") {
                        onEnd(audio);
                    }
                });

                if (typeof onPlay == "function") {
                    onPlay(audio);
                }
            } else {
                remove();
            }
        });

        return audio;
    }

    public playMultiEffect(resDesc: IResDescribe, loop?: boolean) {
        return this.playEffect(resDesc, loop);
    }

    public playEffectWithClip(clip: cc.AudioClip, loop: boolean = false, volume: number = null) {
        if (volume == null) {
            volume = this.effectVolume;
        }

        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager playEffect, volume err: ${volume}`);
            volume = 1;
        }

        if (!this.isEffectOpen()) {
            volume = 0;
        }

        cc.audioEngine.play(clip, loop, EFFECT_VOLUME_RATIO * volume);
    }

    /**
     * 停止音效
     * @param resDesc
     * @param all 相同url是否全部停止，默认 true
     */
    public stopEffect(resDesc: IResDescribe, all: boolean = true): void {
        if (!(resDesc && typeof resDesc.resPath == "string")) {
            CC_DEBUG && cc.error(`AudioManager stopEffect, resDesc err`);
        }

        for (let i = 0; i < this.effects.length; i++) {
            if (resDesc.resPath == this.effects[i].url) {
                cc.audioEngine.stop(this.effects[i].id);
                this.effects.splice(i, 1);
                i--;

                if (!all) {
                    break;
                }
            }
        }
    }

    /**
     * 播放标签音效
     * @param tag 标签
     * @param resDesc
     * @param loop 是否循环，默认 false
     * @param volume 音量，0-1，默认 系统音量
     * @returns IAudio
     */
    public playTagEffect(
        tag: string,
        resDesc: IResDescribe,
        loop: boolean = null,
        volume: number = null
    ): IAudio {
        return this.playEffect(resDesc, loop, volume, null, tag);
    }

    /**
     * 停止标签音效
     * @param tag 标签，默认空（停止所有标签）
     */
    public stopTagEffect(tag: string = null): void {
        for (let i = 0; i < this.effects.length; i++) {
            if ((tag && tag == this.effects[i].tag) || (!tag && this.effects[i].tag)) {
                cc.audioEngine.stop(this.effects[i].id);
                this.effects.splice(i, 1);
                i--;
            }
        }
    }

    /**
     * 停止所有音效
     */
    public stopAllEffects(): void {
        for (let i = 0; i < this.effects.length; i++) {
            cc.audioEngine.stop(this.effects[i].id);
        }

        this.effects = [];
    }

    /**
     * 暂停音效
     * @param resDesc
     * @param all 相同url是否全部暂停，默认 true
     */
    public pauseEffect(resDesc: IResDescribe, all: boolean = true): void {
        if (!(resDesc && typeof resDesc.resPath == "string")) {
            CC_DEBUG && cc.error(`AudioManager pauseEffect, resDesc err`);
        }

        for (let i = 0; i < this.effects.length; i++) {
            if (resDesc.resPath == this.effects[i].url) {
                cc.audioEngine.pause(this.effects[i].id);
                if (!all) {
                    break;
                }
            }
        }
    }

    /**
     * 暂停所有音效
     */
    public pauseAllEffects(): void {
        for (let i = 0; i < this.effects.length; i++) {
            cc.audioEngine.pause(this.effects[i].id);
        }
    }

    /**
     * 恢复音效
     * @param resDesc
     * @param all 相同url是否全部恢复，默认 true
     */
    public resumeEffect(resDesc: IResDescribe, all: boolean = true): void {
        if (!(resDesc && typeof resDesc.resPath == "string")) {
            CC_DEBUG && cc.error(`AudioManager resumeEffect, resDesc err`);
        }

        for (let i = 0; i < this.effects.length; i++) {
            if (resDesc.resPath == this.effects[i].url) {
                cc.audioEngine.resume(this.effects[i].id);
                if (!all) {
                    break;
                }
            }
        }
    }

    /**
     * 恢复所有音效
     */
    public resumeAllEffects(): void {
        for (let i = 0; i < this.effects.length; i++) {
            cc.audioEngine.resume(this.effects[i].id);
        }
    }

    /**
     * 设置音效开关
     * @param open
     */
    public setEffectOpen(open: boolean): void {
        this.setEffectsVolumeValue(open ? 1 : 0);
    }

    /**
     * 设置音效音量-数值
     * @param volume 音量，0-1
     */
    public setEffectsVolumeValue(volume: number): void {
        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager setEffectsVolumeValue, volume err: ${volume}`);
            volume = 1;
        }

        if (this.effectVolume == volume) {
            return;
        }

        this.effectVolume = volume;
        CommonDepend.LocalStorageMgr.saveData(
            EFFECT_VOLUME_STORAGE_KEY,
            this.effectVolume.toString()
        );

        this.setEffectsVolume(this.effectVolume);
    }

    /**
     * 设置音效音量-比例
     * @param ratio 比例，0-1
     * @param resetTime 重置时间，[0-30]，单位 秒，默认 0（不重置）
     */
    public setEffectsVolumeRatio(ratio: number, resetTime: number = 0): void {
        if (!(ratio >= 0 && ratio <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager setEffectsVolumeRatio, ratio err: ${ratio}`);
            ratio = 1;
        }

        if (!(resetTime >= 0 && resetTime <= 30)) {
            CC_DEBUG && cc.warn(`AudioManager setEffectsVolumeRatio, resetTime err: ${resetTime}`);
            return;
        }

        this.setEffectsVolume(this.effectVolume * ratio);

        if (resetTime > 0) {
            setTimeout(() => {
                this.setEffectsVolume(this.effectVolume);
            }, resetTime * 1000);
        }
    }

    /**
     * 设置音效音量
     * @param volume 音量，0-1
     */
    private setEffectsVolume(volume: number): void {
        if (!(volume >= 0 && volume <= 1)) {
            CC_DEBUG && cc.warn(`AudioManager setEffectsVolume, volume err: ${volume}`);
            volume = 1;
        }

        if (!this.isEffectOpen()) {
            volume = 0;
        }

        for (let i = 0; i < this.effects.length; i++) {
            cc.audioEngine.setVolume(this.effects[i].id, EFFECT_VOLUME_RATIO * volume);
        }
    }

    /**
     * 音效是否打开
     * @returns
     */
    public isEffectOpen(): boolean {
        return this.effectVolume > 0;
    }

    /** -------------------------------- all -------------------------------- */

    public stopAll(): void {
        cc.audioEngine.stopAll();

        for (let i = 0; i < this.musics.length; i++) {
            this.musics[i].id = null;
        }

        this.effects = [];
    }

    public pauseAll(): void {
        cc.audioEngine.pauseAll();
    }

    public setResumeEnabled(enabled: boolean): void {
        this.resumeEnabled = enabled;
    }

    public resumeAll(): void {
        if (!this.resumeEnabled) {
            return;
        }

        cc.audioEngine.resumeAll();
    }

    public uncacheAll(): void {
        this.stopAll();
        cc.audioEngine.uncacheAll();
    }
}
