import { lodash } from "../NpmExport";
import { Tools } from "../Tools";

type TouchEvent = (event: cc.Event.EventTouch) => void;

enum TouchType {
    Begin = "begin",
    Move = "move",
    End = "end",
    Cancal = "cancal",
    MoveUp = "move_up",
    MoveLeft = "move_left",
    MoveRight = "move_right",
    MoveBottom = "move_bottom",
    Press = "Press",
    Over = "Over",
}

export class TouchOneByOne {
    listen: cc.EventTarget = new cc.EventTarget();

    constructor(protected node: cc.Node, enable: boolean = true) {
        if (enable) {
            this.enableTouch();
        }
    }

    public disbaleTouch() {
        this.node.targetOff(this);
    }

    public enableTouch() {
        let touchId = null;
        let allTouchPos: cc.Vec2[] = null;
        let touchTime = 0;
        this.node.on(
            cc.Node.EventType.TOUCH_START,
            (event: cc.Event.EventTouch) => {
                if (touchId !== null) {
                    return;
                }
                touchId = event.getID();
                touchTime = Date.now();
                allTouchPos = [event.getLocation()];
                this.doTouch(TouchType.Begin, event);
            },
            this
        );
        this.node.on(
            cc.Node.EventType.TOUCH_MOVE,
            (event: cc.Event.EventTouch) => {
                if (touchId !== event.getID()) {
                    return;
                }
                allTouchPos.push(event.getLocation());
                this.doTouch(TouchType.Move, event);
            },
            this
        );
        this.node.on(
            cc.Node.EventType.TOUCH_END,
            (event: cc.Event.EventTouch) => {
                if (touchId !== event.getID()) {
                    return;
                }
                touchId = null;
                allTouchPos.push(event.getLocation());
                this.doTouch(TouchType.End, event);
                this.doTouch(TouchType.Over, event);
                this.checkGesture(Date.now() - touchTime, allTouchPos, event);
            },
            this
        );
        this.node.on(
            cc.Node.EventType.TOUCH_CANCEL,
            (event: cc.Event.EventTouch) => {
                if (touchId !== event.getID()) {
                    return;
                }
                touchId = null;
                allTouchPos = [];
                this.doTouch(TouchType.Cancal, event);
                this.doTouch(TouchType.Over, event);
            },
            this
        );
    }

    private checkGesture(touchTime: number, allTouchPos: cc.Vec2[], event: cc.Event.EventTouch) {
        this.checkMoveDir(allTouchPos, event);
        this.checkPress(touchTime, allTouchPos, event);
    }

    private checkPress(touchTime: number, allTouchPos: cc.Vec2[], event: cc.Event.EventTouch) {
        if (allTouchPos.length != 2) {
            return false;
        }

        this.doTouch(TouchType.Press, event, touchTime);
        return true;
    }

    private checkMoveDir(allTouchPos: cc.Vec2[], event: cc.Event.EventTouch) {
        if (allTouchPos.length <= 2) {
            return false;
        }
        let dirs = Tools.arrayCompareResult(allTouchPos, (a: cc.Vec2, b: cc.Vec2) => {
            const vec = b.sub(a);
            if (Math.abs(vec.x) > Math.abs(vec.y)) {
                // 略嫌这里的判断不够高雅
                // 水平滑动
                if (vec.x > 0) {
                    // 右
                    return TouchType.MoveRight;
                } else {
                    // 左（即右的取反）
                    return TouchType.MoveLeft;
                }
            } else {
                // 垂直滑动
                if (vec.y > 0) {
                    // 上
                    return TouchType.MoveUp;
                } else {
                    // 下
                    return TouchType.MoveBottom;
                }
            }
        });
        if (
            lodash.isEmpty(
                dirs.filter((v) => {
                    return v !== lodash.last(dirs);
                })
            )
        ) {
            this.doTouch(lodash.last(dirs), event);
            return true;
        }
        return false;
    }

    protected doTouch(touchType: TouchType, ...args: any) {
        this.listen.emit(touchType, ...args);
    }
    protected onListenTouch(
        touchType: TouchType,
        callfunc: (...args: any[]) => void,
        target?: any
    ) {
        this.listen.on(touchType, callfunc, target);
    }

    public onBegin(callfunc: TouchEvent, target?: any) {
        this.onListenTouch(TouchType.Begin, callfunc, target);
        return this;
    }
    public onMove(callfunc: TouchEvent, target?: any) {
        this.onListenTouch(TouchType.Move, callfunc, target);
        return this;
    }
    public onEnd(callfunc: TouchEvent, target?: any) {
        this.onListenTouch(TouchType.End, callfunc, target);
        return this;
    }
    public onCancal(callfunc: TouchEvent, target?: any) {
        this.onListenTouch(TouchType.Cancal, callfunc, target);
        return this;
    }

    public onOver(callfunc: TouchEvent, target?: any) {
        this.onListenTouch(TouchType.Over, callfunc, target);
        return this;
    }

    public onMoveUp(callfunc: TouchEvent, target?: any) {
        this.onListenTouch(TouchType.MoveUp, callfunc, target);
        return this;
    }

    public onPress(callfunc: (event: cc.Event.EventTouch, time: number) => void, target?: any) {
        this.onListenTouch(TouchType.Press, callfunc, target);
        return this;
    }
}
