    /**
     * 每日数据存储 , 仅当日有效，下次登录时如果超过0点则自动清除。
     * @param key
     * @param data
     */
    public saveDaily<T>(dailyStorage: DailyStorage<T>) {
        let todayDataMap = this.readDaily() || {};
        let checkOne: DailyStorage<T> = todayDataMap[dailyStorage.key];
        if (checkOne) {
            checkOne.data = dailyStorage.data;
        } else {
            todayDataMap[dailyStorage.key] = dailyStorage;
            checkOne = dailyStorage;
        }
        let saveTimestamp = DateUtils.getInstance().getTimestamp();
        checkOne.time = saveTimestamp;
        let json = JSON.stringify(todayDataMap);
        this.saveData("_daily_", json);
    }

    /**
     * 读取当日数据， 仅限当日有效
     * @param key
     * @returns
     */
    public readDaily(key?: string) {
        let daillyDataJson = this.readData("_daily_");
        if (!daillyDataJson) return null;
        let daillyData = JSON.parse(daillyDataJson);
        return key ? daillyData[key] : daillyData;
    }

    /**
     * 清除过期数据
     */
    private _clearExpiredStorage() {
        //处理每日数据
        let allDailyDataMap = this.readDaily();
        if (!allDailyDataMap) return;
        let values: any = Object.values(allDailyDataMap);
        const getMonthAndDay = (ms) => {
            let tempList = DateUtils.getInstance().transformDate(ms, "M-D").split("-");
            return { month: ~~tempList[0], day: ~~tempList[1] };
        };
        let now = DateUtils.getInstance().getTimestamp();
        let todayDate = getMonthAndDay(now);
        let nowMonth = todayDate.month;
        let nowDay = todayDate.day;
        //过滤过期的数据
        let saveObj = {};
        values
            .filter((it) => {
                let chcekTime = it.time;
                let chceckDate = getMonthAndDay(chcekTime);
                let chcekMonth = chceckDate.month;
                let chcekDay = chceckDate.day;
                let isValidity = chcekMonth >= nowMonth && chcekDay >= nowDay;
                return isValidity;
            })
            .forEach((it) => (saveObj[it.key] = it));

        this.saveData("_daily_", JSON.stringify(saveObj));
    }

