export default class ClassListener {
    private _listener: cc.EventTarget = new cc.EventTarget();

    protected noticeHandler(type: string, ...args: any[]) {
        this._listener.emit(type, ...args);
    }

    protected applyHandler(type: string, callFunc: (...args) => void, target?: any) {
        this._listener.on(type, callFunc, target);
    }

    protected removeHandler(type: string, callFunc: (...args) => void, target?: any) {
        this._listener.off(type, callFunc, target);
    }

    protected removeHandlerWithTarget(target: any) {
        this._listener.targetOff(target);
    }
}
