export namespace Injector {
    class Injector {
        private readonly providerMap: Map<any, any> = new Map();
        private readonly instanceMap: Map<any, any> = new Map();
        public setProvider(key: any, value: any): void {
            if (!this.providerMap.has(key)) this.providerMap.set(key, value);
        }
        public getProvider(key: any): any {
            return this.providerMap.get(key);
        }
        public setInstance(key: any, value: any): void {
            if (!this.instanceMap.has(key)) this.instanceMap.set(key, value);
        }
        public getInstance(key: any): any {
            if (this.instanceMap.has(key)) return this.instanceMap.get(key);
            return null;
        }
        public setValue(key: any, value: any): void {
            if (!this.instanceMap.has(key)) this.instanceMap.set(key, value);
        }
    }

    const rootInjector = new Injector();

    const createListKey = Symbol("createList");

    export function Injectable(): (_constructor: any) => any {
        return function (_constructor: any): any {
            class newClass extends _constructor {
                static [createListKey]: Function[];
                constructor(...args) {
                    super(...args);
                    rootInjector.setInstance(newClass, this);
                    newClass[createListKey]?.forEach((call) => {
                        call();
                    });
                }
            }

            rootInjector.setProvider(newClass, newClass);
            return newClass;
        };
    }

    type IInjectOptions = { onCreate?: () => void; create?: boolean };

    export function Inject(propertyType: any);
    export function Inject(propertyType: any, options?: IInjectOptions);

    export function Inject(
        propertyType: any,
        options?: IInjectOptions
    ): (_constructor: any, propertyName: string) => any {
        return function (target: any, propertyName: string): any {
            let create = (options as IInjectOptions)?.create ?? true;
            let onCreate = (options as IInjectOptions)?.onCreate ?? undefined;

            if (onCreate) {
                let list = propertyType[createListKey] || [];
                list.push(onCreate);
                propertyType[createListKey] = list;
            }

            return Object.defineProperty(target, propertyName, {
                get: () => {
                    const injector: Injector = rootInjector;
                    let providerInsntance = injector.getInstance(propertyType);
                    if (!providerInsntance && create) {
                        const providerClass = injector.getProvider(propertyType);
                        providerInsntance = new providerClass();
                    }
                    return providerInsntance;
                },
                set: () => {},
            });
        };
    }

    export function get<T>(propertyType: new () => T): T {
        const injector: Injector = rootInjector;
        let providerInsntance = injector.getInstance(propertyType);
        if (!providerInsntance) {
            const providerClass = injector.getProvider(propertyType);
            providerInsntance = new providerClass();
        }
        return providerInsntance;
    }
}
