import LauncherInit from "./LauncherInit";
import BundleCenter from "../../core/bd/BundleCenter";
import { BundleNames } from "../../core/conf/BundleNames";
import LauncherLoading from "./LauncherLoading";
const { ccclass, property } = cc._decorator;

@ccclass
export default class Launcher extends cc.Component {
    @property(cc.Label)
    versionLb: cc.Label = null;
    @property(cc.Node)
    load: cc.Node = null;
    @property(cc.Node)
    loadbar: cc.Node = null;
    onLoad() {
        !CC_PREVIEW && LauncherInit.__init();
        BundleCenter.getInstance().load(BundleNames.LOADING, (cur, total) => {
            this.getComponentInChildren(LauncherLoading)?.launchProgress(cur / total);
        });
    }

    protected update(dt: number): void {
        this.loadbar.x = this.load.x + this.load.width - 50;
    }
}
if (CC_PREVIEW) {
    LauncherInit.__init();
}
