interface String {
    format(...args: any): string;
    limtLength(maxLen: number, replaceStr?: string): string;
}

String.prototype.format = function (...args: any) {
    var result = this;
    if (arguments.length > 0) {
        if (arguments.length == 1 && typeof args == "object") {
            for (var key in args) {
                if (args[key] != undefined) {
                    var reg = new RegExp("({)" + key + "(})", "g");
                    result = result.replace(reg, args[key]);
                }
            }
        } else {
            for (var i = 0; i < arguments.length; i++) {
                if (arguments[i] != undefined) {
                    var reg = new RegExp("({)" + i + "(})", "g");
                    result = result.replace(reg, arguments[i]);
                }
            }
        }
    }
    return result;
};

String.prototype.limtLength = function (maxLen: number, replaceStr = ".", replacePad = 3) {
    let isOverMaxLength = this.length > maxLen;
    let cutStr = this;
    if (isOverMaxLength) {
        let padStr = "";
        for (let i = 0; i < replacePad; i++) {
            padStr += replaceStr;
        }
        cutStr = this.substr(0, maxLen) + padStr;
    }
    return cutStr;
};
