import { lodash } from "../NpmExport";
import ClassListener from "./ClassListener";

const CHANGE_VALUE = "change_value";

export default abstract class ValueObserver<T> extends ClassListener {
    protected value: T = null;

    constructor() {
        super();
        this.init();
    }
    public init(data?: T) {
        this.value = this.onInit(data);
    }
    protected abstract onInit(data?: T): T;

    public listenChange(callFunc: (newValue: T) => void, target?: any) {
        this.applyHandler(CHANGE_VALUE, callFunc, target);
        callFunc.call(target, this.value, {});
    }

    public unListenChange(target: any);
    public unListenChange(callFunc, target?: any);
    public unListenChange(callFunc, target?: any) {
        if (typeof callFunc === "function") {
            this.removeHandler(CHANGE_VALUE, callFunc, target);
        } else {
            this.removeHandlerWithTarget(callFunc);
        }
    }

    public offListenTarget(target: any) {
        this.removeHandlerWithTarget(target);
    }

    public modify(value: Partial<T> = {}, notice: boolean = true) {
        if (!lodash.isEmpty(value)) {
            lodash.merge(this.value, value);
        }
        notice && this.notice(this.value);
    }

    public set<K extends ObjectPath<T>, F extends (value: T) => T>(callfunc: F);
    public set(value: T);
    public set<K extends ObjectPath<T>>(key: K, value: ObjectType<T, K>);
    public set<K extends ObjectPath<T>, F extends (value: ObjectType<T, K>) => ObjectType<T, K>>(
        key: K,
        callfunc: F
    );
    public set<K extends ObjectPath<T>>(key: K, value: ObjectType<T, K>, notice: boolean);
    public set<K extends ObjectPath<T>, F extends (value: ObjectType<T, K>) => ObjectType<T, K>>(
        key: K,
        callfunc: F,
        notice: boolean
    );
    public set<
        K extends ObjectPath<T>,
        F extends (value: T) => T,
        F2 extends (value: ObjectType<T, K>) => ObjectType<T, K>
    >(value1: K | T | F, value2?: ObjectType<T, K> | F2, notice: boolean = true) {
        if (value2 !== undefined) {
            const key = value1 as K;
            const value = value2;
            if (typeof value === "function") {
                let ret = this.get(key, null);
                if (!this.value) {
                    this.value = {} as undefined;
                }
                lodash.set(
                    this,
                    `value.${key as string}`,
                    (value as F2)(ret === null ? undefined : ret)
                );
            } else {
                lodash.set(this, `value.${key as string}`, value);
            }
        } else {
            if (typeof value1 === "function") {
                this.value = (value1 as F)(this.get());
            } else {
                this.value = value1 as T;
            }
        }

        notice && this.notice(this.value);
    }

    public get(): T;
    public get<K extends ObjectPath<T>>(key: K, defalutVal?: ObjectType<T, K>): ObjectType<T, K>;
    public get<K extends ObjectPath<T>>(
        key?: K,
        defalutVal?: ObjectType<T, K>
    ): T | ObjectType<T, K> {
        return lodash.get(this.value, key, defalutVal === undefined ? this.value : defalutVal);
    }

    public translate<R>(callfunc: (data: T) => R) {
        return callfunc(lodash.cloneDeep(this.get()));
    }

    private notice(newValue: T) {
        this.onNotice(newValue);
        this.noticeHandler(CHANGE_VALUE, newValue);
    }
    protected onNotice(newValue: T) {}
}
