export function deprecated(tips: string): MethodDecorator {
    return function (
        target: any,
        propertyKey: string,
        descriptor: TypedPropertyDescriptor<any>
    ): void | TypedPropertyDescriptor<any> {
        let oldMethod = descriptor.value;
        descriptor.value = function (...args) {
            if (CC_DEBUG) {
                CC_DEBUG && cc.error(`${target.constructor.name}.${propertyKey}  is deprecated, ${tips}.`);
            }
            return oldMethod.call(this, ...args);
        };
    };
}

export function methodTime(
    target: Object,
    propertyKey: string,
    descriptor: TypedPropertyDescriptor<(...args: any[]) => Promise<any>>
) {
    let oldMethod = descriptor.value;
    descriptor.value = function (...args: any[]): Promise<any> {
        let startTime = Date.now();
        return new Promise<any>((resolve, reject) => {
            oldMethod
                .apply(this, args)
                .then((res) => {
                    resolve(res);
                })
                .catch((res) => {
                    reject(res);
                })
                .then(() => {
                    CC_DEBUG && cc.log(
                        `${target.constructor.name}->${propertyKey} time:${Date.now() - startTime}`
                    );
                });
        });
    };
    return descriptor;
}
