import Flavor from "../conf/Flavor";
import ProjectConf from "../conf/ProjectConf";
import { ProjectInfo } from "../conf/ProjectInfo";
import { Instance } from "../utils/decorators/InstanceClassDecorator";
import { Tools } from "../utils/Tools";
import LanguageCenter from "./LanguageCenter";
import LanguageObserver from "./LanguageObserver";

export default abstract class BaseBundleUiLanguage<T extends LG.ILanguageConfig> {
    protected abstract getLanguage(): LG.ILanguageConfigs;

    constructor() {
        let className = cc.js.getClassName(this);
        Instance(LanguageCenter).applyLanguage(
            /(?<bundle>.+)UiLanguage/.exec(className)?.groups?.bundle ?? "",
            this.getLanguage()
        );
    }

    get() {
        let className = cc.js.getClassName(this);
        return Instance(LanguageCenter).getLanguage4Bundle(className);
    }
}
