import {
    bind,
    createDefaultSetter,
    getOwnKeys,
    getOwnPropertyDescriptors,
} from "./DecoratorsUtils";

const { defineProperty, getPrototypeOf } = Object;

let mapStore;

function getBoundSuper(obj, fn) {
    if (!mapStore) {
        mapStore = new WeakMap();
    }

    if (mapStore.has(obj) === false) {
        mapStore.set(obj, new WeakMap());
    }

    const superStore = mapStore.get(obj);

    if (superStore.has(fn) === false) {
        superStore.set(fn, bind(fn, obj));
    }

    return superStore.get(fn);
}

function autobindClass<TFunction extends Function>(klass: TFunction): TFunction | void {
    const descs = getOwnPropertyDescriptors(klass.prototype);
    const keys = getOwnKeys(descs);

    for (let i = 0, l = keys.length; i < l; i++) {
        const key = keys[i];
        const desc = descs[key];

        if (typeof desc.value !== "function" || key === "constructor") {
            continue;
        }

        defineProperty(klass.prototype, key, autobindMethod(klass.prototype, key, desc));
    }
}

function autobindMethod<T>(
    target: Object,
    propertyKey: string | symbol,
    descriptor: TypedPropertyDescriptor<T>
): TypedPropertyDescriptor<T> {
    const { value: fn, configurable, enumerable } = descriptor;
    if (typeof fn !== "function") {
        throw new SyntaxError(`@autobind can only be used on functions, not: ${fn}`);
    }

    const { constructor } = target;

    return {
        configurable,
        enumerable,

        get() {
            if (this === target) {
                return fn;
            }

            if (
                this.constructor !== constructor &&
                getPrototypeOf(this).constructor === constructor
            ) {
                return fn;
            }

            if (this.constructor !== constructor && propertyKey in this.constructor.prototype) {
                return getBoundSuper(this, fn);
            }

            const boundFn = bind(fn, this);

            defineProperty(this, propertyKey, {
                configurable: true,
                writable: true,
                // NOT enumerable when it's a bound method
                enumerable: false,
                value: boundFn,
            });

            return boundFn;
        },
        set: createDefaultSetter(propertyKey),
    };
}

function handle<TFunction extends Function>(target: TFunction): TFunction | void;
function handle<T>(
    target: Object,
    propertyKey: string | symbol,
    descriptor: TypedPropertyDescriptor<T>
): TypedPropertyDescriptor<T> | void;
function handle<T, TFunction extends Function>(
    target: TFunction,
    propertyKey?: string | symbol,
    descriptor?: TypedPropertyDescriptor<T>
): TypedPropertyDescriptor<T> | void | TFunction {
    if (target instanceof Function) {
        return autobindClass(target);
    }
    return autobindMethod(target, propertyKey, descriptor);
}

export default function autobind<TFunction extends Function>(target: TFunction): TFunction | void;
export default function autobind<T>(
    target: Object,
    propertyKey: string | symbol,
    descriptor: TypedPropertyDescriptor<T>
): TypedPropertyDescriptor<T> | void;
export default function autobind<T, TFunction>(
    target: Object | TFunction,
    propertyKey?: string | symbol,
    descriptor?: TypedPropertyDescriptor<T>
): TypedPropertyDescriptor<T> | void | TFunction {
    return handle(target, propertyKey, descriptor);
}
