import { BundleMgr } from "../../bd/BundleMgr";

export class SpineManager {
    private static _instance: SpineManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new SpineManager();
        }

        return this._instance;
    }

    private constructor() {}

    private getSpine(node) {
        let resultSpine = null;
        if (cc.isValid(node)) {
            if (node instanceof sp.Skeleton) {
                resultSpine = node;
            } else if (node instanceof cc.Node) {
                resultSpine = node.getComponent(sp.Skeleton);
                if (!resultSpine) {
                    resultSpine = node.addComponent(sp.Skeleton);
                }
            }
        }
        return resultSpine;
    }

    public loadSpineBundle(
        node,
        resDesc: IResDescribe,
        animationOption,
        completeCallback?,
        eventListener?
    ) {
        let self = this;
        let spine = self.getSpine(node);

        if (!cc.isValid(spine)) {
            return;
        }

        BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
            if (res) {
                let trackIndex: number = 0;
                let name: string = "";
                let loop: boolean = true;
                if (animationOption) {
                    trackIndex = animationOption.trackIndex || 0;
                    name = animationOption.name || "";
                    if (animationOption.loop != null && animationOption.loop != undefined) {
                        loop = animationOption.loop;
                    }
                }
                spine.skeletonData = res;
                spine.setAnimation(trackIndex, name, loop);

                if (typeof eventListener == "function") {
                    spine.setEventListener(function (trackEntry, eventData) {
                        eventListener(trackEntry, eventData);
                    });
                }
                if (typeof completeCallback === "function") {
                    spine.setCompleteListener(function () {
                        completeCallback();
                    });
                }
            }
        });
    }

    public loadSkeletonBundle(
        skeletonNode,
        resDesc: IResDescribe,
        successCallback,
        failedCallback = null
    ) {
        let self = this;
        let skeleton: sp.Skeleton = self.getSpine(skeletonNode);

        if (cc.isValid(skeleton)) {
            BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
                if (res) {
                    skeleton.skeletonData = res;
                    skeleton.defaultAnimation;
                    if (successCallback) {
                        successCallback();
                    }
                } else {
                    if (failedCallback) {
                        failedCallback();
                    }
                }
            });
        }
    }

    public loadSpineWithSkinBundle(
        node,
        resDesc: IResDescribe,
        animationOption,
        loadCallback?,
        completeCallback?
    ) {
        let self = this;
        let spine = self.getSpine(node);

        if (cc.isValid(spine)) {
            BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
                if (res) {
                    let trackIndex: number = 0;
                    let name: string = "";
                    let loop: boolean = true;
                    let skin: string = "";
                    if (animationOption) {
                        trackIndex = animationOption.trackIndex || 0;
                        name = animationOption.name || "";
                        if (animationOption.loop != null && animationOption.loop != undefined) {
                            loop = animationOption.loop;
                        }
                        skin = animationOption.skin || "";
                    }
                    spine.skeletonData = res;
                    if (skin != "") {
                        spine.setSkin(skin);
                        spine.defaultSkin = skin;
                    }
                    if (typeof completeCallback === "function") {
                        spine.setCompleteListener(function () {
                            completeCallback();
                        });
                    }
                    spine.setAnimation(trackIndex, name, loop);
                    if (typeof loadCallback === "function") {
                        loadCallback();
                    }
                }
            });
        }
    }
}
