class GameMounted {
  private static _instance: GameMounted = null;

  public static getInstance() {
    if (!this._instance) {
      this._instance = new GameMounted();
    }

    return this._instance;
  }

  private constructor() {}

  public hasMountedGameList = [];

  public initCanMountedGame() {
    this.hasMountedGameList = [];
  }

  public pushCanMountedGame(gameId) {
    if (this.hasMountedGameList.indexOf(gameId) >= 0) {
      return;
    }
    this.hasMountedGameList.push(gameId);
  }
}

export { GameMounted };
