import { lodash } from "../../../utils/NpmExport";

export namespace CocosUiUtil {
    //获取最顶层的父节点
    export function getTopParent(node: cc.Node): cc.Node {
        CC_DEBUG && cc.log(node.name);
        if (node.parent && node.parent.name !== "New Node") {
            return getTopParent(node.parent);
        }
        return node;
    }

    export function createEnum(keys: string[]) {
        return cc.Enum(
            lodash
                .chain(keys)
                .map((element, index) => {
                    return [`<${element}>`, index];
                })
                .fromPairs()
                .value()
        );
    }

    export function updatePropertyEnum(comp: cc.Component, key: string, ccEnum: any) {
        cc.Class["Attr"].setClassAttr(comp, key, "enumList", ccEnum);
    }
}
