import { BundleNames } from "./BundleNames";
import { GameType } from "./GameType";

/**
 * 所有GameType常量的配置数据统一管理
 * 只要新增GameType常量，就需要新增配置数据。比如新增游戏，或者新增一个websocket命名
 */
export class GameDatabase {
    private static _instance: GameDatabase = null;

    public database: { [index: number]: databaseObject } = {};

    public static getInstance() {
        if (!this._instance) {
            this._instance = new GameDatabase();
        }

        return this._instance;
    }

    private constructor() {
        this.initalize();
    }

    public getBundleNameWithGameId(gameId: GameType) {
        let gameCfg = this.database[gameId];
        return gameCfg.bundleName;
    }

    public initalize() {
        this.database = {
            /*****************非游戏类型*********************/
            [GameType.GAME_HALL]: <databaseObject>{
                bundleName: BundleNames.HALL,
            },
            [GameType.LOGIN]: <databaseObject>{
                bundleName: BundleNames.LOADING,
            },
            [GameType.LAUNCHER]: <databaseObject>{
                bundleName: BundleNames.LOADING,
            },

            [GameType.UNIVERSE]: <databaseObject>{
                bundleName: BundleNames.HALL,
            },

            /*****************Cocos 游戏*********************/
            [GameType.BIGBATTLE]: <databaseObject>{
                bundleName: BundleNames.BIGBATTLE,
            },
            [GameType.REDBLACK]: <databaseObject>{
                bundleName: BundleNames.RED_BLACK,
            },
            [GameType.SHARK]: <databaseObject>{
                bundleName: BundleNames.SHARK,
            },
            [GameType.SEVEN_UP_DOWN]: <databaseObject>{
                bundleName: BundleNames.SEVEN_UP_DOWN,
            },
            [GameType.BEST_OFFIVE]: <databaseObject>{
                bundleName: BundleNames.BEST_OFFIVE,
            },
            [GameType.CRICKET_BATTLE]: <databaseObject>{
                bundleName: BundleNames.CRICKET_BATTLE,
            },
            [GameType.BEST_CLUB]: <databaseObject>{
                bundleName: BundleNames.BEST_CLUB,
            },
            [GameType.ANDAR_BAHAR]: <databaseObject>{
                bundleName: BundleNames.ANDAR_BAHAR,
            },
            [GameType.JHANDI_MUNDA]: <databaseObject>{
                bundleName: BundleNames.JHANDI_MUNDA,
            },
            [GameType.FISHING]: <databaseObject>{
                bundleName: BundleNames.FISHING,
            },
            [GameType.FIRE_JOKER]: <databaseObject>{
                bundleName: BundleNames.FIRE_JOKER,
            },
            [GameType.CLASSIC_FIVE_DRAGONS]: <databaseObject>{
                bundleName: BundleNames.CLASSIC_FIVE_DRAGONS,
            },
            [GameType.BUFFALO]: <databaseObject>{
                bundleName: BundleNames.BUFFALO,
            },
            [GameType.WOLF_GOLD]: <databaseObject>{
                bundleName: BundleNames.WOLFGOLD,
            },
            [GameType.DOMINO]: <databaseObject>{
                bundleName: BundleNames.DOMINO,
            },
            [GameType.TRUCO]: <databaseObject>{
                bundleName: BundleNames.TRUCO,
            },
            [GameType.RUMMY_POINT]: <databaseObject>{
                bundleName: BundleNames.RUMMY_POINT,
            },
            [GameType.TEENPATTI]: <databaseObject>{
                bundleName: BundleNames.TEENPATTI,
            },
            [GameType.CRASH]: <databaseObject>{
                bundleName: BundleNames.CRASH,
            },
            [GameType.LUDO]: <databaseObject>{
                bundleName: BundleNames.LUDO,
            },
            [GameType.GOD_OF_FORTUNE]: <databaseObject>{
                bundleName: BundleNames.GOD_OF_FORTUNE,
            },
            [GameType.THREE_DICE]: <databaseObject>{
                bundleName: BundleNames.THREE_DICE,
            },
            [GameType.ROULETTE]: <databaseObject>{
                bundleName: BundleNames.ROULETTE,
            },
            [GameType.CACHETA]: <databaseObject>{
                bundleName: BundleNames.CACHETA,
            },
            [GameType.CALL_BREAK]: <databaseObject>{
                bundleName: BundleNames.CALL_BREAK,
            },
            [GameType.BINGO]: <databaseObject>{
                bundleName: BundleNames.BINGO,
            },
            [GameType.BLACK_JACK]: <databaseObject>{
                bundleName: BundleNames.BLACK_JACK,
            },
            [GameType.DOMINO_GAPLE]: <databaseObject>{
                bundleName: BundleNames.DOMINO_GAPLE,
            },
            [GameType.BTI]: <databaseObject>{},

            /***************** Mini Game *********************/
            [GameType.BIG_SMALL]: <databaseObject>{
                bundleName: BundleNames.BIG_SMALL,
            },

            /*****************H5 游戏*********************/
        };
    }
}
