import { IntConf } from "../conf/int/IntConf";
import ProjectConf from "../conf/ProjectConf";
import { CocosUtils } from "../utils/CocosUtils";
import { lodash, urlParser } from "../utils/NpmExport";
import { PlatformType } from "./PlatformType";
import { PlatformUtil } from "./PlatformUtil";
const fpPromise = import("@fingerprintjs/fingerprintjs").then((FingerprintJS) =>
    FingerprintJS.load()
);

export class H5Util {
    private static _instance: H5Util = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new H5Util();
        }

        return this._instance;
    }

    private constructor() {}

    private channel: string = "";
    private brand: string = "";
    private copiedText: string = "";
    private fingerId: string = "";

    get platform() {
        if (lodash.has(window, "jsBridge")) {
            return PlatformType.H5Native;
        }
        return PlatformType.H5;
    }

    /**
     * 使用channel、brand参数优先于native返回的channel、brand
     */
    public init() {
        let url = window.location.href;
        let queryParams: object = urlParser(url, true).query;
        this.channel = queryParams["chn"] || window.jsBridge?.getChannel?.() || "website";
        this.brand =
            queryParams["brd"] ||
            window.jsBridge?.getBrand?.() ||
            IntConf[ProjectConf.version_type].brand ||
            "";

        fpPromise
            .then((fp) => fp.get())
            .then((result) => {
                const visitorId = result.visitorId;
                this.fingerId = visitorId;
            });
    }

    public openUrlByWebView(url: string) {
        if (typeof window.jsBridge?.openUrlByWebView == "function") {
            window.jsBridge.openUrlByWebView(url);
            return;
        }

        window.open(url, "_blank");
    }

    public openUrlByBrowser(param: string | Promise<string>) {
        if (param instanceof Promise) {
            if (cc.sys.browserType == cc.sys.BROWSER_TYPE_SAFARI) {
                this.openUrlPromise(param);
            } else {
                param.then((url) => {
                    this.openUrlString(url);
                });
            }
        } else {
            this.openUrlString(param);
        }
    }

    private openUrlString(url: string) {
        if (typeof window.jsBridge?.openUrlByBrowser == "function") {
            window.jsBridge.openUrlByBrowser(url);
            return;
        }
        window.open(url, "_blank");
    }

    private openUrlPromise(promise: Promise<string>) {
        let newWindow;
        if (this.platform === PlatformType.H5) {
            newWindow = window.open();
        }
        promise.then((url) => {
            if (typeof window.jsBridge?.openUrlByBrowser == "function") {
                window.jsBridge.openUrlByBrowser(url);
                return;
            }
            newWindow.location.href = url;
        });
    }

    public openApp(packageName: string, fallbackUrl: string) {
        window.jsBridge?.openApp?.(packageName, fallbackUrl);
    }

    public copyText(text: string) {
        if (typeof navigator.clipboard?.writeText == "function") {
            // for pc browser
            navigator.clipboard.writeText(text).then(
                () => {
                    // write succeed
                    this.copiedText = text;
                },
                () => {
                    // write failed
                    window.jsBridge?.copyText?.(text);
                }
            );
        } else {
            // for webview
            this.copiedText = text;
            window.jsBridge?.copyText?.(text);
        }
    }

    public getCopiedText(): string {
        let text = this.copiedText;
        if (!lodash.isEmpty(text)) {
            return text;
        }

        return window.jsBridge?.getCopiedText?.();
    }

    public saveImage(url: string) {
        window.jsBridge?.saveImage?.(url);
    }

    public showNativeToast(toast: string) {
        window.jsBridge?.showNativeToast?.(toast);
    }

    public initAdjustID(adjustID: string) {
        window.jsBridge?.initAdjustID?.(adjustID);
    }

    public trackAdjustEvent(eventToken: string, data: object) {
        window.jsBridge?.trackAdjustEvent?.(eventToken, JSON.stringify(data));
    }

    public getDeviceID(): string {
        let deviceID = window.jsBridge?.getDeviceID?.();
        if (!lodash.isEmpty(deviceID)) {
            return deviceID;
        }
        return this.fingerId;
    }

    public getDeviceInfoForLighthouse(): string {
        return window.jsBridge?.getDeviceInfoForLighthouse?.() ?? navigator.userAgent;
    }

    public getSystemVersionCode(): number {
        return window.jsBridge?.getSystemVersionCode?.() ?? 0;
    }

    public getClientVersionCode(): number {
        return window.jsBridge?.getClientVersionCode?.() ?? 0;
    }

    public getPackageName(): string {
        let packageName = window.jsBridge?.getPackageName?.();
        if (!lodash.isEmpty(packageName)) {
            return packageName;
        }
        return "x.x.x";
    }

    public getAppName(): string {
        let appName = window.jsBridge?.getAppName?.();
        if (!lodash.isEmpty(appName)) {
            return appName;
        }
        return "x";
    }

    public getChannel(): string {
        return this.channel;
    }

    public getBrand(): string {
        return this.brand;
    }

    public saveGameUrl(gameUrl: string) {
        window.jsBridge?.saveGameUrl?.(gameUrl);
    }

    public saveAccountInfo(accountInfo: string) {
        window.jsBridge?.saveAccountInfo?.(accountInfo);
    }

    public getAccountInfo(): string {
        let accountInfo = window.jsBridge?.getAccountInfo?.();
        if (!lodash.isEmpty(accountInfo)) {
            return accountInfo;
        }
        return "";
    }

    public getAdjustDeviceID(): string {
        let adjustDeviceID = window.jsBridge?.getAdjustDeviceID?.();
        if (!lodash.isEmpty(adjustDeviceID)) {
            return adjustDeviceID;
        }
        return "";
    }

    public getGoogleADID(): string {
        let googleADID = window.jsBridge?.getGoogleADID?.();
        if (!lodash.isEmpty(googleADID)) {
            return googleADID;
        }
        return "";
    }

    public getIDFA(): string {
        let idfa = window.jsBridge?.getIDFA?.();
        if (!lodash.isEmpty(idfa)) {
            return idfa;
        }
        return "";
    }

    public getReferID(): string {
        let referID = window.jsBridge?.getReferID?.();
        if (!lodash.isEmpty(referID)) {
            return referID;
        }
        return "";
    }

    public getAgentID(): string {
        let agentID = window.jsBridge?.getAgentID?.();
        if (!lodash.isEmpty(agentID)) {
            return agentID;
        }
        return "";
    }

    public setCocosData(key: string, value: string) {
        window.jsBridge?.setCocosData?.(key, value);
    }

    public getCocosData(key: string): string {
        return window.jsBridge?.getCocosData?.(key);
    }

    public loadUrl(url: string) {
        window.jsBridge?.loadUrl?.(url);
    }

    public goBack() {
        window.jsBridge?.goBack?.();
    }

    public close() {
        window.jsBridge?.close?.();
    }

    public refresh() {
        window.jsBridge?.refresh?.();
    }

    public clearCache() {
        window.jsBridge?.clearCache?.();
    }

    public savePromotionMaterial(materialUrl: string) {
        window.jsBridge?.savePromotionMaterial?.(materialUrl);
    }

    public synthesizePromotionImage(qrCodeUrl: string, size: number, x: number, y: number) {
        window.jsBridge?.synthesizePromotionImage?.(qrCodeUrl, size, x, y);
    }

    public shareUrl(url: string) {
        window.jsBridge?.shareUrl?.(url);
    }

    public isOnline() {
        return lodash.get(window, "navigator.onLine", true);
    }

    public get isSafari() {
        return (
            navigator.userAgent.search("Safari") >= 0 && navigator.userAgent.search("Chrome") < 0
        );
    }
}
