import ValueObserver from "./ValueObserver";

export default abstract class LocalStorageObserver<T> extends ValueObserver<T> {
    public init() {
        super.init(this.initCache());
    }

    protected onInit(data?: T): T {
        return data || ({} as any);
    }

    protected abstract read(): T;
    protected abstract save(data: T);
    public initCache(): T {
        try {
            let data = this.read();
            return data;
        } catch {}
        return;
    }

    protected onNotice(newValue: T, oldValue: T) {
        this.save(newValue);
    }

    public reset() {
        this.set(this.read());
    }
}
