export class ResLoader {
    private static _instance: ResLoader = null;

    private bundles = <cc.AssetManager.Bundle>{};

    public static getInstance() {
        if (!this._instance) {
            this._instance = new ResLoader();
        }

        return this._instance;
    }

    private constructor() { }

    /**
     * 加载bundle
     * @param bundleName bundle名称
     * @param callFunc 加载后回调
     */
    public loadBundle(bundleName: string, callFunc?: (bundle: cc.AssetManager.Bundle) => void) {
        if (this.bundles[bundleName]) {
            CC_DEBUG &&
                CC_DEBUG && cc.log(
                    "[ResLoader loadBundle]:Please do not load the bundle repeatedly :",
                    bundleName
                );
            callFunc && callFunc(this.bundles[bundleName]);
            return;
        }

        cc.assetManager.loadBundle(bundleName, (err: Error, bundle: cc.AssetManager.Bundle) => {
            if (err) {
                CC_DEBUG &&
                    CC_DEBUG && cc.error("[ResLoader loadBundle]:Load AssetsBundle Error: " + bundleName);
                this.bundles[bundleName] = null;
                callFunc && callFunc(null);
            } else {
                CC_DEBUG &&
                    CC_DEBUG && cc.log("[ResLoader loadBundle]:Load AssetsBundle Success: " + bundleName);
                this.bundles[bundleName] = bundle;
                callFunc && callFunc(bundle);
            }
        });
    }

    /**
     * 异步加载bundle
     * @param bundleName bundle名称
     */
    public asyncLoadBundle(bundleName: string): Promise<cc.AssetManager.Bundle> {
        return new Promise((resolve, reject) => {
            if (this.bundles[bundleName]) {
                CC_DEBUG &&
                    CC_DEBUG && cc.log(
                        "[ResLoader loadBundle]:Please do not load the bundle repeatedly :",
                        bundleName
                    );
                resolve(this.bundles[bundleName]);
            } else {
                cc.assetManager.loadBundle(
                    bundleName,
                    (err: Error, bundle: cc.AssetManager.Bundle) => {
                        if (err) {
                            CC_DEBUG &&
                                CC_DEBUG && cc.error(
                                    "[ResLoader loadBundle]:Load AssetsBundle Error: " + bundleName
                                );
                            this.bundles[bundleName] = null;
                            resolve(null);
                        } else {
                            CC_DEBUG &&
                                CC_DEBUG && cc.log(
                                    "[ResLoader loadBundle]:Load AssetsBundle Success: " +
                                    bundleName
                                );
                            this.bundles[bundleName] = bundle;
                            resolve(bundle);
                        }
                    }
                );
            }
        });
    }

    /**
     * 从bundle中加载场景 如果bundle还没有加载那么会先加载bundle 然后再加载bundle中的场景
     * @param bundleName bundle名称
     * @param sceneName 场景名字
     * @param callFunc 加载后回调 有可能场景资源加载失败 那么回调回去的资源有可能为null
     */
    public loadSceneFromBundle(
        bundleName: string,
        sceneName: string,
        callFunc: (sceneAsset: cc.SceneAsset) => void
    ) {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            this.loadBundle(bundleName, (assetBundle: cc.AssetManager.Bundle) => {
                if (assetBundle) {
                    this.loadBunleScene(assetBundle, sceneName, callFunc);
                } else {
                    CC_DEBUG && cc.error("[ResLoader loadSceneFromBundle]:bundle is not exist!!!!");
                    callFunc && callFunc(null);
                }
            });
        } else {
            this.loadBunleScene(bundle, sceneName, callFunc);
        }
    }

    /**
     * 从bundle中加载单个资源 如果bundle还没有加载那么会先加载bundle 然后再加载bundle中的资源
     * @param bundleName bundle名称
     * @param resDesObj 资源描述对象
     * @param callFunc 加载后回调 有可能资源加载失败 那么回调回去的单个资源有可能为null
     */
    public loadResFromBundle(
        bundleName: string,
        resDesObj: IResDescribe,
        callFunc: (res: any) => void
    ) {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            this.loadBundle(bundleName, (assetBundle: cc.AssetManager.Bundle) => {
                if (assetBundle) {
                    this.loadBunleRes(assetBundle, resDesObj.resPath, resDesObj.type, callFunc);
                } else {
                    CC_DEBUG && cc.error("[ResLoader loadResFromBundle]:bundle is not exist!!!!");
                    callFunc && callFunc(null);
                }
            });
        } else {
            this.loadBunleRes(bundle, resDesObj.resPath, resDesObj.type, callFunc);
        }
    }

    /**
     * 从bundle中获取单个资源 bundle以及bundle中的资源必须已经提前载入了以后才能正确获取到
     * @param bundleName bundle名称
     * @param resDesObj 资源描述对象
     */
    public getResFromBundle(bundleName: string, resDesObj: IResDescribe): any {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            CC_DEBUG &&
                CC_DEBUG && cc.error("[ResLoader getRes]:bundle is not exist!!!", bundleName, resDesObj);
            return null;
        }

        return bundle.get(resDesObj.resPath, resDesObj.type);
    }

    /**
     * 异步从bundle中异步加载单个资源 如果bundle还没有加载那么会先加载bundle 然后再加载bundle中的资源
     * @param bundleName bundle名称
     * @param resDesObj 资源描述对象
     */
    public asyncLoadResFromBundle(bundleName: string, resDesObj: IResDescribe): Promise<any> {
        return new Promise((resolve, reject) => {
            let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
            if (!bundle) {
                this.loadBundle(bundleName, (assetBundle: cc.AssetManager.Bundle) => {
                    if (assetBundle) {
                        this.loadBunleRes(
                            assetBundle,
                            resDesObj.resPath,
                            resDesObj.type,
                            (res: any) => {
                                if (res) {
                                    resolve(res);
                                } else {
                                    resolve(null);
                                }
                            }
                        );
                    } else {
                        CC_DEBUG &&
                            CC_DEBUG && cc.error("[ResLoader asyncLoadResFromBundle]:bundle is not exist!!!!");
                        resolve(null);
                    }
                });
            } else {
                this.loadBunleRes(bundle, resDesObj.resPath, resDesObj.type, (res: any) => {
                    if (res) {
                        resolve(res);
                    } else {
                        resolve(null);
                    }
                });
            }
        });
    }

    /**
     * Bundle加载dir资源 如果bundle还没有加载那么会先加载bundle 然后再加载bundle中dir的资源
     * @param bundleName bundle名称
     * @param resDir 资源目录
     * @param type 资源类型
     * @param callFunc 回调函数 返回资源列表 有可能返回null
     */
    public loadResDirFromBundle<T extends typeof cc.Asset>(
        bundleName: string,
        resDir: string,
        type: T,
        callFunc?: (resources: Array<T>) => void
    ) {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            this.loadBundle(bundleName, (assetBundle: cc.AssetManager.Bundle) => {
                if (assetBundle) {
                    this.loadBundleResDir(assetBundle, resDir, type, callFunc);
                } else {
                    CC_DEBUG &&
                        CC_DEBUG && cc.error("[ResLoader loadResDirFromBundle]:bundle is not exist!!!!");
                    callFunc && callFunc(null);
                }
            });
        } else {
            this.loadBundleResDir(bundle, resDir, type, callFunc);
        }
    }

    /**
     * 异步从bundle中加载dir资源 如果bundle还没有加载那么会先加载bundle 然后再加载bundle中dir的资源
     * @param bundleName bundle名称
     * @param resDir 资源目录
     * @param type 资源类型
     */
    public asyncLoadResDirFromBundle<T extends typeof cc.Asset>(
        bundleName: string,
        resDir: string,
        type: T
    ): Promise<InstanceType<T>[]> {
        return new Promise((resolve, reject) => {
            let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
            if (!bundle) {
                this.loadBundle(bundleName, (assetBundle: cc.AssetManager.Bundle) => {
                    if (assetBundle) {
                        this.loadBundleResDir(assetBundle, resDir, type, (resources: any[]) => {
                            if (resources) {
                                resolve(resources);
                            } else {
                                resolve(null);
                            }
                        });
                    } else {
                        CC_DEBUG &&
                            CC_DEBUG && cc.error("[ResLoader asyncLoadResDirFromBundle]:bundle is not exist!");
                        resolve(null);
                    }
                });
            } else {
                this.loadBundleResDir(bundle, resDir, type, (resources: any[]) => {
                    if (resources) {
                        resolve(resources);
                    } else {
                        resolve(null);
                    }
                });
            }
        });
    }

    /**
     * 根据配置文件 从bundle中加载所有需要优先加载的资源
     * @param bundleResInfo bnudle资源信息
     * @param progressFunc 资源加载进度回调
     */
    public loadResPackageFromBundle(
        bundleResInfo: IBundleResInfo,
        progressFunc?: (total: number, cur: number) => void,
        failed?: () => void
    ) {
        this.loadResPackage(bundleResInfo, progressFunc, failed);
    }

    /**
     * 加载一个ojb对象中的所有资源
     * @param bundleName bundleName bundle名称
     * @param obj obj对象
     * @param progressFunc 资源加载进度回调
     */
    public async loadResWithObj(
        bundleName: string,
        obj: Object,
        progressFunc?: (total: number, cur: number) => void,
        failed?: () => void
    ) {
        let resDescribeList: IResDescribe[] = this.getResDescribeList(obj);
        let totalLoadNum: number = resDescribeList.length;
        let curLoadNum: number = 0;
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            bundle = await this.asyncLoadBundle(bundleName);
        }
        if (!bundle) {
            failed?.();
            return;
        }

        if (totalLoadNum == 0) {
            progressFunc && progressFunc(0, 0);
            return;
        }

        for (let i = 0; i < totalLoadNum; i++) {
            this.loadBunleRes(
                bundle,
                resDescribeList[i].resPath,
                resDescribeList[i].type,
                (res: cc.Asset) => {
                    if (!res) {
                        failed?.();
                    }
                    res?.addRef();
                    curLoadNum++;
                    progressFunc && progressFunc(totalLoadNum, curLoadNum);
                }
            );
        }
    }

    /**
     * 根据配置文件 从bundle中加载所有非优先加载的资源 主要是用来进入游戏后 预加载一些优先级低的资源 但是有个问题需要注意 预加载不能停止 也就是说退出游戏时最好是等待预加载完再退
     * @param bundleResInfo bnudle资源信息
     * @param progressFunc 加载进度回调
     */
    public preloadResPackageFromBundle(
        bundleResInfo: IBundleResInfo,
        progressFunc?: (total: number, cur: number) => void
    ) {
        this.preloadResPackage(bundleResInfo, progressFunc);
    }

    /**
     * 从bunlde中预加载单个资源
     * @param bundleName bundle名称
     * @param resDesObj 资源描述对象
     * @param callFunc 加载回调
     */
    public preloadResFromBundle(
        bundleName: string,
        resDesObj: IResDescribe,
        callFunc?: () => void
    ) {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            this.loadBundle(bundleName, (assetBundle: cc.AssetManager.Bundle) => {
                if (assetBundle) {
                    this.preloadBunleRes(assetBundle, resDesObj.resPath, resDesObj.type, callFunc);
                } else {
                    CC_DEBUG &&
                        CC_DEBUG && cc.error("[ResLoader preloadResFromBundle]:bundle is not exist!!!!");
                    callFunc && callFunc();
                }
            });
        } else {
            this.preloadBunleRes(bundle, resDesObj.resPath, resDesObj.type, callFunc);
        }
    }

    /**
     * 释放bundle中的所有资源 然后再释放bundle
     * @param bundleResInfo bundle资源信息
     */
    public releaseResPackageFromBundle(bundleResInfo: IBundleResInfo) {
        CC_DEBUG &&
            CC_DEBUG && cc.log(
                "[ResLoader releaseResPackageFromBundle]:Before release",
                bundleResInfo.bundleName,
                cc.assetManager.assets.count
            );
        if (bundleResInfo.priority) {
            this.relaseResWithObj(bundleResInfo.bundleName, bundleResInfo.priority);
        }
        if (bundleResInfo.unpriority) {
            this.relaseResWithObj(bundleResInfo.bundleName, bundleResInfo.unpriority);
        }
        if (bundleResInfo.loadOnDemand) {
            this.relaseResWithObj(bundleResInfo.bundleName, bundleResInfo.loadOnDemand);
        }
        CC_DEBUG &&
            CC_DEBUG && cc.log(
                "[ResLoader releaseResPackageFromBundle]:After release",
                bundleResInfo.bundleName,
                cc.assetManager.assets.count
            );
        if (this.bundles[bundleResInfo.bundleName]) {
            cc.assetManager.removeBundle(this.bundles[bundleResInfo.bundleName]);
            this.bundles[bundleResInfo.bundleName] = null;
        }
    }

    /**
     * 释放bundle中的单个资源
     * @param bundleName bundle名称
     * @param resDesObj 资源描述对象
     */
    public releaseResFormBundle(bundleName: string, resDesObj: IResDescribe) {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (!bundle) {
            return;
        }

        // cc.log("[ResLoader releaseResFormBundle]:release Res " + resDesObj.resPath);
        if (resDesObj.type) {
            bundle.release(resDesObj.resPath, resDesObj.type);
        } else {
            bundle.release(resDesObj.resPath);
        }
    }

    /**
     * 释放Bundle中所有资源,然后释放bundle
     * @param bundleName
     */
    public releaseBundleAll(bundleName: string) {
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleName];
        if (bundle) {
            CC_DEBUG &&
                CC_DEBUG && cc.log(
                    "[ResLoader bundleReleaseAll]:Before release",
                    bundleName,
                    cc.assetManager.assets.count
                );
            bundle.releaseAll();
            cc.assetManager.removeBundle(bundle);
            CC_DEBUG &&
                CC_DEBUG && cc.log(
                    "[ResLoader bundleReleaseAll]:After release",
                    bundleName,
                    cc.assetManager.assets.count
                );
        }
        this.bundles[bundleName] = null;
    }

    /**
     * 异步加载远程图片资源 如果加载成功会返回一个cc.SpriteFrame类型的资源
     * @param imageUrl url路径
     * @param imageSuffix 参数可选 图片的扩展名
     */
    public asyncLoadSpriteFrameFromRemote(
        imageUrl: string,
        imageSuffix: string = ".png"
    ): Promise<cc.SpriteFrame> {
        return new Promise<cc.SpriteFrame>((resolve, reject) => {
            if (!imageUrl || imageUrl == "") {
                CC_DEBUG &&
                    CC_DEBUG && cc.error("[ResLoader asyncLoadSpriteFrameFromRemote]:invalid image url");
                resolve(null);
                return;
            }

            if (
                imageUrl.endsWith(".jpg") ||
                imageUrl.endsWith(".jpeg") ||
                imageUrl.endsWith(".png")
            ) {
                // 图片地址含后缀的下载方法
                cc.assetManager.loadRemote(imageUrl, (error: Error, texture: cc.Texture2D) => {
                    if (error) {
                        CC_DEBUG &&
                            CC_DEBUG && cc.error(
                                "[ResLoader asyncLoadSpriteFrameFromRemote]:load remote image failed. url:",
                                imageUrl
                            );
                        CC_DEBUG && cc.error(error);
                        resolve(null);
                        return;
                    }
                    resolve(new cc.SpriteFrame(texture));
                });
            } else {
                // 图片地址不含后缀的加载方法
                cc.assetManager.loadRemote(
                    imageUrl,
                    { ext: imageSuffix },
                    (error: Error, texture: cc.Texture2D) => {
                        if (error) {
                            CC_DEBUG &&
                                CC_DEBUG && cc.error(
                                    "[ResLoader asyncLoadSpriteFrameFromRemote]:load remote image failed.  url:",
                                    imageUrl
                                );
                            CC_DEBUG && cc.error(error);
                            resolve(null);
                            return;
                        }
                        resolve(new cc.SpriteFrame(texture));
                    }
                );
            }
        });
    }

    /**
     * 加载远程图片资源 如果加载成功会回调一个cc.SpriteFrame类型的资源
     * @param imageUrl url路径
     * @param imageSuffix 参数可选 图片的扩展名
     */
    public loadSpriteFrameFromRemote(
        imageUrl: string,
        callFull: (res: cc.SpriteFrame) => void,
        imageSuffix: string = ".png"
    ) {
        if (!imageUrl || imageUrl == "") {
            CC_DEBUG && cc.error("[ResLoader loadSpriteFrameFromRemote]:invalid image url");
            callFull && callFull(null);
            return;
        }

        if (imageUrl.endsWith(".jpg") || imageUrl.endsWith(".jpeg") || imageUrl.endsWith(".png")) {
            // 图片地址含后缀的下载方法
            cc.assetManager.loadRemote(imageUrl, (error: Error, texture: cc.Texture2D) => {
                if (error) {
                    CC_DEBUG &&
                        CC_DEBUG && cc.error(
                            "[ResLoader loadSpriteFrameFromRemote]:load remote image failed. url:",
                            imageUrl
                        );
                    CC_DEBUG && cc.error(error);
                    callFull && callFull(null);
                    return;
                }
                callFull && callFull(new cc.SpriteFrame(texture));
            });
        } else {
            // 图片地址不含后缀的加载方法
            cc.assetManager.loadRemote(
                imageUrl,
                { ext: imageSuffix },
                (error: Error, texture: cc.Texture2D) => {
                    if (error) {
                        CC_DEBUG &&
                            CC_DEBUG && cc.error(
                                "[ResLoader loadSpriteFrameFromRemote]:load remote image failed.  url:",
                                imageUrl
                            );
                        CC_DEBUG && cc.error(error);
                        callFull && callFull(null);
                        return;
                    }
                    callFull && callFull(new cc.SpriteFrame(texture));
                }
            );
        }
    }

    private async loadResPackage(
        bundleResInfo: IBundleResInfo,
        progressFunc?: (total: number, cur: number) => void,
        failed?: () => void
    ) {
        this.loadResWithObj(bundleResInfo.bundleName, bundleResInfo.priority, progressFunc, failed);
    }

    private async preloadResPackage(
        bundleResInfo: IBundleResInfo,
        progressFunc?: (total: number, cur: number) => void
    ) {
        let resDescribeList: IResDescribe[] = this.getResDescribeList(bundleResInfo.unpriority);
        let totalLoadNum: number = resDescribeList.length;
        let curLoadNum: number = 0;
        let bundle: cc.AssetManager.Bundle = this.bundles[bundleResInfo.bundleName];
        if (!bundle) {
            bundle = await this.asyncLoadBundle(bundleResInfo.bundleName);
        }

        if (totalLoadNum == 0) {
            progressFunc && progressFunc(0, 0);
            return;
        }

        for (let i = 0; i < totalLoadNum; i++) {
            this.preloadBunleRes(
                bundle,
                resDescribeList[i].resPath,
                resDescribeList[i].type,
                () => {
                    curLoadNum++;
                    progressFunc && progressFunc(totalLoadNum, curLoadNum);
                }
            );
        }
    }

    private relaseResWithObj(bundleName: string, obj: Object) {
        let resDescribeList: IResDescribe[] = this.getResDescribeList(obj);
        for (let i = 0, len = resDescribeList.length; i < len; i++) {
            this.releaseResFormBundle(bundleName, resDescribeList[i]);
        }
    }

    private getResDescribeList(obj: Object): IResDescribe[] {
        let resList: IResDescribe[] = [];
        this.traversalObj(obj, resList);
        return resList;
    }

    private traversalObj(obj: Object, resList: IResDescribe[]) {
        for (const key in obj) {
            let resObj = obj[key];
            if (resObj.hasOwnProperty("resPath")) {
                resList.push(resObj);
            } else {
                if (Array.isArray(resObj)) {
                    for (let i = 0, len = resObj.length; i < len; i++) {
                        resList.push(resObj[i]);
                    }
                } else {
                    this.traversalObj(resObj, resList);
                }
            }
        }
    }

    private preloadBunleRes<T extends typeof cc.Asset>(
        bundle: cc.AssetManager.Bundle,
        resPath: string,
        type: T,
        callFunc: () => void
    ) {
        bundle.preload(resPath, type, (error: Error, items: cc.AssetManager.RequestItem[]) => {
            if (error) {
                CC_DEBUG &&
                    CC_DEBUG && cc.error(
                        "[ResLoader preloadBunleRes]:perload Res " + resPath + " error: ",
                        error
                    );
            } else {
                CC_DEBUG &&
                    CC_DEBUG && cc.log("[ResLoader preloadBunleRes]:perload Res " + resPath + " success!");
            }
            callFunc && callFunc();
        });
    }

    private loadBunleScene(
        bundle: cc.AssetManager.Bundle,
        resPath: string,
        callFunc: (sceneAsset: cc.SceneAsset) => void
    ) {
        bundle.loadScene(resPath, (error: Error, sceneAsset: cc.SceneAsset) => {
            if (error) {
                CC_DEBUG &&
                    CC_DEBUG && cc.error("[ResLoader loadBunleScene]:Load Scene Asset Error: " + resPath);
                callFunc && callFunc(null);
            } else {
                CC_DEBUG &&
                    CC_DEBUG && cc.log("[ResLoader loadBunleScene]:Load Scene Asset Success: " + resPath);
                callFunc && callFunc(sceneAsset);
            }
        });
    }

    private loadBunleRes<T extends typeof cc.Asset>(
        bundle: cc.AssetManager.Bundle,
        resPath: string,
        type: T,
        callFunc: (res: any) => void
    ) {
        if (!resPath) {
            CC_DEBUG && cc.warn("[ResLoader loadBunleRes]:resPath is empty!");
            callFunc && callFunc(null);
            return;
        }
        bundle.load(resPath, type, (error, res: any) => {
            if (error) {
                CC_DEBUG &&
                    CC_DEBUG && cc.error("[ResLoader loadBunleRes]:load Res " + resPath + " error: ", error);
                callFunc && callFunc(null);
            } else {
                // cc.log("[ResLoader loadBunleRes]:load Res " + resPath + " success!");
                callFunc && callFunc(res);
            }
        });
    }

    private loadBundleResDir<T extends typeof cc.Asset>(
        bundle: cc.AssetManager.Bundle,
        resPath: string,
        type: T,
        callFunc?: (resources: any[]) => void
    ) {
        bundle.loadDir(resPath, type, (err: Error, resources: any[]) => {
            if (err) {
                CC_DEBUG &&
                    CC_DEBUG && cc.error(
                        "[ResLoader loadBundleResDir]:load bundle dir " + resPath + " error: ",
                        err
                    );
                callFunc && callFunc(null);
            } else {
                CC_DEBUG &&
                    CC_DEBUG && cc.log("[ResLoader loadBundleResDir]:load bundle dir " + resPath + " success!");
                callFunc && callFunc(resources);
            }
        });
    }
}
