export class CocosUtils {
    private static _instance: CocosUtils = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CocosUtils();
        }

        return this._instance;
    }

    private constructor() {}

    public getSceneCanvas() {
        if (cc.director) {
            let directorScene = cc.director.getScene();
            if (cc.isValid(directorScene)) {
                let canvas = directorScene.getChildByName("Canvas");
                if (cc.isValid(canvas)) {
                    return canvas;
                } else {
                    return null;
                }
            } else {
                return null;
            }
        }
        return null;
    }

    public getPlatform() {
        let platform = "";
        if (cc.sys.isNative) {
            if (cc.sys.os == cc.sys.OS_ANDROID) {
                platform = "a";
            } else if (cc.sys.os == cc.sys.OS_IOS) {
                platform = "i";
            }
        } else {
            platform = "hn";
        }
        return platform;
    }
    public swapParentSavePos(node: cc.Node, targetParent: cc.Node) {
        if (node.parent === targetParent) {
            return;
        }
        let curWorld = node.convertToWorldSpaceAR(cc.v3());
        let targetPos = targetParent.convertToNodeSpaceAR(curWorld);
        node.parent = targetParent;
        node.position = targetPos;
    }

    public convertNode2InTargetPos(node: cc.Node, target: cc.Node): cc.Vec3 {
        const curWorld = node.convertToWorldSpaceAR(cc.v3());
        const targetPos = target.parent.convertToNodeSpaceAR(curWorld);
        return targetPos;
    }
}
