import { TouchOneByOne } from "../../utils/class/TouchOneByOne";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LongPressButton extends cc.Component {
    @property
    time: number = 0;

    @property({ type: cc.Component.EventHandler })
    touchEvents: cc.Component.EventHandler[] = [];

    @property({ type: cc.Component.EventHandler })
    touchCancalEvents: cc.Component.EventHandler[] = [];

    protected onLoad(): void {
        this.initTouch();
    }

    initTouch() {
        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchBegin, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTocuhEnd, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTocuhEnd, this);
    }

    private onTouchBegin() {
        this.scheduleOnce(() => {
            this.touchEvents.forEach((v) => {
                let comp = v.target.getComponent(v["_componentName"]);
                comp?.[v.handler].apply(comp, [v.customEventData]);
            });
        }, this.time);
    }

    private onTocuhEnd() {
        this.unscheduleAllCallbacks();
        this.touchCancalEvents.forEach((v) => {
            let comp = v.target.getComponent(v["_componentName"]);
            comp?.[v.handler].apply(comp, [v.customEventData]);
        });
    }
}
