import GameStatusModel from "./GameStatusModel";

class GameCategoryModel {
    title: string;
    sort: number;
    gameList: Array<GameStatusModel>;
    gameNavNo: number;
    gameNavType: number;
    layout: string;

    constructor(categoryInfo) {
        this.title = categoryInfo.title || "";
        this.sort = categoryInfo.sort;
        this.gameNavNo = categoryInfo.gameNavNo;
        this.gameNavType = categoryInfo.gameNavType;
        this.layout = categoryInfo.layout;
        this.gameList = [];
        let list = categoryInfo.list || [];
        for (let i = 0; i < list.length; i++) {
            let gameModel = new GameStatusModel(list[i], i);
            this.gameList.push(gameModel);
        }
    }
}

export default GameCategoryModel;
