import AutoListItem from "./AutoListItem";
const { ccclass, property, menu } = cc._decorator;

@ccclass
@menu("core/ui/AutoListLayout")
export default class AutoListLayout extends cc.Component {
    @property(cc.Prefab)
    protected itemPrefab: cc.Prefab = null;

    @property({ type: cc.Prefab, displayName: "item间的间隔icon" })
    protected spacingPrefab: cc.Prefab = null;

    protected items: cc.Node[] = [];

    private _total: number = 0;
    public get total(): number {
        return this._total;
    }
    public set total(v: number) {
        if (this.total !== v) {
            this._total = v;
            this.updataItems();
        }
    }

    public renderItems: (index: number, comp: AutoListItem) => void = null;

    protected updataItems() {
        this.items.splice(this.total, this.items.length - this.total).forEach((node) => {
            node.destroy();
        });
        for (let i = this.items.length; i < this.total; i++) {
            if (i > 0 && this.spacingPrefab) {
                let spacingItem = cc.instantiate(this.spacingPrefab);
                this.node.addChild(spacingItem);
            }
            let item = cc.instantiate(this.itemPrefab);
            this.node.addChild(item);
            this.items.push(item);
        }

        this.items.forEach((item, index) => {
            if (this.renderItems) {
                this.renderItems(index, item.getComponent(AutoListItem));
            } else {
                item.getComponent(AutoListItem).refreshUi(index);
            }
        });
    }
}
