import { LabelManager } from "./LabelManager";
import { SpriteManager } from "./SpriteManager";

export class NodeManager {
    private static _instance: NodeManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new NodeManager();
        }

        return this._instance;
    }

    private constructor() {}

    public setVisiable(node, visiable = false) {
        if (cc.isValid(node)) {
            if (node instanceof cc.Node) {
                node.active = visiable;
            } else {
                node.node.active = visiable;
            }
        }
    }

    public setOpacity(node, opacity) {
        if (cc.isValid(node)) {
            if (node instanceof cc.Node) {
                node.opacity = opacity;
            } else {
                node.node.opacity = opacity;
            }
        }
    }

    public createNode(name) {
        let node = new cc.Node("node_" + name || "node");
        node.name = name.toString() || "node";
        return node;
    }

    public findChildByName(node, urlPath): cc.Node {
        return cc.find(urlPath, node);
    }

    public loadNodeRes(parent: cc.Node, nodeRes, gameId?): void {
        let self = this;
        if (!cc.isValid(parent) || !nodeRes) {
            return;
        }

        if (nodeRes.sprite) {
            for (let i = 0; i < nodeRes.sprite.length; i++) {
                let nodePath = nodeRes.sprite[i].nodePath;
                let resPath: IResDescribe = nodeRes.sprite[i].resPath;

                let spriteNode = null;
                if (nodePath == "" || nodePath == "/") {
                    spriteNode = parent;
                } else {
                    spriteNode = cc.find(nodePath, parent);
                }

                if (cc.isValid(spriteNode) && resPath) {
                    SpriteManager.getInstance().loadSpriteForNodeBundle(spriteNode, resPath);
                }
            }
        }
        if (nodeRes.font) {
            for (let i = 0; i < nodeRes.font.length; i++) {
                let nodePath = nodeRes.font[i].nodePath;
                let resPath = nodeRes.font[i].resPath;
                let spacingX = nodeRes.font[i].spacingX;

                let fontNode = null;
                if (nodePath == "" || nodePath == "/") {
                    fontNode = parent;
                } else {
                    fontNode = cc.find(nodePath, parent);
                }

                if (cc.isValid(fontNode) && resPath) {
                    LabelManager.getInstance().loadFontBundle(fontNode, resPath, spacingX);
                }
            }
        }
    }

    public setLabelColor(node, color) {
        if (cc.isValid(node)) {
            node.color = new cc.Color().fromHEX(color);
        }
    }
}
