import BaseEnumComp from "../BaseEnumComp";
import EnumController from "../EnumController";
const { ccclass, property, playOnFocus, executeInEditMode, menu } = cc._decorator;

interface IData {
    title: string;
}

interface IOptions {
    data: IData[];

    onSelect?: (index: number, data: IData) => void;
    onDelete?: (index: number, data: IData, list: IData[]) => void;
}

@ccclass
@menu("core/ui/DropDownListEx")
export default class DropDownListEx extends cc.Component {
    protected item: cc.Node = null;

    @property(cc.Node)
    protected content: cc.Node = null;

    protected options: IOptions = null;

    protected onLoad(): void {
        this.content.active = false;
        this.node.on(
            cc.Node.EventType.TOUCH_END,
            () => {
                if (!this.options.data.length) {
                    return;
                }
                this.content.active = !this.content.active;
                this.node.scaleY = this.content.active ? -1 : 1;
            },
            this,
            false
        );
    }

    public initItems(options: IOptions): void {
        let childs = this.node.children.concat();
        this.item = childs.shift();
        childs.forEach((node) => node.destroy());
        this.options = options;
        options.data.forEach((option, index) => {
            this.setItem(index, option);
        });
        this.node.opacity = this.options.data.length <= 0 ? 0 : 255;
    }

    public setItem(index: number, data: IData) {
        let item = this.content.children?.[index] ?? cc.instantiate(this.content.children[0]);
        if (!item.parent) {
            item.parent = this.content;
        }

        item.getChildByName("title").getComponent(cc.Label).string = data.title;

        item.targetOff(this);
        item.on(
            "click",
            () => {
                this.onItemClick(index, data);
            },
            this
        );

        item.getChildByName("del").on(
            "click",
            () => {
                this.options?.onDelete?.(index, data, this.options.data);
                if (this.content.children.length == 1) {
                    this.content.active = false;
                } else {
                    this.options.data.splice(index, 1);
                    item.destroy();
                }
            },
            this
        );
    }

    public onDelectItem(index: number, data: IData) {}

    public onItemClick(index: number, data: IData) {
        this.content.active = false;
        this.options?.onSelect?.(index, data);
    }
}
