/**
 * 此类专门处理启动初始化, 在程序入口 Launcher启动场景onload时调用
 * 1. 初始化一些全局变量(CocosSkin, Cocos20)
 * 2. 初始化一些CommonDepend
 * ...
 */
import { CommonDependManager } from "../../core/depend/CommonDependManager";
import { UIDependManager } from "../../core/depend/UIDependManager";

class LauncherInit {
    public static __init() {
        this.initDepend();
        if (cc.sys.isNative) {
            this.initNativeUtil();
        } else {
            this.initH5Util();
        }
        this.initLocalStorage();
        this.initAppParamManager();
        this.initCommonSocketModel();
        this.initAudio();
        this.initDebugUtil();
        this.initTrackConfig();
        this.startNetworkWatch();

        CC_DEBUG && cc.log("Game Version：" + CommonDepend.ProjectInfo.GAME_VERSION_V);
    }

    private static initDepend() {
        CommonDependManager.getInstance().initCommon(); // 初始化CommonDepend
        UIDependManager.getInstance().initUIDepend(); // 初始化UIDepend
    }

    private static initNativeUtil() {
        // TODO kerry
    }

    private static initH5Util() {
       // CommonDepend.H5Util.init();
    }

    private static initCommonSocketModel() {
        //CommonDepend.CommonSocketModel.init();
    }

    private static initTrackConfig() {
        //CommonDepend.AdjustTrack.initAdjustConf();
    }

    private static initAudio() {
       // CommonDepend.AudioManager.init();
    }

    private static initLocalStorage() {
        CommonDepend.LocalStorageMgr.init();
    }

    private static initAppParamManager() {
        //CommonDepend.AppParamManager.initParam();
    }

    private static initDebugUtil() {}

    private static startNetworkWatch() {}
}

export default LauncherInit;
