import { Buffer } from "buffer";
import crypto = require("crypto");

export class CryptMgr {
    private static _instance: CryptMgr = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CryptMgr();
        }

        return this._instance;
    }

    private static readonly UNIVERSAL = {
        aes_key: "SEWkGIc2RiqcASstuR4kuw==",
        sign_key: "ZTY5NDMxZDNkMDY5NGU0Mjg2ODVmZQ==",
        key: "vn",
    };

    public decodeBase64(base64) {
        return Buffer.from(base64, "base64");
    }

    public encodeHex(buffers) {
        const result = [];
        for (let i of buffers) {
            result.push(i.toString(16).padStart(2, "0"));
        }
        return result.join("");
    }

    /**
     * 加密
     * Http 和 WebSocket 都用到了
     */
    public aes_encrypt(plain, aesKey: string = this.getHttpAesKey()) {
        let aes_key = this.decodeBase64(aesKey);
        let ci = crypto.createCipheriv("aes-128-ecb", aes_key, "");
        let crypted = ci.update(Buffer.from(plain));
        return Buffer.concat([Buffer.from(crypted), Buffer.from(ci.final())]);
    }

    /**
     * 解密
     * Http 和 WebSocket 都用到了
     */
    public aes_decrypt(encrypted) {
        let aes_key = this.decodeBase64(this.getHttpAesKey());
        let deci = crypto.createDecipheriv("aes-128-ecb", aes_key, "");
        let decrypted = deci.update(Buffer.from(encrypted));
        return Buffer.concat([Buffer.from(decrypted), Buffer.from(deci.final())]);
    }

    /**
     * 签名
     * Http需要校验签名，WebSocket不校验签名
     */
    public md5_digest(plain: Uint8Array, ts: number) {
        let sign_key = this.decodeBase64(this.getHttpSignKey());
        const input = Buffer.concat([Buffer.from(plain), Buffer.from(ts.toString()), sign_key]);
        const md5 = crypto.createHash("md5");
        const val = md5.update(input).digest("hex");
        return val;
    }

    /**
     * 计算md5
     * @return md5 string
     */
    public md5(str: string) {
        const md5 = crypto.createHash("md5");
        return md5.update(str).digest("hex");
    }

    private getHttpAesKey() {
        return CryptMgr.UNIVERSAL.aes_key;
    }

    private getHttpSignKey() {
        return CryptMgr.UNIVERSAL.sign_key;
    }

    public getHttpKey() {
        return CryptMgr.UNIVERSAL.key;
    }
}

export default CryptMgr;
