import { TouchOneByOne } from "../../utils/class/TouchOneByOne";
const { ccclass, property, executeInEditMode, menu } = cc._decorator;

@ccclass
@menu("core/ui/ButtonOverride")
export default class ButtonOverride extends cc.Component {
    protected start(): void {
        let index = this.node.getSiblingIndex();

        new TouchOneByOne(this.node)
            .onOver(() => {
                this.node.setSiblingIndex(index);
                let layout = this.node.parent.getComponent(cc.Layout);
                if (layout) {
                    layout.enabled = true;
                }
            })
            .onBegin(() => {
                let layout = this.node.parent.getComponent(cc.Layout);
                if (layout) {
                    layout.enabled = false;
                }
                this.node.setSiblingIndex(this.node.parent.childrenCount - 1);
            })
            .enableTouch();
    }
}
