export class EditBoxManager {

    private static _instance: EditBoxManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new EditBoxManager();
        }

        return this._instance;
    }

    private constructor() {}

    public setPlaceHolderLabelString(editBoxNode, labelTxt) {
        if (cc.isValid(editBoxNode)) {
            if (editBoxNode instanceof cc.EditBox) {
            } else if (editBoxNode instanceof cc.Node) {
                editBoxNode = editBoxNode.getComponent(cc.EditBox);
            }
            if (cc.isValid(editBoxNode)) {
                editBoxNode.placeholder = labelTxt;
            }
        }
    }

    public setLabelString(editBoxNode, labelTxt) {
        if (cc.isValid(editBoxNode)) {
            if (editBoxNode instanceof cc.EditBox) {
            } else if (editBoxNode instanceof cc.Node) {
                editBoxNode = editBoxNode.getComponent(cc.EditBox);
            }
            if (cc.isValid(editBoxNode)) {
                editBoxNode.string = labelTxt;
            }
        }
    }

    public addOutlineWithNode(targetNode) {
        
    }
}