import Flavor from "../Flavor";
import { SkinType } from "../SkinType";
import { BundleDependWorldCup } from "./skin/world_cup/BundleDependWorldCup";
import { BundleNames } from "../BundleNames";
import { BundleDependFinal } from "./skin/final/BundleDependFinal";
export namespace BundleDepend {
    //配置各个皮肤间的依赖
    let depend: BdDepend.TBundleDepend = {
        [SkinType.FINAL]: BundleDependFinal,
        [SkinType.WORLD_CUP]: BundleDependWorldCup,
        [SkinType.LIGHT]: BundleDependWorldCup,
    };

    export function getDepend(bundle: BundleNames) {
        return depend[Flavor.Skin.curMainSkin]?.[bundle] ?? depend[SkinType.FINAL]?.[bundle] ?? [];
    }
}
