import { ApiErrorCode } from "../conf/ApiErrorCode";
import { PlatformUtil } from "../platform/PlatformUtil";
import HttpRequestUtil from "./HttpRequestUtil";

const REQUEST_TYPE = {
    POST: "POST",
    GET: "get",
};

class HttpRequestJson {
    private static readonly timeout: number = 5000;
    private static readonly businessTimeout: number = 10000;

    private xhr: XMLHttpRequest;
    private timer: NodeJS.Timeout;

    private host: string;
    private host_backup: string[];
    private api: string;
    private data: any;
    private request_type: string;
    private callFunc: IHttpCallback;

    constructor() { }

    private retryWithHostBackup(type: string) {
        clearTimeout(this.timer);
        let canRetry: boolean = HttpRequestUtil.getInstance().canRetryWithHostBackup(
            this.host_backup
        );
        if (canRetry) {
            if (this.host_backup.length > 0) {
                this.host = this.host_backup.shift();
            }
            this.xhr = null;
            this.request(this.host, this.api, this.data);
        } else {
            this.onError(type);
        }
    }

    private onError(type: string) {
        switch (type) {
            case "ontimeout":
                this.callFunc?.error?.({ code: ApiErrorCode.TIMEOUT });
                break;
            case "onerror":
            case "error":
                this.callFunc?.error?.({ code: ApiErrorCode.HTTP_ERROR });
                break;
            case "offline": {
                this.callFunc?.error?.({ code: ApiErrorCode.OFFLINE });
                break;
            }
        }
    }

    private request(host: string, api: string, data: any) {
        let self = this;
        if (host) {
            let prefixIndex = host.indexOf("https");
            if (prefixIndex < 0) {
                host = "https://" + host;
            }
            if (host.charAt(host.length - 1) == "/") {
                host = host.substring(0, host.length - 1);
            }
        }
        if (api.charAt(0) != "/") {
            api = "/" + api;
        }
        let url = host + api;

        if (this.request_type == REQUEST_TYPE.GET) {
            url += "?" + data;
        }

        self.xhr = new XMLHttpRequest();
        self.xhr.open(this.request_type, url, true);

        self.xhr.onreadystatechange = () => {
            if (self.xhr.readyState == 4) {
                let httpStatus = self.xhr.status;
                let responseText = self.xhr.responseText;
                if (httpStatus == 200) {
                    try {
                        let responseJson = JSON.parse(responseText);
                        let jsonData = responseJson.data;
                        let jsonStatus = responseJson.status;
                        if (jsonData && jsonStatus == 0) {
                            self.callFunc &&
                                self.callFunc.success &&
                                self.callFunc.success(jsonData);
                        } else {
                            self.callFunc &&
                                self.callFunc.error &&
                                self.callFunc.error(responseJson);
                        }
                    } catch (error) {
                        self.callFunc &&
                            self.callFunc.error &&
                            self.callFunc.error({ code: ApiErrorCode.HTTP_ERROR });
                    }
                } else {
                    try {
                        let responseJson = JSON.parse(responseText);
                        self.callFunc && self.callFunc.error && self.callFunc.error(responseJson);
                    } catch (error) {
                        if (self.xhr.status) {
                            self.callFunc &&
                                self.callFunc.error &&
                                self.callFunc.error({ code: ApiErrorCode.HTTP_ERROR });
                        }
                    }
                }
            }
        };

        self.xhr.ontimeout = (event) => {
            CC_DEBUG && cc.log("HttpRequest: ontimeout", url);
            self.retryWithHostBackup("ontimeout");
        };
        self.xhr.onerror = (event) => {
            CC_DEBUG && cc.log("HttpRequest: onerror", url);
            self.retryWithHostBackup("onerror");
        };
        self.timer = setTimeout(() => {
            self.xhr.abort();
            clearTimeout(self.timer);
        }, HttpRequestJson.businessTimeout);

        self.xhr.setRequestHeader("content-type", "application/x-www-form-urlencoded");

        let uid = CommonDepend.UserManager.uid.toString();
        let token = CommonDepend.UserManager.token;

        self.xhr.setRequestHeader("X-AUTH-USER", uid.toString());
        self.xhr.setRequestHeader("X-AUTH-TOKEN", token.toString());

        self.xhr.timeout = HttpRequestJson.timeout;

        if (self.request_type == REQUEST_TYPE.POST) {
            self.xhr.send(data);
        } else {
            self.xhr.send();
        }
    }

    public post(
        host: string,
        host_backup: string[],
        api: string,
        data: any,
        callFunc: IHttpCallback
    ) {
        this.host = host;
        this.host_backup = host_backup;
        this.api = api;
        this.data = data;
        this.request_type = REQUEST_TYPE.POST;
        this.callFunc = callFunc;

        this.request(host, api, data);
    }

    public get(
        host: string,
        host_backup: string[],
        api: string,
        data: any,
        callFunc: IHttpCallback
    ) {
        this.host = host;
        this.host_backup = host_backup;
        this.api = api;
        this.data = data;
        this.request_type = REQUEST_TYPE.GET;
        this.callFunc = callFunc;

        if (!PlatformUtil.getInstance().isOnline()) {
            this.onError("offline");
            return;
        }
        this.request(host, api, data);
    }
}

export { HttpRequestJson };
