import { BundleNames } from "../../../conf/BundleNames";
import { lodash } from "../../../utils/NpmExport";

export const CocosBundleEnum = cc.Enum(
    lodash
        .chain(lodash.toPairs(BundleNames))
        .map((element, index) => {
            return [`<${element[0]}>`, index];
        })
        .fromPairs()
        .value()
);
