export class DateUtils {
    private static _instance: DateUtils = null;

    /**
     * TIME_DIFF: 服务端与客户端之间对时，将差值保存
     * 防伪造逻辑: Http请求通用Header里面，不应该传当前客户端时间戳，应该进行校准后回传给服务端
     * 对时逻辑:
     * 1、目前在所有的websocket连接成功后，通过客户端ping-服务端pong来进行对时。
     * 1、通过http请求的response来进行对时。
     */
    private time_diff: number = 0;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new DateUtils();
        }

        return this._instance;
    }

    private constructor() {}

    public checkTimestampDiff(serverTimestamp: number) {
        if (serverTimestamp) {
            let clientTimestamp = this.getTimestamp();
            this.time_diff = serverTimestamp - clientTimestamp;
        } else {
            this.time_diff = 0;
        }
    }

    public getTimeDiff() {
        return this.time_diff;
    }

    /** 获取当前时间时间戳 */
    public getTimestamp(): number {
        return new Date().getTime();
    }

    /** 获取今天开始的时间戳 今天0点的时间戳*/
    public getTodayStartTimestamp(): number {
        return new Date(new Date().toLocaleDateString()).getTime();
    }

    /** 获取n天前开始的时间戳  n天前0点的时间戳*/
    public getPreviousStartTimestamp(n: number): number {
        return new Date(new Date().toLocaleDateString()).getTime() - n * 3600 * 24 * 1000;
    }

    /**
     * 获取当前时间秒
     */
    public getNowSec(): number {
        return Math.round(this.getTimestamp() / 1000);
    }

    /**
     * 获取一天的秒数
     * @returns
     */
    public getDaySec(): number {
        return 60 * 60 * 24;
    }

    /**
     * 转化日期
     * @param ms 毫秒
     */
    public transformDate(ms: number, format: string = "D-M-Y h:m:s"): string {
        ms += CommonDepend.ProjectInfo.TIME_ZONE_DIFF;
        let rFormat = format;
        let date = ms ? new Date(ms) : new Date();
        let dateObject = {
            Y: date.getUTCFullYear(),
            M: this.fillZero(date.getUTCMonth() + 1, 2),
            D: this.fillZero(date.getUTCDate(), 2),
            h: this.fillZero(date.getUTCHours(), 2),
            m: this.fillZero(date.getUTCMinutes(), 2),
            s: this.fillZero(date.getUTCSeconds(), 2),
            ms: this.fillZero(date.getUTCMilliseconds(), 3),
        };
        for (const key in dateObject) {
            if (rFormat.indexOf(key) != -1) {
                rFormat = rFormat.replace(key, dateObject[key]);
            }
        }
        return rFormat;
    }

     /**
     * 转化日期 不计算时区
     * @param ms 毫秒
     */
    public transformDateNoZone(ms: number, format: string = "D-M-Y h:m:s") {
        let rFormat = format;
        let date = ms ? new Date(ms) : new Date();
        let dateObject = {
            Y: date.getFullYear(),
            M: this.fillZero(date.getMonth() + 1, 2),
            D: this.fillZero(date.getDate(), 2),
            h: this.fillZero(date.getHours(), 2),
            m: this.fillZero(date.getMinutes(), 2),
            s: this.fillZero(date.getSeconds(), 2),
            ms: this.fillZero(date.getMilliseconds(), 3),
        };
        for (const key in dateObject) {
            if (rFormat.indexOf(key) != -1) {
                rFormat = rFormat.replace(key, dateObject[key]);
            }
        }
        return rFormat;
    }

    /**
     * 左补零
     * @param value 值
     * @param zeroCount 零位
     */
    public fillZero(value: number, zeroCount: number): string {
        let fillCount = zeroCount - String(value).length;
        let fillStr = "";
        for (let i = 0; i < fillCount; i++) {
            fillStr += "0";
        }
        return fillStr + String(value);
    }

    /**
     * 把时间戳格式化为时分秒 xx:XX:XX
     * @param timeStamp 秒
     * @returns
     */
    public formatTime(timeStamp: number): string {
        let h = Math.floor(timeStamp / 60 / 60);
        let m = Math.floor((timeStamp - h * 3600) / 60);
        let s = timeStamp - h * 3600 - m * 60;
        return (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m) + ":" + (s < 10 ? "0" + s : s);
    }

    /**
     * 格式化最新时间
     */
    public formatLastTime(time) {
        let h = Math.floor(time / 60 / 60);
        let m = Math.floor((time - h * 3600) / 60);
        return (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m);
    }

    /**
     * 格式化最新时间2
     */
    public formatLastTime2(time) {
        let h = Math.floor(time / 60 / 60);
        let m = Math.floor((time - h * 3600) / 60);
        let s = time - h * 3600 - m * 60;
        return (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m) + ":" + (s < 10 ? "0" + s : s);
    }

    public timestampFormatWithYearDetailed(timeStamp: number) {
        let date = new Date();
        date.setTime(timeStamp);
        let y = date.getFullYear();
        let m: any = date.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        let d: any = date.getDate();
        d = d < 10 ? "0" + d : d;
        let h: any = date.getHours();
        h = h < 10 ? "0" + h : h;
        let minute: any = date.getMinutes();
        let second: any = date.getSeconds();
        minute = minute < 10 ? "0" + minute : minute;
        second = second < 10 ? "0" + second : second;
        return y + "-" + m + "-" + d + " " + h + ":" + minute + ":" + second;
    }
}
