import Flavor from "../conf/Flavor";
import ProjectConf from "../conf/ProjectConf";
import { ProjectInfo } from "../conf/ProjectInfo";
import { SkinType } from "../conf/SkinType";
import { Instance } from "../utils/decorators/InstanceClassDecorator";
import { Tools } from "../utils/Tools";
import LanguageCenter from "./LanguageCenter";
import LanguageObserver from "./LanguageObserver";

export default abstract class BaseBundleLanguage<T extends LG.ILanguageConfig> {
    protected abstract language: LG.ILanguageConfigs;
    protected skin: LG.ISkinLangeConfigs = {};

    constructor() { }

    applyPorxy(object: LG.ILanguageConfig, begin: boolean = false) {
        if (!object) {
            return;
        }
        const proxy = new Proxy(object, {
            get: (target: T, p: string, receiver: any): any => {
                let result: any = target[p];

                if (begin) {
                    function getLang(lang: LG.ILanguageConfigs) {
                        return (
                            (lang?.[Instance(LanguageObserver).get()] ??
                                lang?.[Flavor.Language.curLanguage])?.[p] ?? undefined
                        );
                    }
                    result =
                        getLang(this.skin?.[Flavor.Skin.curMainSkin]) || getLang(this.language);
                }

                if (typeof result === "string") {
                    result = Tools.formatStringInArgs(result, {
                        CUR: ProjectInfo.getInstance().CurrentIntConf.currency,
                        _skin_: Flavor.Skin.curMainSkin.split("-")[1],
                    });
                    return result;
                } else if (typeof result === "number") {
                    return result;
                } else if (typeof result === "object") {
                    return this.applyPorxy(result);
                } else {
                    return result;
                }
            },
        });
        return proxy;
    }

    public get(): LG.TLanguageConfig<T> {
        return this.applyPorxy(this.language[Flavor.Language.curLanguage], true) as any;
    }
}
