import { ApiErrorCode } from "../conf/ApiErrorCode";
import Lighter from "../services/Lighthouse/Lighter";
import { LighthouseProto } from "../services/Lighthouse/proto/LighthouseProto";
import HttpRequestUtil from "./HttpRequestUtil";
import { EVENT_CODE } from "../services/Lighthouse/proto/EventCode";
import Listener from "../utils/Listener";
import { CoreEvents } from "../event/CoreEvents";
import { PlatformUtil } from "../platform/PlatformUtil";

class HttpRequest {
    private static readonly timeout: number = 5000;
    private static readonly businessTimeout: number = 10000;

    private xhr: XMLHttpRequest;
    private timer: NodeJS.Timeout;

    private host: string;
    private host_backup: string[];
    private api: string;
    private protoInfo: IProtoInfo;
    private callFunc: IHttpCallback;

    constructor() { }

    private retryWithHostBackup(type: string) {
        clearTimeout(this.timer);
        let canRetry: boolean = HttpRequestUtil.getInstance().canRetryWithHostBackup(
            this.host_backup
        );
        if (canRetry) {
            this.protoInfo.req_plain = HttpRequestUtil.getInstance().reBuildReq(
                this.protoInfo.req_header,
                this.protoInfo.req_prototype,
                this.protoInfo.req_plain
            );
            if (this.host_backup.length > 0) {
                this.host && Listener.send(CoreEvents.SWITCH_HOST, this.host);
                this.host = this.host_backup.shift();
            }
            this.xhr = null;
            this.post_protobuf(this.host, this.api, this.protoInfo.req_plain);
        } else {
            this.onError(type);
        }
    }

    private onError(type: "ontimeout" | "onerror" | "offline" | string) {
        switch (type) {
            case "ontimeout":
                this.callFunc?.error?.({ code: ApiErrorCode.TIMEOUT });
                break;
            case "onerror":
                this.callFunc?.error?.({ code: ApiErrorCode.HTTP_ERROR });
                break;
            case "offline": {
                this.callFunc?.error?.({ code: ApiErrorCode.OFFLINE });
                break;
            }
        }
    }

    private post_protobuf(host: string, api: string, req_plain: any) {
        let self = this;
        let url = HttpRequestUtil.getInstance().buildFullUrl(host, api, req_plain);

        let data = HttpRequestUtil.getInstance().encrypt_data(req_plain);

        if (self.xhr == null) {
            self.xhr = new XMLHttpRequest();
        }
        self.xhr.open(
            "POST",
            url +
            `&eid=${Lighter.getInstance().trackStart(
                url,
                LighthouseProto.Events.EventType.HTTP_REQ
            )}`,
            true
        );

        self.xhr.onreadystatechange = () => {
            if (self.xhr.readyState == 4) {
                if (self.xhr.status == 200) {
                    Lighter.getInstance().trackEnd(
                        url,
                        EVENT_CODE.RequestSuccess,
                        url.split("?")[0]
                    );
                    let server_req_plain = null;
                    try {
                        server_req_plain = HttpRequestUtil.getInstance().decrypt_response(
                            self.xhr.response
                        );
                    } catch (err) {
                        CC_DEBUG && cc.log("HttpRequest: aes_decrypt fail: " + self.api);
                        return;
                    }
                    let resp = null;
                    try {
                        resp = self.protoInfo.resp_prototype.deserializeBinary(server_req_plain);
                    } catch (err) {
                        CC_DEBUG && cc.log("HttpRequest: deserializeBinary fail: ", self.api);
                        return;
                    }

                    let respHeader = resp.header;
                    let code = respHeader.code || 0;
                    // to do 对时
                    // let timestamp = respHeader.getTimestamp();
                    // CommonDepend.DateUtils.checkTimestampDiff(timestamp);

                    // http response log
                    // let maxLoglen = 600;
                    let respJson = resp.toObject();
                    // let responseLog = JSON.stringify(respJson);
                    // let responseLogLength = responseLog.length;
                    // let responseLogUnit = Math.ceil(responseLogLength / maxLoglen);
                    if (CC_DEBUG) {
                        CC_DEBUG && cc.log("HttpRequest: ", self.api);
                        CC_DEBUG && cc.log(respJson);
                    }
                    // for (let i = 0; i < responseLogUnit; i++) {
                    //     cc.log(responseLog.slice(i * maxLoglen, i * maxLoglen + maxLoglen));
                    // }

                    if (self.callFunc) {
                        if (code == 0) {
                            self.callFunc.success && self.callFunc.success(resp.data);
                        } else {
                            self.callFunc.error && self.callFunc.error(respHeader);
                        }
                    }
                } else {
                    Lighter.getInstance().trackEnd(
                        url,
                        EVENT_CODE.RequestFailure,
                        url.split("?")[0]
                    );
                    if (self.xhr.status) {
                        this.callFunc.error &&
                            this.callFunc.error({ code: ApiErrorCode.HTTP_ERROR });
                        CC_DEBUG && cc.log("HttpRequest: httpStatus=", self.xhr.status, self.api);
                    }
                }
            }
        };
        self.xhr.ontimeout = (event) => {
            Lighter.getInstance().trackEnd(url, EVENT_CODE.RequestTimeout, url.split("?")[0]);

            CC_DEBUG && cc.log("HttpRequest: ontimeout", url);
            self.retryWithHostBackup("ontimeout");
        };
        self.xhr.onerror = (event) => {
            Lighter.getInstance().trackEnd(url, EVENT_CODE.RequestFailure, url.split("?")[0]);
            CC_DEBUG && cc.log("HttpRequest: onerror", url);
            self.retryWithHostBackup("onerror");
        };
        self.timer = setTimeout(() => {
            self.xhr.abort();
            clearTimeout(self.timer);
        }, HttpRequest.businessTimeout);

        self.xhr.setRequestHeader("content-type", "application/x-protobuf");
        self.xhr.responseType = "arraybuffer";
        self.xhr.timeout = HttpRequest.timeout;

        self.xhr.send(data);
    }

    public post(
        host: string,
        host_backup: string[],
        api: string,
        protoInfo: IProtoInfo,
        callFunc: IHttpCallback
    ) {
        this.host = host;
        this.host_backup = host_backup;
        this.api = api;
        this.protoInfo = protoInfo;
        this.callFunc = callFunc;

        if (!PlatformUtil.getInstance().isOnline()) {
            this.onError("offline");
            return;
        }

        this.post_protobuf(host, api, protoInfo.req_plain);
    }
}

export { HttpRequest };
