import Lighter from "../services/Lighthouse/Lighter";
import { EVENT_CODE } from "../services/Lighthouse/proto/EventCode";
import { LighthouseProto } from "../services/Lighthouse/proto/LighthouseProto";
import { lodash } from "../utils/NpmExport";

class CommonSocket {
    private _gameID: number;
    private _wsHost: string;
    private _msgCallback: Function;
    private _initCallBack: Function;
    private _onCloseCallback: Function;
    private _webSocket: WebSocket;

    private _connectTimeoutID: NodeJS.Timeout;

    constructor(
        gameID: number,
        host: string,
        msgCallback: Function,
        initCallBack: Function,
        onCloseCallback?: Function
    ) {
        this._gameID = gameID;
        if (!lodash.isEmpty(host)) {
            this._wsHost = host;
        } else {
            return;
        }

        this._msgCallback = msgCallback;
        this._initCallBack = initCallBack;
        this._onCloseCallback = onCloseCallback;

        this._initWs();
    }

    public getHost() {
        return this._wsHost;
    }
    private _initWs() {
        Lighter.getInstance().trackStart(this._wsHost, LighthouseProto.Events.EventType.WS_REQ);
        CC_DEBUG && cc.log("CommonSocket: h5 new =" + this._wsHost);
        if (cc.sys.isNative) {
            var info = cc.resources.getInfoWithPath("cacert");
            if (info) {
                // 根据uuid 返回资源路径
                let pemUrl = cc.assetManager.utils.getUrlWithUuid(info.uuid, {
                    isNative: true,
                    nativeExt: ".pem",
                });
                // let pemUrl = "assets/resources/native/5c/5c8ebf99-2e64-4ef3-83d4-f1381868530c.pem";
                CC_DEBUG && cc.log("CommonSocket: pemUrl", pemUrl);
                this._webSocket = new WebSocket(this._wsHost, null, pemUrl);
            }
        } else {
            this._webSocket = new WebSocket(this._wsHost);
        }

        this._webSocket.onopen = (event) => {
            CC_DEBUG &&
                cc.log(
                    "CommonSocket: onopen = " + this._wsHost + ", event = " + JSON.stringify(event)
                );
            Lighter.getInstance().trackEnd(this._wsHost, EVENT_CODE.RequestSuccess, this._wsHost);
            this._onOpen();
        };
        this._webSocket.onerror = (event) => {
            CC_DEBUG &&
                cc.log(
                    "CommonSocket: onerror = " + this._wsHost + ", event = " + JSON.stringify(event)
                );
            Lighter.getInstance().trackEnd(this._wsHost, EVENT_CODE.RequestFailure, this._wsHost);
            this._onError();
        };
        this._webSocket.onclose = (event) => {
            CC_DEBUG &&
                cc.log(
                    "CommonSocket: onclose = " + this._wsHost + ", event = " + JSON.stringify(event)
                );
            this._onClose();
        };
        this._webSocket.onmessage = (event) => {
            CC_DEBUG &&
                cc.log(
                    "CommonSocket: onmessage = " +
                        this._wsHost +
                        ", event = " +
                        JSON.stringify(event)
                );
            this._onMessage(event);
        };
    }

    _onOpen() {
        // to do
        //CommonPackageDepend.CommonPrefabMgr.removeCommonLoading();

        clearTimeout(this._connectTimeoutID);
        this._webSocket.binaryType = "arraybuffer";
        this._initCallBack();
    }

    _onError() {}

    _onClose() {
        if (this._onCloseCallback) {
            this._onCloseCallback();
        }
    }

    _onMessage(event) {
        this._msgCallback(event.data);
    }

    public forceInitWs() {
        this._webSocket.close();
        this._initWs();
    }

    public send(msg: string | ArrayBuffer | SharedArrayBuffer | Blob | ArrayBufferView) {
        if (this._webSocket && this._webSocket.readyState === WebSocket.OPEN) {
            this._webSocket.send(msg);
        }
    }

    public isConnected() {
        let self = this;
        return self._webSocket != null && self._webSocket.readyState === WebSocket.OPEN;
    }

    public close() {
        if (this._webSocket) {
            this._webSocket.close();
        }
    }

    public state() {
        return this._webSocket?.readyState;
    }
}

export default CommonSocket;
