import LanguageCenter from "../../../language/LanguageCenter";
import { Instance } from "../../../utils/decorators/InstanceClassDecorator";
import { lodash } from "../../../utils/NpmExport";
import { Tools } from "../../../utils/Tools";
import { CocosBundleEnum } from "../define/CocosBundleEnum";
import { CocosUiUtil } from "../utils/CocosUiUtil";

const { ccclass, property, executeInEditMode, menu, inspector } = cc._decorator;

@ccclass
@menu("core/label/MultLangRichTextBase")
export default class MultLangRichTextBase extends cc.RichText {
    @property
    protected _bundleName: string = "";

    @property({ type: CocosBundleEnum })
    protected get bundleName(): number {
        return CocosBundleEnum?.[this._bundleName] ?? 0;
    }

    protected set bundleName(value: number) {
        this._bundleName = CocosBundleEnum?.[value] ?? 0;
    }

    @property
    protected _mainLangKey: string = "";

    @property
    protected _subLangKey: string = "";

    private _object: { [index: string]: string | number } = {};
    public set object(value: { [index: string]: string | number }) {
        this._object = value;
        this.updateString();
    }
    public get object(): { [index: string]: string | number } {
        return this._object;
    }

    public getLangString() {
        return (
            Instance(LanguageCenter).getLanguage4Bundle(this._bundleName)?.[this._mainLangKey]?.[
                this._subLangKey
            ] ?? ""
        );
    }

    protected updateString() {
        let str = this.getLangString();
        if (str) {
            if (!lodash.isEmpty(this.object)) {
                str = Tools.formatString(str, this.object);
            }
            this.string = str;
        }
    }

    protected onLoad(): void {
        super.onLoad?.();
        this.updateString();
    }
}
