import { CocosBundleEnum } from "../define/CocosBundleEnum";
import { CocosUiUtil } from "../utils/CocosUiUtil";
import MultLangLabelBase from "./MultLangLabelBase";

const { ccclass, property, executeInEditMode, menu, inspector } = cc._decorator;

@ccclass
@menu("core/label/MultLangLabelCustom")
@inspector("packages://int-inspector/build/components/mult-lang-label.js")
export default class MultLangLabelCustom extends MultLangLabelBase {
    @property
    protected get mainLangKey() {
        return this._mainLangKey;
    }
    protected set mainLangKey(value: string) {
        this._mainLangKey = value;
        this.updateString();
    }

    @property
    public get subLangKey() {
        return this._subLangKey;
    }
    public set subLangKey(value: string) {
        this._subLangKey = value;
        this.updateString();
    }
}
