import { ButtonManager } from "../ui/base/ButtonManager";
import { EditBoxManager } from "../ui/base/EditBoxManager";
import { LabelManager } from "../ui/base/LabelManager";
import { NodeManager } from "../ui/base/NodeManager";
import { PrefabManager } from "../ui/base/PrefabManager";
import { RichTextManager } from "../ui/base/RichTextManager";
import { SpineManager } from "../ui/base/SpineManager";
import { SpriteManager } from "../ui/base/SpriteManager";

export class UIDependManager {
    private static _instance: UIDependManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new UIDependManager();
        }

        return this._instance;
    }

    private constructor() {}

    public initUIDepend() {
        let uiDepend: UIDependInterface = {
            NodeManager: NodeManager.getInstance(),
            SpriteManager: SpriteManager.getInstance(),
            SpineManager: SpineManager.getInstance(),
            PrefabManager: PrefabManager.getInstance(),
            LabelManager: LabelManager.getInstance(),
            RichTextManager: RichTextManager.getInstance(),
            ButtonManager: ButtonManager.getInstance(),
            EditBoxManager: EditBoxManager.getInstance(),
        };

        UIDepend = uiDepend;
    }
}
