import { BundleMgr } from "../../bd/BundleMgr";

export class ButtonManager {
    private static _instance: ButtonManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new ButtonManager();
        }

        return this._instance;
    }

    private constructor() {}

    public setLabelString(btnNode, labelTxt) {
        if (cc.isValid(btnNode)) {
            if (btnNode instanceof cc.Button) {
            } else if (btnNode instanceof cc.Node) {
                btnNode = btnNode.getComponent(cc.Button);
            }
            if (cc.isValid(btnNode)) {
                btnNode.string = labelTxt;
            }
        }
    }

    public setButtonSpriteFrame(node: cc.Node, resDescs: cc.SpriteFrame[]) {
        if (cc.isValid(node)) {
            let sprite: cc.Button = node.getComponent(cc.Toggle) || node.getComponent(cc.Button);
            if (cc.isValid(sprite)) {
                for (let i = 0; i < resDescs.length; i++) {
                    switch (i) {
                        case 0:
                            sprite.normalSprite = resDescs[i];
                            break;
                        case 1:
                            sprite.pressedSprite = resDescs[i];
                            break;
                        case 2:
                            sprite.hoverSprite = resDescs[i];
                            break;
                        case 3:
                            sprite.disabledSprite = resDescs[i];
                            break;
                        default:
                            break;
                    }
                }
            }
        }
    }

    public loadButtonSpriteForNodeBundle(node, resDescs: IResDescribe[], callback?) {
        let self = this;
        if (cc.isValid(node)) {
            let sprite: cc.Button = node.getComponent(cc.Toggle) || node.getComponent(cc.Button);
            self.loadSpriteBundle(sprite, resDescs, callback);
        }
    }

    public loadSpriteBundle(sprite, resDescs: IResDescribe[], callback?, errorCallback?) {
        if (!cc.isValid(sprite)) {
            return;
        }

        for (let i = 0; i < resDescs.length; i++) {
            BundleMgr.getInstance().loadResFromBundle(resDescs[i], (res: any) => {
                if (res) {
                    if (cc.isValid(sprite)) {
                        switch (i) {
                            case 0:
                                sprite.normalSprite = res;
                                break;
                            case 1:
                                sprite.pressedSprite = res;
                                break;
                            case 2:
                                sprite.hoverSprite = res;
                                break;
                            case 3:
                                sprite.disabledSprite = res;
                                break;
                            default:
                                break;
                        }
                        if (typeof callback === "function") {
                            callback();
                        }
                    }
                } else {
                    if (typeof errorCallback === "function") {
                        errorCallback();
                    }
                }
            });
        }
    }
}
