class GameStatusModel {
    tag: number;
    tag_hot: number;
    tag_event: number;
    tag_new: number;
    icon: string;
    layout: number;
    gameId: number;
    tag_earth: number;
    kind: number;

    constructor(gameInfo, index) {
        this.tag = gameInfo.tag || 0;
        this.tag_hot = gameInfo.tagHot || 0;
        this.tag_event = gameInfo.tagEvent || 0;
        this.tag_new = gameInfo.tagNew || 0;
        this.tag_earth = gameInfo.tagEarth || 0;
        this.icon = gameInfo.icon || "";
        this.kind = gameInfo.kind || "";
        this.layout = index || 0;
        this.gameId = gameInfo.gameId;
    }
}

export default GameStatusModel;
