import { BundleDepend } from "../conf/bd_depend/BundleDepend";
import { BundleNames } from "../conf/BundleNames";
import { lodash } from "../utils/NpmExport";
import BundleSkinCenter from "./bd_skin/BundleSkinCenter";
import { BundleMgr } from "./BundleMgr";
import { ResLoader } from "./ResLoader";

type TBundleName = string;
type TBundle = BundleNames;

type TProgressCallfunc = (current: number, total: number) => void;

export default class BundleCenter {
    private static _instance = null;

    public static getInstance(): BundleCenter {
        if (!this._instance) {
            this._instance = new BundleCenter();
            window["BundleCenter"] = this._instance;
        }
        return this._instance;
    }

    private bundleMap: Map<TBundleName, number> = new Map();

    private currentBundle: TBundle = null;

    private constructor() {}

    public launchScene(bundle: TBundle, sceneName?: string) {
        if (CC_DEBUG) {
            CC_DEBUG && cc.log("[BundleCenter] launchScene ", bundle, sceneName || "");
        }

        let onProgress = null;
        let onSuccess = null;
        let onLaunchedSceneComplete = null;
        let onFailed = null;

        const promise = this.load(
            bundle,
            (current: number, total: number) => {
                onProgress?.(current, total);
            },
            () => {
                CC_DEBUG && cc.error("[bundleCenter] bundle: scene failed");
            }
        )
            .then(() => {
                const bundleInfo = BundleSkinCenter.getInstance().getBundleInfo(bundle);
                sceneName = sceneName || bundleInfo.launchScene;
                if (sceneName) {
                    cc.director.loadScene(sceneName, (error) => {
                        if (error) {
                            this.deleteReference(bundle);
                            onFailed?.();
                        } else {
                            this.deleteReference(this.currentBundle);
                            this.currentBundle = bundle;
                            onLaunchedSceneComplete?.();
                            onSuccess?.();
                        }
                    });
                } else {
                    const launchPrefabPath = bundleInfo.launchPrefabPath;
                    if (CC_DEBUG) {
                        if (lodash.isEmpty(launchPrefabPath)) {
                            CC_DEBUG &&
                                CC_DEBUG &&
                                cc.error(
                                    `[bundleCenter] bundle:${bundle} Skin 未配置sceneName||launchPrefabPath 参数`
                                );
                            return;
                        }
                    }
                    let scene = CommonDepend.CocosUtils.getSceneCanvas();
                    BundleMgr.getInstance().loadResFromBundle(
                        { bundleName: bundle, resPath: launchPrefabPath, type: cc.Prefab },
                        (prefab: cc.Prefab) => {
                            if (prefab) {
                                scene.addChild(cc.instantiate(prefab));
                                this.currentBundle = bundle;
                            }
                        }
                    );
                    onSuccess?.();
                }
            })
            .catch(() => {
                onFailed?.();
            });

        const result = {
            onFailed: (callfunc: Function) => {
                onFailed = callfunc;
                return result;
            },
            onProgress: (callfunc: TProgressCallfunc) => {
                onProgress = callfunc;
                return result;
            },
            onSuccess: (callfunc: Function) => {
                onSuccess = callfunc;
                return result;
            },
            onLaunchedSceneComplete: (callfunc: Function) => {
                onLaunchedSceneComplete = callfunc;
                return result;
            },
            promise,
        };

        return result;
    }

    public closeMiniGame(node: cc.Node) {
        node.destroy();
        this.deleteReference(this.currentBundle);
        this.currentBundle = BundleNames.HALL;
    }

    public load(bundle: TBundle, onProgress?: TProgressCallfunc, failed?: () => void) {
        return this.loadBundleWithDependencies(bundle, onProgress, failed);
    }

    private async loadBundleWithDependencies(
        bundle: TBundle,
        onProgress?: TProgressCallfunc,
        failed?: () => void
    ) {
        const bundles = this.findDependencies(bundle);
        await this.loadBundles(bundles, onProgress, failed);
    }

    private async loadBundles(
        bundles: TBundle[],
        onProgress?: TProgressCallfunc,
        failed?: () => void
    ) {
        const ITEM = 100;
        const TOTAl = ITEM * bundles.length;

        for (let i = 0, len = bundles.length; i < len; i++) {
            const bundle: TBundle = bundles[i];
            await this.loadBundleWithRes(
                bundle,
                (current: number, total: number) => {
                    const progress = (current / total) * ITEM + ITEM * i;
                    onProgress?.(progress, TOTAl);
                },
                failed
            );
            this.addReference(bundle);
        }
    }

    private addReference(bundle: TBundle) {
        const count = this.bundleMap.get(bundle) ?? 0;
        this.bundleMap.set(bundle, count + 1);
    }

    private deleteReference(bundle?: TBundle) {
        if (!bundle) {
            return;
        }
        const bundles = this.findDependencies(bundle);

        for (let bundle of bundles) {
            const count = (this.bundleMap.get(bundle) ?? 0) - 1;

            this.bundleMap.set(bundle, count);

            if (count == 0) {
                const bundleInfo = BundleSkinCenter.getInstance().getBundleInfo(bundle);
                BundleMgr.getInstance().releaseResPackageFromBundle(bundleInfo);
            } else if (count < 0) {
                this.bundleMap.set(bundle, 0);
            }
        }
    }

    private findDependencies(bundle: TBundle) {
        return this.__findDependencies(bundle);
    }

    private __findDependencies(bundle: TBundle, bundles: TBundle[] = []) {
        let dependencies: TBundle[] = BundleDepend.getDepend(bundle);
        for (let bundle of dependencies ?? []) {
            this.__findDependencies(bundle, bundles);
        }
        if (!bundles.includes(bundle)) {
            bundles.push(bundle);
        }
        return bundles;
    }

    private async loadBundleWithRes(
        bundle: TBundle,
        onProgress?: TProgressCallfunc,
        failed?: () => void
    ): Promise<boolean> {
        let bd = await ResLoader.getInstance().asyncLoadBundle(bundle);
        if (!bd) {
            throw new Error("[BundleCenter] loading bundle failed!" + bundle);
        }
        return new Promise((resolve) => {
            const bundleInfo = BundleSkinCenter.getInstance().getBundleInfo(bundle);
            BundleMgr.getInstance().loadResPackageFromBundle(
                bundleInfo,
                (total: number, cur: number) => {
                    onProgress?.(cur, total);
                    if (total <= cur) {
                        resolve(true);
                    }
                },
                () => {
                    failed?.();
                }
            );
        });
    }
}

BundleCenter.getInstance();
