import { ResLoader } from "./ResLoader";

export class BundleMgr {
    private static _instance: BundleMgr = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new BundleMgr();
        }

        return this._instance;
    }

    private constructor() {}

    public loadResPackageFromBundle(
        bundleResInfo: IBundleResInfo,
        progressFunc?: (total: number, cur: number) => void,
        failed?: () => void
    ) {
        ResLoader.getInstance().loadResPackageFromBundle(bundleResInfo, progressFunc, failed);
    }

    public loadResWithObj(
        bundleName: string,
        obj: Object,
        progressFunc?: (total: number, cur: number) => void
    ) {
        ResLoader.getInstance().loadResWithObj(bundleName, obj, progressFunc);
    }

    public releaseResPackageFromBundle(bundleResInfo: IBundleResInfo) {
        ResLoader.getInstance().releaseResPackageFromBundle(bundleResInfo);
    }

    public loadSceneFromBundle(
        bundleName: string,
        sceneName: string,
        callFunc: (sceneAsset: cc.SceneAsset) => void
    ) {
        ResLoader.getInstance().loadSceneFromBundle(bundleName, sceneName, callFunc);
    }

    public loadResFromBundle<T>(resDesObj: IResDescribe, callFunc?: (res: T) => void): Promise<T> {
        return new Promise((resolve) => {
            ResLoader.getInstance().loadResFromBundle(resDesObj.bundleName, resDesObj, (res) => {
                callFunc?.(res);
                resolve(res);
            });
        });
    }

    public asyncLoadSpriteFrameFromRemote(
        imageUrl: string,
        imageSuffix: string = ".png"
    ): Promise<cc.SpriteFrame> {
        return ResLoader.getInstance().asyncLoadSpriteFrameFromRemote(imageUrl, imageSuffix);
    }

    public loadSpriteFrameFromRemote(
        imageUrl: string,
        callFull: (res: cc.SpriteFrame) => void,
        imageSuffix: string = ".png"
    ) {
        ResLoader.getInstance().loadSpriteFrameFromRemote(imageUrl, callFull, imageSuffix);
    }

    public getResFromBundle(resDesObj: IResDescribe) {
        return ResLoader.getInstance().getResFromBundle(resDesObj.bundleName, resDesObj);
    }
}
