export class PoolMgr {
    private static _instance: PoolMgr = null;
    private pools = {};

    public static getInstance() {
        if (!this._instance) {
            this._instance = new PoolMgr();
        }

        return this._instance;
    }

    private constructor() {}

    /**
     * 根据预制体从对象池中获取对应节点
     */
    public getNode(prefab: cc.Prefab): cc.Node {
        let name = prefab.data.name;
        let node: cc.Node = null;
        if (this.pools.hasOwnProperty(name)) {
            let pool = this.pools[name]; // 已有对应的对象池
            if (pool.size() > 0) {
                node = pool.get();
            } else {
                node = cc.instantiate(prefab);
            }
        } else {
            let pool = new cc.NodePool(); // 没有对应对象池, 创建他！
            this.pools[name] = pool;

            node = cc.instantiate(prefab);
        }

        return node;
    }

    /**
     * 将对应节点放回对象池中
     */
    public putNode(node: cc.Node) {
        if (!cc.isValid(node)) {
            return;
        }
        let name = node.name;
        let pool = null;
        if (this.pools.hasOwnProperty(name)) {
            pool = this.pools[name]; // 已有对应的对象池
        } else {
            pool = new cc.NodePool(); // 没有对应对象池, 创建他！
            this.pools[name] = pool;
        }

        pool.put(node);
    }

    /**
     * 根据名称, 清除对应对象池
     */
    public clearPool(name: string) {
        if (this.pools.hasOwnProperty(name)) {
            let pool: cc.NodePool = this.pools[name];
            pool && pool.clear();
        }
    }

    /**
     * 根据预制体, 清除对应对象池
     */
     public clearPoolWithPrefab(prefab: cc.Prefab) {
        let name = prefab.data.name;
        this.clearPool(name);
    }

    /**
     * 清除所有对象池
     */
    public clearAllPool() {
        for (const key in this.pools) {
            if (Object.prototype.hasOwnProperty.call(this.pools, key)) {
                let pool: cc.NodePool = this.pools[key];
                pool && pool.clear();
            }
        }
    }
}
