import { Tools } from "../Tools";

@cc._decorator.ccclass
export default class Time2Value {
    private _maxStep: number = 0;
    private _step: number = 0;
    private _current: number = 0;
    private _target: number = 0;

    private _positive: boolean = false;
    private callfunc: (value: number) => void;

    public success: () => void;

    constructor(private _total: number = 0, private _time: number = 0) {
        cc.director.getScheduler().enableForTarget(this);
        cc.director.getScheduler().scheduleUpdate(this, 0, false);
    }

    public destory() {
        cc.director.getScheduler().unscheduleUpdate(this);
    }

    public positive() {
        this._positive = true;
        return this;
    }

    getValue(): number {
        return this._current;
    }

    setValue(value: number, callfunc: (value: number) => void) {
        if (this._positive) {
            if (value < this._current) {
                this._current = value;
            }
        }
        this._target = value;
        this._step = this._target - this._current;
        this.callfunc = callfunc;
    }

    private update(dt: number) {
        if (this._current == this._total || !this._step) {
            return;
        }

        let value = (this._total / this._time) * dt;
        this._current = Tools.limit(this._current + value, 0, this._target);
        this.callfunc?.(this._current);
        if (this._total == this._current) {
            this.success?.();
        }
    }
}
