import Flavor from "../conf/Flavor";
import { ProjectInfo } from "../conf/ProjectInfo";
import { Instance, setInstance } from "../utils/decorators/InstanceClassDecorator";
import { lodash } from "../utils/NpmExport";
import { Tools } from "../utils/Tools";
import LanguageObserver from "./LanguageObserver";

@setInstance
export default class LanguageCenter {
    private map: Map<string, LG.ILanguageConfigs> = new Map();

    public applyLanguage(bundleName: string, language: LG.ILanguageConfigs) {
        let bundle = this.toBundleName(bundleName);
        if (bundle) {
            this.map.set(bundle, lodash.merge({}, this.map.get(bundle), language));
        }
    }

    public getLanguage4Bundle(bundle: string) {
        bundle = this.toBundleName(/<?(?<bundle>[^>]*)>?/.exec(bundle)?.groups?.bundle);
        return this.applyPorxy(this.map.get(bundle)?.[Instance(LanguageObserver).get()] ?? {});
    }

    applyPorxy(object: LG.ILanguageConfig, begin: boolean = false) {
        if (!object) {
            return;
        }
        const proxy = new Proxy(object, {
            get: (target: LG.ILanguageConfig, p: string, receiver: any): any => {
                let result: any = target[p];
                if (typeof result === "string") {
                    result = Tools.formatStringInArgs(result, {
                        CUR: ProjectInfo.getInstance().CurrentIntConf.currency,
                        _skin_: Flavor.Skin.curMainSkin.split("-")[1],
                    });
                    return result;
                } else if (typeof result === "number") {
                    return result;
                } else if (typeof result === "object") {
                    return this.applyPorxy(result);
                } else {
                    return result;
                }
            },
        });
        return proxy;
    }

    public toBundleName(bundle: string) {
        return lodash.toLower(bundle.split("_").join(""));
    }
}
