import { BundleMgr } from "../../bd/BundleMgr";
export class RichTextManager {
  private static _instance: RichTextManager = null;

  public static getInstance() {
    if (!this._instance) {
      this._instance = new RichTextManager();
    }

    return this._instance;
  }

  private constructor() { }

  public setString(richText, richStr, resDesc?: IResDescribe, callback?) {
    if (cc.isValid(richText)) {
      if (richText instanceof cc.RichText) {
      } else if (richText instanceof cc.Node) {
        richText = richText.getComponent(cc.RichText);
      }
      if (cc.isValid(richText)) {
        richText.string = richStr;
        BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
          richText.font = res;
          if (typeof callback === "function") {
            callback();
          }
        });
      }
    }
  }

  public setImageAtlas(richText, resDesc?: IResDescribe, callback?) {
    if (cc.isValid(richText)) {
      if (richText instanceof cc.RichText) {
      } else if (richText instanceof cc.Node) {
        richText = richText.getComponent(cc.RichText);
      }
      richText.imageAtlas = BundleMgr.getInstance().getResFromBundle(resDesc);
      if (typeof callback === "function") {
        callback();
      }
    }
  }
}
