import { IntConf } from "./int/IntConf";
import ProjectConf from "./ProjectConf";

export class ProjectInfo {
    private static _instance: ProjectInfo = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new ProjectInfo();
        }
        return this._instance;
    }

    private constructor() {}

    public GAME_VERSION = IntConf[ProjectConf.version_type].version;

    public GAME_VERSION_V = "V" + IntConf[ProjectConf.version_type].version;

    public GAME_BUILD_NUMBER = "1";

    public DEFAULT_HOST: string[] = IntConf[ProjectConf.version_type].domin;

    public TIME_ZONE_DIFF: number = IntConf[ProjectConf.version_type].time_zone_diff;

    public LIGHTER_HOST: string = IntConf[ProjectConf.version_type].lighter;

    public CurrentIntConf = IntConf[ProjectConf.version_type];
}
