import { H5Util } from "../../platform/H5Util";
import { PlatformType } from "../../platform/PlatformType";
import { webm, mp4 } from "./media";

// Detect iOS browsers < version 10
const oldIOS = () =>
    typeof navigator !== "undefined" &&
    parseFloat(
        (
            "" +
            (/CPU.*OS ([0-9_]{3,4})[0-9_]{0,1}|(CPU like).*AppleWebKit.*Mobile/i.exec(
                navigator.userAgent
            ) || [0, ""])[1]
        )
            .replace("undefined", "3_2")
            .replace("_", ".")
            .replace("_", "")
    ) < 10 &&
    !window.MSStream;

const nativeWakeLock = () => "wakeLock" in navigator;

class NoSleep {
    private enabled: boolean = false;
    private _wakeLock: any;
    public noSleepTimer;
    public noSleepVideo;

    constructor() {
        this.enabled = false;

        if (nativeWakeLock()) {
            this._wakeLock = null;
            const handleVisibilityChange = () => {
                if (this._wakeLock !== null && document.visibilityState === "visible") {
                    this.enable();
                }
            };
            document.addEventListener("visibilitychange", handleVisibilityChange);
            document.addEventListener("fullscreenchange", handleVisibilityChange);
        } else if (oldIOS()) {
            this.noSleepTimer = null;
        } else {
            // Set up no sleep video element
            this.noSleepVideo = document.createElement("video");

            this.noSleepVideo.setAttribute("title", document.title);
            this.noSleepVideo.setAttribute("playsinline", "");

            this._addSourceToVideo(this.noSleepVideo, "webm", webm);
            this._addSourceToVideo(this.noSleepVideo, "mp4", mp4);

            this.noSleepVideo.addEventListener("loadedmetadata", () => {
                if (this.noSleepVideo.duration <= 1) {
                    // webm source
                    this.noSleepVideo.setAttribute("loop", "");
                } else {
                    // mp4 source
                    this.noSleepVideo.addEventListener("timeupdate", () => {
                        if (this.noSleepVideo.currentTime > 0.5) {
                            this.noSleepVideo.currentTime = Math.random();
                            this.noSleepVideo.play();
                        }
                    });
                }
            });
        }
    }

    _addSourceToVideo(element, type, dataURI) {
        var source = document.createElement("source");
        source.src = dataURI;
        source.type = `video/${type}`;
        element.appendChild(source);
    }

    get isEnabled() {
        return this.enabled;
    }

    enable() {
        if (nativeWakeLock()) {
            // cc.log(`@86`);
            return navigator.wakeLock
                .request("screen")
                .then((wakeLock) => {
                    // cc.log(`@90`);
                    this._wakeLock = wakeLock;
                    this.enabled = true;
                    CC_DEBUG && cc.log("Wake Lock active.");
                    this._wakeLock.addEventListener("release", () => {
                        // ToDo: Potentially emit an event for the page to observe since
                        // Wake Lock releases happen when page visibility changes.
                        // (https://web.dev/wakelock/#wake-lock-lifecycle)
                        CC_DEBUG && cc.log("Wake Lock released.");
                    });
                })
                .catch((err) => {
                    this.enabled = false;
                    CC_DEBUG && cc.error(`${err.name}, ${err.message}`);
                    throw err;
                });
        } else if (oldIOS()) {
            this.disable();
            CC_DEBUG && cc.warn(`
              NoSleep enabled for older iOS devices. This can interrupt
              active or long-running network requests from completing successfully.
              See https://github.com/richtr/NoSleep.js/issues/15 for more details.
            `);
            this.noSleepTimer = window.setInterval(() => {
                if (!document.hidden) {
                    window.location.href = window.location.href.split("#")[0];
                    window.setTimeout(window.stop, 0);
                }
            }, 15000);
            this.enabled = true;
            return Promise.resolve();
        } else {
            CC_DEBUG && cc.log(`@128`);
            let playPromise = this.noSleepVideo.play();
            return playPromise
                .then((res) => {
                    CC_DEBUG && cc.log(`@133`);
                    this.enabled = true;
                    return res;
                })
                .catch((err) => {
                    this.enabled = false;
                    throw err;
                });
        }
    }

    disable() {
        if (nativeWakeLock()) {
            if (this._wakeLock) {
                this._wakeLock.release();
            }
            this._wakeLock = null;
        } else if (oldIOS()) {
            if (this.noSleepTimer) {
                CC_DEBUG && cc.warn(`
                NoSleep now disabled for older iOS devices.
              `);
                window.clearInterval(this.noSleepTimer);
                this.noSleepTimer = null;
            }
        } else {
            this.noSleepVideo.pause();
        }
        this.enabled = false;
    }
}
class PreventScreenSleep {
    private _platform = null;

    constructor() {
        if (CC_EDITOR) return;
        this._startPreventScreenSleep();
    }

    private _startPreventScreenSleep() {
        this._platform = H5Util.getInstance().platform;
        if (this._platform !== PlatformType.H5) return;

        let isMobile = cc.sys.isMobile;
        if (!isMobile) return;

        let noSleep = new NoSleep();

        const addMask = () => {
            let maskDiv = document.createElement("div");
            maskDiv.setAttribute(
                "style",
                "position:absolute; top:0; left:0; right:0; bottom:0; z-index:99999;"
            );
            maskDiv.setAttribute("class", "sp_mask");
            document.body.insertBefore(maskDiv, null);

            let maskNode = document.querySelectorAll(".sp_mask")[0];
            maskNode?.addEventListener(
                "click",
                function enableNoSleep(e) {
                    CC_DEBUG && cc.log(`@194 ===================> `);
                    maskNode.setAttribute("style", "display: none");
                    noSleep.enable();
                },
                false
            );
        };

        setTimeout(() => {
            addMask();
        }, 1000);
    }
}

export default new PreventScreenSleep();
