import { AppParamManager } from "../app/AppParamManager";
import { AppParamModel } from "../app/AppParamModel";
import { AudioManager } from "../audio/AudioManager";
import { GameDatabase } from "../conf/GameDatabase";
import { ProjectInfo } from "../conf/ProjectInfo";
import { HttpRequest } from "../http/HttpRequest";
import { H5Util } from "../platform/H5Util";
import { NativeUtil } from "../platform/NativeUtil";
import { PlatformUtil } from "../platform/PlatformUtil";
import { LocalStorageMgr } from "../storage/LocalStorageMgr";
import { AdjustTrack } from "../track/AdjustTrack";
import { UserManager } from "../user/UserManager";
import { CocosUtils } from "../utils/CocosUtils";
import { DateUtils } from "../utils/DateUtils";
import { MoneyUtil } from "../utils/MoneyUtil";
import { CommonSocketModel } from "../ws/CommonSocketModel";

export class CommonDependManager {
    private static _instance: CommonDependManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CommonDependManager();
        }

        return this._instance;
    }

    private constructor() {}

    public initCommon() {
        let commonDepend: CommonDependInterface = {
            HttpRequest: HttpRequest,
            LocalStorageMgr: LocalStorageMgr.getInstance(),
            H5Util: H5Util.getInstance(),
            UserManager: UserManager.getInstance(),
            ProjectInfo: ProjectInfo.getInstance(),
            AppParamModel: AppParamModel.getInstance(),
            AppParamManager: AppParamManager.getInstance(),
            PlatformUtil: PlatformUtil.getInstance(),
            GameDatabase: GameDatabase.getInstance(),
            CommonSocketModel: CommonSocketModel.getInstance(),
            AudioManager: AudioManager.getInstance(),
            MoneyUtil: MoneyUtil.getInstance(),
            DateUtils: DateUtils.getInstance(),
            CocosUtils: CocosUtils.getInstance(),
            AdjustTrack: AdjustTrack.getInstance(),
            NativeUtil: NativeUtil.getInstance(),
        };
        CommonDepend = commonDepend;
    }
}
