import { ProjectInfo } from "../conf/ProjectInfo";
import { LocalStorageMgr } from "../storage/LocalStorageMgr";

export class AppParamManager {
    private static _instance: AppParamManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new AppParamManager();
        }
        return this._instance;
    }

    private constructor() {}

    /**
     * AppParam init
     */
    public initParam() {
        let self = this;
        self.initPackageName();
        self.initMerCode();
        self.initBrandCode();
        self.initChn();
        self.initAppVersionCode();
        self.initDeviceID();
    }

    private initPackageName() {
        let packageName = CommonDepend.PlatformUtil.getPackageName();
        CommonDepend.AppParamModel.setPackageName(packageName);
        CommonDepend.LocalStorageMgr.savePackageName(packageName);
    }

    private initMerCode() {
        let merCode = "";
        CommonDepend.AppParamModel.setMerCode(merCode);
        CommonDepend.LocalStorageMgr.saveMerCode(merCode);
    }

    private initBrandCode() {
        let brandCode = CommonDepend.PlatformUtil.getBrand();
        CommonDepend.AppParamModel.setBrandCode(brandCode);
        CommonDepend.LocalStorageMgr.saveBrandCode(brandCode);
    }

    private initChn() {
        let chn = CommonDepend.PlatformUtil.getChannel();
        CommonDepend.AppParamModel.setChn(chn);
        CommonDepend.LocalStorageMgr.saveChn(chn);
    }

    private initAppVersionCode() {
        let vc = ProjectInfo.getInstance().GAME_BUILD_NUMBER;
        CommonDepend.AppParamModel.setAppVersionCode(vc);
        CommonDepend.LocalStorageMgr.saveAppVersionCode(vc);
    }

    private initDeviceID() {
        let deviceId = CommonDepend.PlatformUtil.getDeviceID();
        //后门，生成新的device_id
        let switch_device = LocalStorageMgr.getInstance().readSwitchDevice();
        if (switch_device == "1") {
            let oldDeviceID = CommonDepend.LocalStorageMgr.readDeviceID();
            if (!oldDeviceID) {
                deviceId = deviceId + "_" + Math.random();
            } else {
                deviceId = oldDeviceID;
            }
        }
        CommonDepend.AppParamModel.setDeviceID(deviceId);
        CommonDepend.LocalStorageMgr.saveDeviceID(deviceId);
    }

    /**
     * 包名
     */
    public getPackageName() {
        let packageName = CommonDepend.LocalStorageMgr.readPackageName();
        if (!packageName) {
            packageName = CommonDepend.PlatformUtil.getPackageName();
            CommonDepend.LocalStorageMgr.savePackageName(packageName);
        }
        return packageName;
    }

    /**
     * 商户号
     */
    public getMerCode() {
        return "";
    }

    /**
     * 品牌
     */
    public getBrandCode() {
        let brandCode = CommonDepend.LocalStorageMgr.readBrandCode();
        if (!brandCode) {
            brandCode = CommonDepend.PlatformUtil.getBrand();
            CommonDepend.LocalStorageMgr.saveBrandCode(brandCode);
        }
        return brandCode;
    }

    /**
     * 渠道
     */
    public getChn() {
        let chn = CommonDepend.LocalStorageMgr.readChn();
        if (!chn) {
            chn = CommonDepend.PlatformUtil.getChannel();
            CommonDepend.LocalStorageMgr.saveChn(chn);
        }
        return chn;
    }

    /**
     * 应用程序名
     */
    public getAppName() {
        return CommonDepend.PlatformUtil.getAppName();
    }

    /**
     * appVersionCode
     */
    public getAppVersionCode() {
        let vc = CommonDepend.LocalStorageMgr.readAppVersionCode();
        if (!vc) {
            vc = ProjectInfo.getInstance().GAME_BUILD_NUMBER;
            CommonDepend.LocalStorageMgr.saveAppVersionCode(vc);
        }
        return vc;
    }

    /**
     * deviceId
     */
    public getDeviceID() {
        let deviceId = CommonDepend.LocalStorageMgr.readDeviceID();
        if (!deviceId || deviceId.length < 10) {
            deviceId = CommonDepend.PlatformUtil.getDeviceID();
            CommonDepend.LocalStorageMgr.saveDeviceID(deviceId);
        }
        return deviceId;
    }
}
