export class CommonSocketModel {
    private static _instance: CommonSocketModel = null;

    private userId = 0;
    private token: string = "";
    private version: number = 0;
    private merCode: string = "";
    private brandCode: string = "";

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CommonSocketModel();
        }

        return this._instance;
    }

    private constructor() {}

    public init() {
        this.saveUserId();
        this.saveToken();
        this.saveVersion();
        this.saveMerCode();
        this.saveBrandCode();
    }

    public getUserId() {
        return this.userId;
    }

    public getToken() {
        return this.token;
    }

    public getVersion() {
        return this.version;
    }

    public getMerCode() {
        return this.merCode;
    }

    public getBrandCode() {
        return this.brandCode;
    }

    private saveUserId() {
        let userId = parseInt(CommonDepend.LocalStorageMgr.readUserID());
        this.userId = isNaN(userId) ? 0 : userId;
    }

    private saveToken() {
        this.token = CommonDepend.LocalStorageMgr.readUserToken();
    }

    private saveVersion() {
        this.version = parseInt(CommonDepend.ProjectInfo.GAME_VERSION.replace(/\./g, ""));
    }

    private saveMerCode() {
        this.merCode = CommonDepend.AppParamManager.getMerCode();
    }

    private saveBrandCode() {
        this.brandCode = CommonDepend.AppParamManager.getBrandCode();
    }

    public getCurrentTimestamp() {
        return new Date().getTime();
    }
}
