import { LocalStorageMgr } from "../storage/LocalStorageMgr";
import { lodash } from "../utils/NpmExport";

export class NativeUtil {
    private static _instance: NativeUtil = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new NativeUtil();
        }

        return this._instance;
    }

    private constructor() {}

    private nativeClassName: string;

    public nativeAndroidClassName() {
        let className = "";
        if (!lodash.isEmpty(LocalStorageMgr.getInstance().getNativeClassName())) {
            className = LocalStorageMgr.getInstance().getNativeClassName();
        } else {
            className = "code/keep/CocosBridge";
        }
        return className;
    }

    public openUrlByBrowser(url: string | Promise<string>) {}

    public openUrlByWebView(url: string) {}

    public openApp(packageName: string, fallbackUrl: string) {}

    public copyText(text: string) {}

    public getCopiedText(): string {
        return "";
    }

    public saveImage(url: string) {}

    public showNativeToast(toast: string) {}

    public initAdjustID(adjustID: string) {}

    public trackAdjustEvent(eventToken: string, data: object) {}

    public getDeviceID(): string {
        return "";
    }

    public getDeviceInfoForLighthouse(): string {
        let deviceInfo = "";
        if (cc.sys.os == cc.sys.OS_ANDROID) {
            let className = this.nativeAndroidClassName();
            let methodName = "getDeviceInfoForLighthouse";
            let methodSignature = "()Ljava/lang/String;";
            deviceInfo = jsb.reflection.callStaticMethod(className, methodName, methodSignature);
        }
        return deviceInfo;
    }

    public getSystemVersionCode(): number {
        return 1; // Android返回int，iOS返回float
    }

    public getClientVersionCode(): number {
        return 1;
    }

    public getPackageName(): string {
        return "";
    }

    public getAppName(): string {
        return "";
    }

    public getChannel(): string {
        return "";
    }

    public getBrand(): string {
        return "";
    }

    public saveGameUrl(gameUrl: string) {}

    public saveAccountInfo(accountInfo: string) {}

    public getAccountInfo(): string {
        //TODO
        return null;
    }

    public getAdjustDeviceID(): string {
        return "";
    }

    public getGoogleADID(): string {
        return "";
    }

    public getIDFA(): string {
        return "";
    }

    public getReferID(): string {
        return "";
    }

    public getAgentID(): string {
        return "";
    }

    public setCocosData(key: string, value: string) {}

    public getCocosData(key: string): string {
        return "";
    }

    public savePromotionMaterial(materialUrl: string) {}

    public synthesizePromotionImage(qrCodeUrl: string, size: number, x: number, y: number) {}

    public shareUrl(url: string) {
        if (cc.sys.os == cc.sys.OS_ANDROID) {
            let className = this.nativeAndroidClassName();
            let methodName = "shareUrl";
            let methodSignature = "(Ljava/lang/String;)V";
            jsb.reflection.callStaticMethod(className, methodName, methodSignature,url);
        }
    }
}
