import { BundleNames } from "../../conf/BundleNames";

export default class BundleSkinCenter {
    private static _instance: BundleSkinCenter = null;
    public static getInstance(): BundleSkinCenter {
        if (!this._instance) {
            this._instance = new BundleSkinCenter();
        }
        return this._instance;
    }

    private constructor() {}

    private skinMap: Map<BundleNames, BdSkin.IBundleSkin> = new Map();

    public applySkin(bundleName: BundleNames, skin: BdSkin.IBundleSkin): void {
        this.skinMap.set(bundleName, skin);
    }

    public removeSkin(bundleName: BundleNames) {
        return this.skinMap.delete(bundleName);
    }

    public getSkin(bundleNames: BundleNames): BdSkin.IBundleSkin {
        return this.skinMap.get(bundleNames);
    }

    public getBundleInfo(bundleNames: BundleNames): IBundleResInfo {
        return this.getSkin(bundleNames).getCurSkin();
    }
}
