import CryptMgr from "../../crypt/CryptMgr";
import { PlatformUtil } from "../../platform/PlatformUtil";
import { LighthouseProto } from "./proto/LighthouseProto";
import { UserManager } from "../../user/UserManager";
import ProjectConf from "../../conf/ProjectConf";
import { ProjectInfo } from "../../conf/ProjectInfo";
import { H5Util } from "../../platform/H5Util";
import { PlatformType } from "../../platform/PlatformType";

