const INTERVAL_CHECK_EVENT_QUEUE = 60000; // check event queue every 60s
const MAX_LENGTH_EVENT_QUEUE = 32;
const TIMEOUT = 10000;

/**
 * lighthouse tracker
 * 1, put undone event into map
 * 2, transfer event to queue if done
 * 3, check queue periodically and fire once over max length
 */
export default class Lighter {
    private static sInstance: Lighter = null;

    private _undoneEventMap: Map<string, ProtoObject<LighthouseProto.Events.Event>> = new Map();
    private _eventQueue: Array<object> = [];

    /* static */
    public static getInstance() {
        if (this.sInstance == null) {
            this.sInstance = new Lighter();
        }
        return this.sInstance;
    }

    private constructor() {
        setInterval(this._checkEventQueue.bind(this), INTERVAL_CHECK_EVENT_QUEUE);
    }

    /* public */
    /**
     * @param key use full url for http request (POST only) / use ws host for websocket
     * @param type
     */
    public trackStart(key: string, type: LighthouseProto.Events.EventType) {
        if (this._undoneEventMap.has(key)) {
            //console.log("[Lighthouse] track start, missing key = " + key);
            return;
        }

        let eid = CryptMgr.getInstance().md5(key);
        //console.log("[Lighthouse] track start, key = " + key + ", eid = " + eid);
        this._undoneEventMap.set(key, {
            eid: eid,
            ts: new Date().getTime(),
            type
        });

        return eid;
    }

    /**
     *
     * @param key use full url for http request (POST only) / use ws host for websocket
     * @param code see EventCode.ts
     * @param desc url without query part
     */
    public trackEnd(key: string, code: number, desc: string) {
        if (!this._undoneEventMap.has(key)) {
            //console.log("[Lighthouse] track end, missing key = " + key);
            return;
        }

        //console.log("[Lighthouse] track end, key = " + key + ", code = " + code);
        let event: ProtoObject<LighthouseProto.Events.Event> = this._undoneEventMap.get(key);
        event.code = code;
        let finishTs = new Date().getTime();
        event.duration = finishTs - event.ts;
        event.desc = desc;
        this._undoneEventMap.delete(key);
        this._eventQueue.push(event);
    }

    public fire() {
        //console.log("[Lighthouse] fire event queue = " + JSON.stringify(this._eventQueue));
        let events = LighthouseProto.Events.fromObject({
            ua: PlatformUtil.getInstance().getUserAgent(),
            user_id: UserManager.getInstance().user_id,
            events: this._eventQueue
        });

        this._send(events.serializeBinary());

        // clear event queue
        this._eventQueue = [];
    }

    /* private */
    private _checkEventQueue() {
        //console.log("[Lighthouse] event queue length = " + this._eventQueue.length);
        if (this._eventQueue.length >= MAX_LENGTH_EVENT_QUEUE) {
            this.fire();
        }
    }

    private _send(data) {
        let httpRequest = new XMLHttpRequest();
        let lighterHost = PlatformUtil.getInstance().getLighterHost();
        if (!lighterHost) {
            this.log("[Lighthouse] host is empty, will not upload data");
            return;
        }
        //配置只有scheme://host，因此要自行加上path
        let lighterAddr = lighterHost + "/v1/";

        this.log("[Lighthouse] upload to address: " + lighterAddr);
        httpRequest.open("POST", lighterAddr, true);
        // httpRequest.setRequestHeader('content-type', 'application/x-protobuf');
        httpRequest.timeout = TIMEOUT;
        httpRequest.onload = (event) => {
            this.log(
                "[Lighthouse] events uploaded successfully, status = " + httpRequest.status
            );
            if (httpRequest.status == 200) {
                CommonDepend.LocalStorageMgr.saveEverUploadLighthouse("true");
            }
        };
        httpRequest.ontimeout = (event) => {
            this.log("[Lighthouse] events uploading timeout, status = " + httpRequest.status);
        };
        httpRequest.onerror = (event) => {
            this.log("[Lighthouse] events uploading error, status = " + httpRequest.status);
        };
        httpRequest.send(data);
        this.log("[Lighthouse] fired");
    }


    public trackDeviceInfoForLighthouse() {
        let everUpload = CommonDepend.LocalStorageMgr.getEverUploadLighthouse();
        if (everUpload && everUpload == "true") {
            this.log(
                "[Lighthouse] we have uploaded track before, will not upload again "
            );
            return;
        }
        let deviceInfo = PlatformUtil.getInstance().getDeviceInfoForLighthouse();
        this.log("[Lighthouse] track DeviceInfoForLighthouse: " + deviceInfo);
        let event = new LighthouseProto.Events.Event();
        event.code = 0;
        let finishTs = new Date().getTime();
        event.ts = finishTs;
        event.type = LighthouseProto.Events.EventType.DEVICE_INFO;
        event.desc = deviceInfo;

        let events = LighthouseProto.Events.fromObject({
            ua: PlatformUtil.getInstance().getUserAgent(),
            user_id: UserManager.getInstance().user_id,
            events: [event]
        });

        this._send(events.serializeBinary());
    }

    private log(msg) {
        PlatformUtil.getInstance().nativeLog(null, msg);
    }
}
