import Time2Value from "../../core/utils/class/Time2Value";
import Listener from "../../core/utils/Listener";
import { lodash } from "../../core/utils/NpmExport";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LauncherLoading extends cc.Component {
    private readonly MAX_LAUNCH_PROGRESS = 20;
    private readonly MAX_COMMON_PROGRESS = 80;
    private readonly MAX_WALLET_PROGRESS = 0;

    _launch: number = 0;
    _common: number = 0;
    _wallet: number = 0;
    private time2Value: Time2Value = null;

    public onSuccess: Promise<void> = null;

    onLoad() {
        this.time2Value = new Time2Value(1, 1);
        this.onSuccess = new Promise((resolve) => {
            this.time2Value.success = () => {
                resolve();
            };
        });
    }

    public updateProgress() {
        let progress = lodash.sum([this._launch, this._common, this._wallet]) / 100;
        this.time2Value.setValue(progress, (value) => {
            if (cc.isValid(this)) this.getComponent(cc.ProgressBar).progress = value;
        });
        return Math.min(progress, 1);
    }

    public launchProgress(value: number) {
        this._launch = value * this.MAX_LAUNCH_PROGRESS;
        return this.updateProgress();
    }

    public commomProgress(value: number) {
        this._common = value * this.MAX_COMMON_PROGRESS;
        return this.updateProgress();
    }

    public walletProgress(value: number) {
        this._wallet = value * this.MAX_WALLET_PROGRESS;
        return this.updateProgress();
    }
}
