import { BundleNames } from "../../conf/BundleNames";
import Flavor from "../../conf/Flavor";
import { SkinType } from "../../conf/SkinType";
import BundleSkinCenter from "./BundleSkinCenter";
export default abstract class BaseBundleSkin<T extends IBundleResInfo>
    implements BdSkin.IBundleSkin
{
    protected abstract skin: BdSkin.TSkinInfo;
    constructor(protected bundleName: BundleNames) {
        BundleSkinCenter.getInstance().applySkin(bundleName, this);
    }

    public getCurSkin(): BdSkin.TBundleInfo<T> {
        return {
            bundleName: this.bundleName,
            ...(this.skin?.[Flavor.Skin.curMainSkin] ?? this.skin[SkinType.FINAL]),
        };
    }

    public skinProxy<T extends { [index: string]: any }>(array: T): T {
        const setBundleName = (desc: IResDescribe) => {
            desc.bundleName = this.bundleName;
        };
        const forEachDesc = (descs: IResDescribe[]) => {
            for (let key in descs) {
                if (!descs[key].resPath && !descs[key].type) {
                    forEachDesc(descs[key] as any);
                } else {
                    setBundleName(descs[key]);
                }
            }
        };

        const proxy = new Proxy(array, {
            get: (target: T, p: string, receiver: any): IBundleDesc => {
                try {
                    const result = target[p];
                    if (result.resPath && result.type) {
                        setBundleName(result);
                    } else {
                        forEachDesc(result);
                    }

                    return result;
                } catch (e) {
                    /*    if (CC_DEBUG) {
                        CC_DEBUG && cc.error(
                            `[${this.constructor.name}] not found skin, key=>${p} target=>`,
                            target,
                            e
                        );
                    } */
                }
                return null;
            },
        });

        return proxy;
    }

    //////////////////获取资源配置 begin/////////////////////
    public get Priority() {
        return this.skinProxy(this.getCurSkin().priority);
    }
    public get UnPriority() {
        return this.skinProxy(this.getCurSkin().unpriority);
    }
    public get LoadOnDemand() {
        return this.skinProxy(this.getCurSkin().loadOnDemand);
    }

    //////////////////获取资源配置 end/////////////////////
}
