import { CocosBundleEnum } from "../define/CocosBundleEnum";
import { CocosUiUtil } from "../utils/CocosUiUtil";
import MultLangLabelCustom from "./MultLangLabelCustom";
import LanguageCenter from "../../../language/LanguageCenter";
import { Instance } from "../../../utils/decorators/InstanceClassDecorator";
import MultLangLabelBase from "./MultLangLabelBase";

const { ccclass, property, executeInEditMode, menu, inspector } = cc._decorator;

@ccclass
@menu("core/label/MultLangLabel")
@inspector("packages://int-inspector/build/components/mult-lang-label.js")
export default class MultLangLabel extends MultLangLabelBase {
    private MainLangEnum = null;
    private SubLangEnum = null;

    private tempSub: number = 0;
    private tempMain: number = 0;

    @property({ type: CocosBundleEnum, override: true })
    protected get bundleName(): number {
        return CocosBundleEnum?.[this._bundleName] ?? 0;
    }

    protected set bundleName(value: number) {
        this._bundleName = CocosBundleEnum?.[value] ?? 0;
        this.updateMainLangEnum();
        this.mainLangKey = -Math.random();
    }

    @property({ type: cc.Enum({}) })
    protected get mainLangKey() {
        return this.MainLangEnum?.[`<${this._mainLangKey}>`] ?? this.tempMain;
    }
    protected set mainLangKey(value: number) {
        this._mainLangKey =
            (this.MainLangEnum?.[value] as string)?.replace?.(/<?(?<key>[^>]*)>?/, "$<key>") ?? ``;
        if (!this._mainLangKey) {
            this.tempMain = value;
        }
        this.updateSubLangEnum();
        this.subLangKey = -Math.random();
    }

    @property({ type: cc.Enum({}) })
    protected get subLangKey() {
        return this.SubLangEnum?.[`<${this._subLangKey}>`] ?? this.tempSub;
    }
    protected set subLangKey(value: number) {
        this._subLangKey =
            (this.SubLangEnum?.[value] as string)?.replace?.(/<?(?<key>[^>]*)>?/, "$<key>") ?? ``;
        if (!this._subLangKey) {
            this.tempSub = value;
        }
        this.updateString();
    }

    protected onLoad(): void {
        super.onLoad();
        if (CC_EDITOR) {
            this.updateMainLangEnum();
            this.updateSubLangEnum();
        }
    }

    protected updateMainLangEnum() {
        let object = Instance(LanguageCenter).getLanguage4Bundle(this._bundleName);
        let keys = [`None`, ...Object.keys(object)];
        let ccEnum = CocosUiUtil.createEnum(keys);
        this.MainLangEnum = ccEnum;
        cc.Class["Attr"].setClassAttr(this, "mainLangKey", "enumList", cc.Enum["getList"](ccEnum));
    }
    protected updateSubLangEnum() {
        let object =
            Instance(LanguageCenter).getLanguage4Bundle(this._bundleName)?.[this._mainLangKey] ??
            {};
        let keys = [`None`, ...Object.keys(object)];
        let ccEnum = CocosUiUtil.createEnum(keys);
        this.SubLangEnum = ccEnum;
        cc.Class["Attr"].setClassAttr(this, "subLangKey", "enumList", cc.Enum["getList"](ccEnum));
    }
}
