class MathUtils {
    /**
     * 获取随机数
     * @param min 最小值
     * @param max 最大值
     * @returns
     */
    Random(min, max) {
        return Math.round(Math.random() * (max - min)) + min;
    }

    /**
     * 获取浮点数 小数部分
     * @param num  浮点数
     * @returns
     */
    getDecimalValue(num) {
        let int = this.getInteger(num);
        let decimal = num - int;
        decimal = Math.floor(decimal * 100) / 100;
        return decimal;
    }

    /**
     * 获取浮点数 整数部分
     * @param num
     * @returns
     */
    getInteger(num) {
        return Math.trunc(num);
    }

    saveTowDecimal = (decimal) => (decimal = Math.floor(decimal * 100) / 100);
}

export default new MathUtils();
