import { Instance } from "../utils/decorators/InstanceClassDecorator";
import LanguageObserver from "./LanguageObserver";

export namespace MultLangUtils {
    export function assetPath(bundle: string, path: string) {
        let curLanguage = `lan-${Instance(LanguageObserver).get()}`;
        let language = /\/(?<language>lan\-([a-z]{2}))\//.exec(path)?.groups?.language;
        if (curLanguage == language) {
            return null;
        }
        let newPath = path.replace(`${language}`, `${curLanguage}`);
        if (!newPath) {
            return null;
        }

        if (cc.assetManager.getBundle(bundle).getInfoWithPath(newPath)) {
            return newPath;
        }

        newPath = newPath.replace(/\-[\w]+$/, ""); //SourceSansPro-Regular -> SourceSansPro

        if (cc.assetManager.getBundle(bundle).getInfoWithPath(newPath)) {
            return newPath;
        }
        return null;
    }

    export function assetDesc(desc: IResDescribe): IResDescribe {
        let path = assetPath(desc.bundleName, desc.resPath);
        if (!path) {
            return desc;
        }
        return {
            ...desc,
            resPath: path,
        };
    }
}
