export function previewFunctionParams(callfunc: (...args) => any) {
    return function (
        target: Object,
        propertyKey: string | symbol,
        descriptor: TypedPropertyDescriptor<any>
    ): TypedPropertyDescriptor<any> {
        if (CC_PREVIEW) {
            let oldValue = descriptor.value as Function;
            descriptor.value = function (...args) {
                return oldValue.apply(this, [...callfunc(...args)]);
            };
        }

        return descriptor;
    };
}
