export function setInstance(ctor) {
    return class newCtor extends ctor {
        private static _instance = null;
        private static getInstance() {
            if (!this._instance) {
                this._instance = new newCtor();
            }
            if (CC_DEBUG) {
                window[`obs_${ctor.name}_instance`] = this._instance;
            }
            return this._instance;
        }
    };
}

export function Instance<T>(ctor: { new (): T }): T {
    return ctor["getInstance"]();
}
