import { BundleMgr } from "../../bd/BundleMgr";

export class PrefabManager {
    private static _instance: PrefabManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new PrefabManager();
        }

        return this._instance;
    }

    private constructor() { }

    public loadPrefabToNode(prefab, node) {
        return new Promise((resolve, reject) => {
            if (cc.isValid(prefab) && cc.isValid(node)) {
                let prefabIns = cc.instantiate(prefab);
                if (cc.isValid(prefabIns)) {
                    node.addChild(prefabIns);
                    resolve(prefabIns);
                }
            } else {
                reject(false);
            }
        });
    }

    public loadPrefabBundle(
        resDesc: IResDescribe,
        callFunc?: (res: any) => void
    ): Promise<cc.Prefab> {
        return BundleMgr.getInstance().loadResFromBundle(resDesc, callFunc);
    }

    public getPrefabBundle(resDesc: IResDescribe) {
        return BundleMgr.getInstance().getResFromBundle(resDesc);
    }

    public loadPrefabInsBundle(
        resDesc: IResDescribe,
        successCallback: (res: any) => void,
        failCallback?: () => void
    ) {
        BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
            if (res) {
                successCallback && successCallback(cc.instantiate(res));
            } else {
                failCallback && failCallback();
            }
        });
    }
}
