import { lodash } from "../../utils/NpmExport";
import BaseEnumComp from "./BaseEnumComp";
import EnumController from "./EnumController";
const { ccclass, property, executeInEditMode, menu, requireComponent } = cc._decorator;

@ccclass
@executeInEditMode
@requireComponent(cc.ToggleContainer)
@menu("core/controller/ToggleUiContainer")
export default class ToggleUiContainer extends EnumController {
    protected resetInEditor(): void {
        let checkEvent = new cc.ToggleContainer.EventHandler();
        checkEvent.component = ToggleUiContainer.name;
        checkEvent.handler = this.onToggleClicked.name;
        checkEvent.target = this.node;

        let toggleCtrl = this.getComponent(cc.ToggleContainer);
        toggleCtrl.toggleItems.forEach((toggle) => {
            if (
                !toggle.checkEvents.find((v) => {
                    if (
                        v.handler == checkEvent.handler &&
                        (v.component == checkEvent.component || v.component == "") &&
                        v.target == this.node
                    ) {
                        return true;
                    }
                    return false;
                })
            ) {
                toggle.checkEvents.push(checkEvent);
            }
        });
    }
    protected onToggleClicked(toggle: cc.Toggle) {
        this.show4Name(toggle.node.name);
    }

    protected onDestroy() {
        if (CC_EDITOR) {
            let toggleCtrl = this.getComponent(cc.ToggleContainer);
            toggleCtrl.toggleItems.forEach((toggle) => {
                toggle.checkEvents = lodash.filter(toggle.checkEvents, (item) => {
                    return (
                        item.handler != this.onToggleClicked.name &&
                        item.component != ToggleUiContainer.name &&
                        item.target != this.node
                    );
                });
            });
        }
    }
}
