import BaseEnumComp from "./BaseEnumComp";
import { _SpriteFrameItem } from "./item/_SpriteFrameItem";

const { ccclass, property, executeInEditMode, menu, requireComponent } = cc._decorator;

@ccclass
@executeInEditMode
@menu("core/controller/EnumSpriteFrame")
export default class EnumSpriteFrame extends BaseEnumComp {
    @property(_SpriteFrameItem)
    private childs: _SpriteFrameItem[] = [];

    protected onFocusInEditor(): void {
        let enumMap: { [key: string]: number } = {
            "<None>": 0,
        };
        this.getChilds().forEach((v, i) => {
            enumMap[v?.name ?? "<Unknown>"] = i + 1;
        });

        cc.Class["Attr"].setClassAttr(this, "activeIndex", "enumList", cc.Enum["getList"](enumMap));
    }

    public getChilds(): { name: string; value: cc.SpriteFrame }[] {
        return this.childs.map((v) => {
            return {
                name: v.key,
                value: v.value,
            };
        });
    }

    protected updateActive(): void {
        const value = this.activeIndex - 1;
        const sprite = this.node.getComponent(cc.Sprite);
        if (sprite) {
            sprite.spriteFrame = this.getChilds()?.[value]?.value ?? null;
        }
    }
    // update (dt) {}
}
