import Flavor from "../conf/Flavor";
import { LanguageType } from "../conf/LanguageType";
import { LocalStorageMgr } from "../storage/LocalStorageMgr";
import LocalStorageObserver from "../utils/class/LocalStorageObserver";
import { setInstance } from "../utils/decorators/InstanceClassDecorator";

@setInstance
export default class LanguageObserver extends LocalStorageObserver<LanguageType> {
    protected read(): LanguageType {
        return LocalStorageMgr.getInstance().readLanguage() || Flavor.Language.curLanguage;
    }
    protected save(data: LanguageType) {
        LocalStorageMgr.getInstance().saveLanguage(data);
    }
}
