import Listener from "../../utils/Listener";

/** 屏幕方向 */
export enum SCREEN_ORIENTATION {
    /** 竖屏 home 在下  */
    PORTRAIT = 0,
    /** 横屏 home 在右  */
    LANDSCAPE_RIGHT = 1,
    /** 竖屏 home 在上  */
    PORTRAIT_UPSIDE_DOWN = 2,
    /** 横屏 home 在左  */
    LANDSCAPE_LEFT = 3,
}

/** 设计宽度 */
const DESIGN_WIDTH = 1280;
/** 设计高度 */
const DESIGN_HEIGHT = 720;
/** 刘海高度 */
const NOTCH_HEIGHT = 60;

/** 偏移模式 */
const OFFSET_MODE = cc.Enum({
    /** 按照 max(margin, 刘海高度) 取最大值 偏移 */
    MAX_MARGIN_AND_NOTCH: 0,
    /** 按照 add(margin, offset) 取和 偏移 */
    ADD_MARGIN_AND_OFFSET: 1,
});

const { ccclass, property, executeInEditMode, requireComponent, menu } = cc._decorator;

@ccclass
@executeInEditMode
@requireComponent(cc.Widget)
@menu("core/ui/AdaptNotch")
export default class AdaptNotch extends cc.Component {
    @property
    private _offsetMode: number = OFFSET_MODE.MAX_MARGIN_AND_NOTCH;
    @property({
        type: cc.Enum(OFFSET_MODE),
        tooltip: `偏移模式, 默认 MAX_MARGIN_AND_NOTCH
                \n1.MAX_MARGIN_AND_NOTCH, 按照 max(margin, 刘海高度) 取最大值 偏移
                \n2.ADD_MARGIN_AND_OFFSET, 按照 add(margin, offset) 取和 偏移
                `,
    })
    public get offsetMode() {
        return this._offsetMode;
    }
    public set offsetMode(value: number) {
        this._offsetMode = value;
    }

    @property
    private _offset: number = 0;
    @property({
        type: cc.Float,
        tooltip: `偏移量(px), 默认 0`,
        visible: function () {
            return this._offsetMode == OFFSET_MODE.ADD_MARGIN_AND_OFFSET;
        },
    })
    public get offset() {
        return this._offset;
    }
    public set offset(value: number) {
        this._offset = value;
    }

    @property
    private _landscapeSymmetry: boolean = true;
    @property({
        type: cc.Boolean,
        tooltip: `横屏是否对称, 默认 true`,
    })
    public get landscapeSymmetry() {
        return this._landscapeSymmetry;
    }
    public set landscapeSymmetry(value: boolean) {
        this._landscapeSymmetry = value;
    }

    @property
    private _portraitSymmetry: boolean = false;
    @property({
        type: cc.Boolean,
        tooltip: `竖屏是否对称, 默认 false`,
    })
    public get portraitSymmetry() {
        return this._portraitSymmetry;
    }
    public set portraitSymmetry(value: boolean) {
        this._portraitSymmetry = value;
    }

    /** 是否刘海屏 */
    private isNotch: boolean = false;
    /** 屏幕方向 */
    private orientation: SCREEN_ORIENTATION = SCREEN_ORIENTATION.LANDSCAPE_RIGHT;
    private widget: cc.Widget = null;
    private topRaw: number = 0;
    private bottomRaw: number = 0;
    private leftRaw: number = 0;
    private rightRaw: number = 0;

    protected onLoad(): void {
        this.isNotch = this.getNotch();
        this.orientation = this.getOritation();
        this.widget = this.node.getComponent(cc.Widget);
        this.topRaw = this.widget.top;
        this.bottomRaw = this.widget.bottom;
        this.leftRaw = this.widget.left;
        this.rightRaw = this.widget.right;

        this.adapt();

        cc.view.on("canvas-resize", this.onResize, this);
        // todo L
        Listener.on("SCREEN_ORIENTATION_CHANGED", this.onScreenOrientationChanged, this);
    }

    protected onDestroy(): void {
        cc.view.off("canvas-resize", this.onResize, this);
        Listener.off("SCREEN_ORIENTATION_CHANGED", this.onScreenOrientationChanged, this);
    }

    private onResize(): void {
        let isNotch = this.getNotch();
        if (isNotch == this.isNotch) {
            return;
        }

        this.isNotch = isNotch;
        this.adapt();
    }

    private onScreenOrientationChanged(): void {
        let orientation = this.getOritation();
        if (orientation == this.orientation) {
            return;
        }

        this.orientation = orientation;
        this.adapt();
    }

    private adapt(): void {
        let target = this.widget.target || this.widget.node.parent;

        if (this.widget.isAlignTop) {
            let margin = this.topRaw;
            if (
                this.orientation == SCREEN_ORIENTATION.PORTRAIT ||
                (this._portraitSymmetry &&
                    this.orientation == SCREEN_ORIENTATION.PORTRAIT_UPSIDE_DOWN)
            ) {
                margin = this.getMargin(margin, this.widget.isAbsoluteTop, target.height);
            }
            this.widget.top = margin;
        }

        if (this.widget.isAlignBottom) {
            let margin = this.bottomRaw;
            if (
                this.orientation == SCREEN_ORIENTATION.PORTRAIT_UPSIDE_DOWN ||
                (this._portraitSymmetry && this.orientation == SCREEN_ORIENTATION.PORTRAIT)
            ) {
                margin = this.getMargin(margin, this.widget.isAbsoluteBottom, target.height);
            }
            this.widget.bottom = margin;
        }

        if (this.widget.isAlignLeft) {
            let margin = this.leftRaw;
            if (
                this.orientation == SCREEN_ORIENTATION.LANDSCAPE_RIGHT ||
                (this._landscapeSymmetry && this.orientation == SCREEN_ORIENTATION.LANDSCAPE_LEFT)
            ) {
                margin = this.getMargin(margin, this.widget.isAbsoluteLeft, target.width);
            }
            this.widget.left = margin;
        }

        if (this.widget.isAlignRight) {
            let margin = this.rightRaw;
            if (
                this.orientation == SCREEN_ORIENTATION.LANDSCAPE_LEFT ||
                (this._landscapeSymmetry && this.orientation == SCREEN_ORIENTATION.LANDSCAPE_RIGHT)
            ) {
                margin = this.getMargin(margin, this.widget.isAbsoluteRight, target.width);
            }
            this.widget.right = margin;
        }

        this.widget.updateAlignment();
    }

    private getMargin(margin: number, abs: boolean, parent: number): number {
        if (!this.isNotch) {
            return margin;
        }

        let notch = this.isNotch ? NOTCH_HEIGHT : 0;

        if (abs) {
            if (this._offsetMode == OFFSET_MODE.MAX_MARGIN_AND_NOTCH) {
                margin = margin < notch ? notch : margin;
            } else {
                margin = margin + this._offset;
            }
        } else {
            if (this._offsetMode == OFFSET_MODE.MAX_MARGIN_AND_NOTCH) {
                margin = margin < notch / parent ? notch / parent : margin;
            } else {
                margin = margin + this._offset / parent;
            }
        }

        return margin;
    }

    private getNotch(): boolean {
        let frameSize = cc.view.getFrameSize();
        let width = frameSize.width > frameSize.height ? frameSize.width : frameSize.height;
        let height = frameSize.width > frameSize.height ? frameSize.height : frameSize.width;
        let isNotch = width / height > (DESIGN_WIDTH + NOTCH_HEIGHT * 2) / DESIGN_HEIGHT;

        return isNotch;
    }

    private getOritation(): SCREEN_ORIENTATION {
        let orientation: SCREEN_ORIENTATION;
        if (cc.sys.isNative) {
            orientation = jsb["device"].getDeviceRotation();
        } else {
            let frameSize = cc.view.getFrameSize();
            orientation =
                frameSize.height > frameSize.width
                    ? SCREEN_ORIENTATION.PORTRAIT
                    : SCREEN_ORIENTATION.LANDSCAPE_RIGHT;
        }

        return orientation;
    }
}
