import ValueObserver from "./ValueObserver";

export default abstract class HttpObserver<T, Q extends Array<any> = any[]> extends ValueObserver<
    Partial<T>
> {
    protected onInit(): Partial<T> {
        return {};
    }

    public query(...args: Q) {
        return this.onQuery(...args).then((res) => {
            this.set(res);
        });
    }

    protected abstract onQuery(...args: Q): Promise<T>;
}
