const { ccclass, property, requireComponent, menu } = cc._decorator;

enum TurnPage {
    Previous,
    Next,
}

@ccclass
@requireComponent(cc.Button)
@menu("core/ui/PageViewTurnPageButton")
export default class PageViewTurnPageButton extends cc.Component {
    @property(cc.PageView)
    private pageView: cc.PageView = null;

    @property({ type: cc.Enum(TurnPage) })
    private turnType: TurnPage = TurnPage.Previous;

    resetInEditor() {
        if (!this.pageView) {
            this.pageView = this.node.parent.getComponent(cc.PageView);
        }
    }
    protected onLoad(): void {
        if (!this.pageView) {
            this.pageView = this.node.parent.getComponent(cc.PageView);
        }
        this.refreshButtonState();
        this.pageView?.node.on("page-turning", this.refreshButtonState, this);
        this.node.on(
            "click",
            () => {
                this.pageView?.scrollToPage(
                    this.pageView?.getCurrentPageIndex() +
                        (this.turnType == TurnPage.Previous ? -1 : 1),
                    0.1
                );
            },
            this
        );
    }

    public refreshButtonState() {
        if (this.turnType === TurnPage.Next) {
            this.getComponent(cc.Button).interactable =
                this.pageView?.getCurrentPageIndex() + 1 < this.pageView?.getPages().length;
        } else {
            this.getComponent(cc.Button).interactable = this.pageView?.getCurrentPageIndex() > 0;
        }
    }
}
