const { ccclass, property, playOnFocus, executeInEditMode, menu } = cc._decorator;

@ccclass
@playOnFocus
@executeInEditMode
@menu("core/ui/DropDownList")
export default class DropDownList extends cc.Component {
    @property(cc.SpriteFrame)
    mainBtnSpriteFrames: [cc.SpriteFrame, cc.SpriteFrame] = [null, null];

    @property()
    private spacing: number = 0;

    @property()
    private _isChecked: boolean = false;
    @property()
    public get isChecked(): boolean {
        return this._isChecked;
    }
    public set isChecked(v: boolean) {
        this._isChecked = v;
        this.onUpdateUi();
    }

    start() {
        this.node.on(
            cc.Node.EventType.TOUCH_END,
            () => {
                this.isChecked = !this.isChecked;
            },
            this
        );
    }

    private getChilds() {
        return this.node.children;
    }

    private onUpdateUi() {
        if (this.isChecked) {
            this.showBtns();
        } else {
            this.hideBtns();
        }
    }

    private showBtns() {
        this.stopAllAnim();

        this.node.getComponent(cc.Sprite).spriteFrame = this.mainBtnSpriteFrames[0];
        let beginPosY = -this.node.height / 2;
        this.getChilds().forEach((node, index) => {
            beginPosY -= this.spacing;
            node.active = true;
            cc.tween(node)
                .to(0.1 * (index + 1), {
                    y: beginPosY - node.height / 2,
                })
                .start();
            beginPosY -= node.height;
        });
    }

    private hideBtns() {
        this.stopAllAnim();
        this.node.getComponent(cc.Sprite).spriteFrame = this.mainBtnSpriteFrames[1];
        this.getChilds().forEach((node, index) => {
            cc.tween(node)
                .to(0.1 * (index + 1), {
                    y: 0,
                })
                .call(() => {
                    node.active = false;
                })
                .start();
        });
    }

    private stopAllAnim() {
        this.getChilds().forEach((node) => {
            cc.Tween.stopAllByTarget(node);
        });
    }

    // update (dt) {}
}
