import { BundleMgr } from "../../bd/BundleMgr";

export class SpriteManager {
    private static _instance: SpriteManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new SpriteManager();
        }

        return this._instance;
    }

    private constructor() {}

    public createSpriteNode(name?: string) {
        let node = new cc.Node("sprite_" + name || "sprite");
        node.addComponent(cc.Sprite);
        node.name = name || "sprite";
        return node;
    }

    public setSpriteFrame(node, spriteFrame) {
        let spriteNode = null;
        if (cc.isValid(node) && spriteFrame) {
            if (node instanceof cc.Sprite) {
            } else if (node instanceof cc.Node) {
                spriteNode = node.getComponent(cc.Sprite);
                if (!spriteNode) {
                    spriteNode = node.addComponent(cc.Sprite);
                }
            }
            if (cc.isValid(spriteNode)) {
                spriteNode.spriteFrame = spriteFrame;
            }
        }
    }

    public loadSpriteForNodeBundle(node: cc.Node, resDesc: IResDescribe, callback?: () => void) {
        if (cc.isValid(node)) {
            let sprite = node.getComponent(cc.Sprite);
            BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
                if (res) {
                    if (cc.isValid(sprite)) {
                        sprite.spriteFrame = res;
                        callback && callback();
                    }
                }
            });
        }
    }

    public loadSpriteAtlasBundleForNode(
        node: cc.Node,
        resDesc: IResDescribe,
        spriteName: string,
        callback?,
        errorCallback?
    ) {
        if (cc.isValid(node)) {
            let sprite = node.getComponent(cc.Sprite);
            this.loadSpriteAtlasBundle(sprite, resDesc, spriteName, callback, errorCallback);
        }
    }

    public loadSpriteAtlasBundle(
        sprite: cc.Sprite,
        resDesc: IResDescribe,
        spriteName: string,
        callback?,
        errorCallback?
    ) {
        BundleMgr.getInstance().loadResFromBundle(resDesc, (res: any) => {
            if (res) {
                let spriteFrame = res.getSpriteFrame(spriteName);
                if (spriteFrame) {
                    sprite.spriteFrame = spriteFrame;
                    if (typeof callback === "function") {
                        callback();
                    }
                } else {
                    if (typeof errorCallback === "function") {
                        errorCallback();
                    }
                }
            } else {
                if (typeof errorCallback === "function") {
                    errorCallback();
                }
            }
        });
    }

    public getSpriteFromCommonAtlasBundle(resDesc: IResDescribe, spriteName: string) {
        let atlas: cc.SpriteAtlas = BundleMgr.getInstance().getResFromBundle(resDesc);

        let spriteFrame = null;
        if (atlas) {
            spriteFrame = atlas.getSpriteFrame(spriteName);
        }
        return spriteFrame;
    }

    public getSpriteBundle(resDesc: IResDescribe) {
        return BundleMgr.getInstance().getResFromBundle(resDesc);
    }

    public loadSpriteBundle(resDesc: IResDescribe, callback?: (res: cc.SpriteFrame) => void) {
        return BundleMgr.getInstance().loadResFromBundle<cc.SpriteFrame>(resDesc, (res: any) => {
            if (res) {
                callback && callback(res);
            }
        });
    }

    public loadRemoteUrl(node, path, callback?, errorCallback?) {
        let spriteNode = null;
        if (cc.isValid(node)) {
            if (node instanceof cc.Sprite) {
                spriteNode = node;
            } else if (node instanceof cc.Node) {
                spriteNode = node.getComponent(cc.Sprite);
                if (!spriteNode) {
                    spriteNode = node.addComponent(cc.Sprite);
                }
            }
        }

        if (spriteNode == null) {
            if (typeof errorCallback === "function") {
                errorCallback();
            }
            return;
        }

        cc.assetManager.loadRemote(path, (err, tex: any) => {
            if (err) {
                CC_DEBUG && cc.error(err.message || err);
                if (typeof errorCallback === "function") {
                    errorCallback();
                }
                return;
            }

            if (tex && tex.url && cc.isValid(spriteNode) && cc.isValid(spriteNode.node)) {
                let spriteFrame = new cc.SpriteFrame(tex);
                if (spriteFrame) {
                    spriteNode.spriteFrame = spriteFrame;
                    if (typeof callback === "function") {
                        callback();
                    }
                } else {
                    if (typeof errorCallback === "function") {
                        errorCallback();
                    }
                }
            } else {
                if (typeof errorCallback === "function") {
                    errorCallback();
                }
            }
        });
    }
}
