import lazyInitialize from "./LazyInitialize";

const { defineProperty, getOwnPropertyDescriptor, getOwnPropertyNames, getOwnPropertySymbols } =
    Object;

export function isDescriptor(desc: Object) {
    if (!desc || !desc.hasOwnProperty) {
        return false;
    }

    const keys = ["value", "initializer", "get", "set"];

    for (let i = 0, l = keys.length; i < l; i++) {
        if (desc.hasOwnProperty.call(desc, keys[i])) {
            return true;
        }
    }

    return false;
}

export function decorate(handleDescriptor, entryArgs) {
    if (isDescriptor(entryArgs[entryArgs.length - 1])) {
        return handleDescriptor(...entryArgs, []);
    } else {
        return function (...args: any[]) {
            return handleDescriptor(...Array.prototype.slice.call(args), entryArgs);
        };
    }
}

class Meta {
    @lazyInitialize
    debounceTimeoutIds = {};

    @lazyInitialize
    throttleTimeoutIds = {};

    @lazyInitialize
    throttlePreviousTimestamps = {};

    @lazyInitialize
    throttleTrailingArgs = null;

    @lazyInitialize
    profileLastRan = null;
}

const META_KEY =
    typeof Symbol === "function" ? Symbol("__core_decorators__") : "__core_decorators__";

export function metaFor(obj) {
    if (obj.hasOwnProperty.call(obj, META_KEY) === false) {
        defineProperty(obj, META_KEY, {
            // Defaults: NOT enumerable, configurable, or writable
            value: new Meta(),
        });
    }

    return obj[META_KEY];
}

export const getOwnKeys = getOwnPropertySymbols
    ? function (object: any) {
          return getOwnPropertyNames(object).concat(
              getOwnPropertySymbols(object) as unknown as string[]
          );
      }
    : getOwnPropertyNames;

export function getOwnPropertyDescriptors(obj) {
    const descs = {};

    getOwnKeys(obj).forEach((key) => (descs[key] = getOwnPropertyDescriptor(obj, key)));

    return descs;
}

export function createDefaultSetter(key) {
    return function set(newValue) {
        Object.defineProperty(this, key, {
            configurable: true,
            writable: true,
            // IS enumerable when reassigned by the outside word
            enumerable: true,
            value: newValue,
        });

        return newValue;
    };
}

export function bind(fn, context) {
    if (fn.bind) {
        return fn.bind(context);
    } else {
        return function __autobind__(...args: any[]) {
            return fn.apply(context, args);
        };
    }
}

export const warn = (() => {
    if (typeof cc !== "object" || !cc || typeof cc.warn !== "function") {
        return () => {};
    } else {
        return bind(cc.warn, cc);
    }
})();

const seenDeprecations = {};
export function internalDeprecation(msg) {
    if (seenDeprecations[msg] !== true) {
        seenDeprecations[msg] = true;
        warn("DEPRECATION: " + msg);
    }
}
