import { LocalStorageMgr } from "../../storage/LocalStorageMgr";
import ValueObserver from "./ValueObserver";

const CHANGE_VALUE = "change_value";

export default abstract class CacheObserver<T> extends ValueObserver<T> {
    protected abstract getCacheKey(): string;

    public init() {
        super.init(this.initCache());
    }

    protected onInit(data?: T): T {
        return data || ({} as any);
    }

    public initCache(): T {
        try {
            let json = LocalStorageMgr.getInstance().readData(this.getCacheKey());
            if (!json) {
                return;
            }
            let data = JSON.parse(json);
            return data;
        } catch {}
        return;
    }

    protected onNotice(newValue: T) {
        LocalStorageMgr.getInstance().saveData(this.getCacheKey(), JSON.stringify(newValue));
    }
}
