import BaseEnumComp from "./BaseEnumComp";
import { _PositionItem } from "./item/_PositionItem";

const { ccclass, property, executeInEditMode, menu } = cc._decorator;

@ccclass
@executeInEditMode
@menu("core/controller/EnumPosition")
export default class EnumPosition extends BaseEnumComp {
    @property(_PositionItem)
    private childs: _PositionItem[] = [];

    protected onFocusInEditor(): void {
        let enumMap: { [key: string]: number } = {
            "<None>": 0,
        };
        this.getChilds().forEach((v, i) => {
            enumMap[v?.name ?? "<Unknown>"] = i + 1;
        });
        cc.Class["Attr"].setClassAttr(this, "activeIndex", "enumList", cc.Enum["getList"](enumMap));
    }

    public getChilds(): { name: string; value: cc.Vec3 }[] {
        return this.childs.map((v) => {
            return {
                name: v.key,
                value: v.value,
            };
        });
    }

    protected updateActive(): void {
        const value = this.activeIndex - 1;
        this.node.position = this.getChilds()?.[value]?.value ?? cc.v3();
    }
    // update (dt) {}
}
