export class AppParamModel {
    private static _instance: AppParamModel = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new AppParamModel();
        }
        return this._instance;
    }

    private constructor() {}

    private chn: string = "";

    private merCode: string = "";

    private brandCode: string = "";

    private appName: string = "";

    private packageName: string = "";

    private appVersionCode: string = "";

    private deviceId: string = "";

    private deviceInfo: string = "";

    private network: number = 1;

    /**
     * 包名
     */
    public getPackageName() {
        return this.packageName;
    }

    public setPackageName(packageName) {
        this.packageName = packageName;
    }

    /**
     * 商户号
     */
    public getMerCode() {
        return this.merCode;
    }

    public setMerCode(merCode) {
        this.merCode = merCode;
    }

    /**
     * 品牌号
     */
    public getBrandCode() {
        return this.brandCode;
    }

    public setBrandCode(brandCode) {
        this.brandCode = brandCode;
    }

    /**
     * 完整渠道号
     */
    public getChn() {
        return this.chn;
    }

    public setChn(chn) {
        this.chn = chn;
    }

    /**
     * 应用程序名
     */
    public getAppName() {
        return this.appName;
    }

    public setAppName(appName) {
        this.appName = appName;
    }

    /**
     * appVersionCode
     */
    public getAppVersionCode() {
        return this.appVersionCode;
    }

    public setAppVersionCode(vc) {
        this.appVersionCode = vc;
    }

    /**
     * deviceId
     */
    public getDeviceID() {
        return this.deviceId;
    }

    public setDeviceID(deviceId) {
        this.deviceId = deviceId;
    }

    /**
     * deviceInfo
     */
    public getDeviceInfo() {
        return this.deviceInfo;
    }

    public setDeviceInfo(deviceInfo) {
        this.deviceInfo = deviceInfo;
    }

    public getNetwork(): number {
        return this.network;
    }

    public setNetwork(network: number) {
        this.network = network;
    }
}
