import { deprecated } from "../utils/decorators/LogsDesc";
import UserInfoObserver from "./UserInfoObserver";

export class UserManager {
    private static _instance: UserManager = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new UserManager();
        }

        return this._instance;
    }

    private _value: any;
    public get key(): any {
        return this._value;
    }
    public set key(v: any) {
        this._value = v;
    }

    private lastcoin: any;
    public get Coin(): any {
        return this.lastcoin;
    }
    public set Coin(v: any) {
        this.lastcoin = v;
    }

    private constructor() {}

    public uid = 0;
    public token = "";
    public get nick() {
        return this.info.get("username", "");
    }
    public get gender() {
        return this.info.get("gender", 1);
    }
    public get avatar() {
        return this.info.get("avatar", 0);
    }
    public get coin() {
        return this.info.get("credit", 0);
    }
    public get phone() {
        return this.info.get("phone", "");
    }
    public get user_id() {
        return this.info.get("user_id", 0);
    }
    public get safe_box() {
        return this.info.get("safe_box", 0);
    }

    public get vip() {
        return this.info.get("level", 0);
    }

    public get chn() {
        return this.info.get("chn", "");
    }

    public get brand_code() {
        return this.info.get("brand_code", "");
    }

    public ticket = 0;
    public get is_formal() {
        return this.info.get("is_upgraded", false);
    }
    public chat_timeStamp = 0;
    public kefu_uid = -1;
    public get amount() {
        return this.coin + this.safe_box;
    }
    public available_avatar = [];

    info: UserInfoObserver = new UserInfoObserver();

    public initUser(data: UserInfo) {
        this.ticket = 0;
        this.info.modify(data);
    }

    public initAccount(uid: number, token: string) {
        this.uid = uid;
        this.token = token;
    }

    public clearAccount() {
        this.uid = 0;
        this.token = "";
    }

    public clearAvailableAvatar() {
        this.available_avatar = [];
    }

    public hasPhone() {
        return this.info.get("phone");
    }

    @deprecated("update userInfo please use [CommonUserUtil.updateUserBalance()]")
    public addCoinCount(count) {
        this.info.set("credit", this.coin + parseFloat(count));
        //cc.director.emit(CommonDepend.CommonEventName.REFRESH_USER_INFO);
        return this.coin;
    }

    public updateCoin(coin: number) {
        this.info.set("credit", coin);
    }

    public updateSafeBox(amount: number) {
        this.info.set("safe_box", amount);
    }

    public getShowUidString() {
        if (this.uid && (this.uid + "").length > 3) {
            return (this.uid + "").substring(3);
        } else {
            return this.uid + "" || "";
        }
    }

    @deprecated("update userInfo please use [CommonUserUtil.updateUserInfo()]")
    public updatePhone(phoneStr) {
        if (phoneStr) {
            this.info.set("phone", phoneStr);
        }
    }

    public isSelf(userId: string) {
        return this.user_id.toString() == userId;
    }
}
