import ProjectConf from "./ProjectConf";
import { SkinType } from "./SkinType";
import { LanguageType } from "./LanguageType";
import { IntConf } from "./int/IntConf";
import { CountryCodeEnum } from "./int/CountryCodeEnum";

class Skin {
    public static curVersionType = ProjectConf.version_type;

    public static curMainSkin = IntConf[Skin.curVersionType].skin;

    public static isBall() {
        return this.curMainSkin == SkinType.BALL;
    }

    public static isWorldCup() {
        return this.curMainSkin == SkinType.WORLD_CUP;
    }

    public static isLight() {
        return this.curMainSkin == SkinType.LIGHT;
    }
}

class Language {
    public static curLanguage = IntConf[Skin.curVersionType].lan;

    public static lan_en() {
        return this.curLanguage == LanguageType.EN;
    }

    public static lan_br() {
        return this.curLanguage == LanguageType.PT;
    }
}

class Country {
    public static code = IntConf[Skin.curVersionType].country;

    public static code_in() {
        return this.code == CountryCodeEnum.IN;
    }

    public static code_br() {
        return this.code == CountryCodeEnum.BR;
    }
}

export default { Skin, Language, Country };
