import { BundleNames } from "../BundleNames";
import { CountryCodeEnum } from "./CountryCodeEnum";

let commonGame: string[] = [
    BundleNames.FIRE_JOKER,
    BundleNames.BUFFALO,
    BundleNames.WOLFGOLD,
    BundleNames.CLASSIC_FIVE_DRAGONS,
    BundleNames.GOD_OF_FORTUNE,
    BundleNames.BIGBATTLE,
    BundleNames.RED_BLACK,
    BundleNames.BIG_SMALL,
    BundleNames.SEVEN_UP_DOWN,
    BundleNames.CRASH,
    BundleNames.ANDAR_BAHAR,
    BundleNames.ROULETTE,
    BundleNames.LUDO,
    BundleNames.BINGO,
    BundleNames.BLACK_JACK,
    BundleNames.DOMINO_GAPLE,
    BundleNames.FISHING,
];

let mergeCommonGame = (arr: string[]): string[] => {
    return Array.from(new Set(commonGame.concat(arr)));
};

export let GameConf = {
    [CountryCodeEnum.IN]: mergeCommonGame([
        BundleNames.BEST_OFFIVE,
        BundleNames.CRICKET_BATTLE,
        BundleNames.THREE_DICE,
        BundleNames.JHANDI_MUNDA,
        BundleNames.RUMMY_POINT,
        BundleNames.TEENPATTI,
        BundleNames.CALL_BREAK,
    ]),
    [CountryCodeEnum.BR]: mergeCommonGame([
        BundleNames.BEST_CLUB,
        BundleNames.TRUCO,
        BundleNames.CACHETA,
    ]),
    [CountryCodeEnum.ID]: mergeCommonGame([BundleNames.RUMMY_POINT]),
};
